<script>
function getHtmlElement() {
  return document.documentElement;
}

export default {
  props: {
    lockClass: {
      type: String,
      default: 'scroll-lock',
    },
    targetQuery: {
      type: Function,
      default: getHtmlElement,
    }
  },
  mounted() {
    this.targetQuery().classList.add(this.lockClass);
  },
  beforeDestroy() {
    this.targetQuery().classList.remove(this.lockClass);
  },
  render() {
    return null;
  }
};
</script>
