import * as cmd from './commands';

export default {
  [cmd.ACTIVE_MEMBERSHIPS](state) {
    const memberships = state.currentUser.memberships;
    const policyRoles = state.policyRoles.app.access;
    const networkRoles = state.currentUser.networkRoles;
    const activeMemberships = memberships.filter(({ status, roles }) => {
      return status === 'ACTIVE'
        && (networkRoles.some(r => r in policyRoles)
        || roles.some(role => role in policyRoles));
    });
    return activeMemberships;
  }
};
