import actions from './actions';
import mutations from './mutations';
import getters from './getters';

const autosaveStore = {
  state: () => ({
    contentApiSocket: null,
    // keys are IDs
    dirty: {},
    inFlight: {},
    inLine: {},
    docsById: {}, // the full doc object(s)
    sentIds: {}, // keys are uuids for packets
    currentDocId: null, // main doc in state, tied to url uuid
    ready: false, // currentDoc has loaded and autosave initialized
    subdocs: [], // loaded subdocs
    error: false, // fatal error state, shut it down
    reconnectingIds: [],
    connectedUsers: []
  }),
  actions: actions,
  mutations: mutations,
  getters: getters,
};

export default autosaveStore;
