<template>
  <div class="m-nav--dashboard__new-story">
    <span v-if="storyTypes.length === 1">
      <res-button
        v-if="isMobile"
        :disabled="loadingNewDoc || readOnly"
        :key="mobileKey"
        design="primary"
      >
        <res-icon name="add" size="medium" />
        <button @click="newDoc(storyTypes[0].type, storyTypes[0].defaults)" />
      </res-button>

      <res-button
        v-else
        :disabled="loadingNewDoc || readOnly"
        :key="mobileKey"
        design="primary"
      >
        <button @click="newDoc(storyTypes[0].type, storyTypes[0].defaults)">New {{ storyTypes[0].type }}</button>
      </res-button>
    </span>
    <res-split-button
      v-else
      :key="mobileKey"
      :mobile="isMobile"
      :disabled="loadingNewDoc || readOnly"
      icon="add"
    >
      <ul>
        <li v-for="(story, index) in storyTypes" :key="index" :active="index === 0">
          <a @click="newDoc(story.type, story.defaults)">
            <span>New {{ story.type }}</span>
            <res-badge v-if="story.status" size="smallest" design="rose">{{ story.status }}</res-badge>
          </a>
        </li>
      </ul>
    </res-split-button>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import { hasPolicyRole } from '../../lib/authorization';
import request from '../../../client/lib/request';

export default {
  props: {
    communityId: {
      type: Number,
      required: true
    },
    isMobile: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      loadingNewDoc: false,
      mobileKey: 1
    };
  },
  computed: {
    ...mapState([
      'currentUser',
      'entitlements',
      'policyRoles'
    ]),
    ...mapGetters([
      'readOnly'
    ]),
    isPagesViewMode() {
      return this.$route.name.includes('pages');
    },
    storyTypes() {
      if (this.isPagesViewMode) {
        return [{ type: 'page', defaults: { entry_type: 'Page' } }];
      }

      let opts = [{ type: 'story', defaults: {} }];
      if (hasPolicyRole(this.currentUser, this.communityId, this.policyRoles.editor.accessStoryTypes)) {
        opts.push(
          { type: 'stream', defaults: { entry_type: 'Stream' } },
          { type: 'link', defaults: { entry_type: 'LinkPost' } }
        );
      }
      if (this.entitlements && this.entitlements.includes('use_packages')) {
        opts.push({ type: 'package', defaults: { entry_type: 'LandingPage' } });
      }
      if (this.entitlements && this.entitlements.includes('use_mapstacks')) {
        opts.push({ type: 'mapstack', defaults: { entry_type: 'MapStack' } });
      }
      if (this.entitlements && this.entitlements.includes('use_customstories')) {
        opts.push({ type: 'custom story', defaults: { entry_type: 'CustomStory' }, status: 'beta' });
      }
      return opts;
    }
  },
  watch: {
    isMobile() {
      this.mobileKey++;
    },
    loadingNewDoc() {
      if (this.loadingNewDoc) {
        this.$store.commit('appLoader/SET_LOADING', true);
      } else {
        this.$store.commit('appLoader/SET_LOADING', false);
      }
    }
  },
  methods: {
    newDoc(type, params = {}) {
      this.loadingNewDoc = true;
      params.community_id = this.communityId;
      this.createDoc(type, params);
    },
    async createDoc(type, params) {
      let docPath = 'compose';
      if (type === 'package') docPath = 'package';
      if (type === 'custom story') docPath = 'customstory';
      try {
        const doc = await request('/newstory', {
          method: 'POST',
          body: params,
          opts: { handleError: false } // stop error handling from bubbling up to Ractive state
        });
        window.location.assign(`/${docPath}/${doc.uuid}`);
      } catch (error) {
        console.log('error', error);
        this.$store.commit('messageBanner/PUT_NOTICE', {
          type: 'negative',
          message: error.message
        });
      }
    }
  }
};
</script>
