<template>
  <div v-if="blockIds.length > 0" class="m-layout-mode__tools">
    <form-subsection :label="formattedLabel">
      <!-- have to re-render all these guys via key because change event not emitted to Resonance :*( -->
      <div v-if="displayParagraphOptions" :key="blockIds.join('-')" class="m-layout-mode__group">
        <label class="is-fixed">Styles</label>
        <res-checkbox
          :checked="currentDropcap"
          class="m-layout-mode__checkbox"
          @change="saveField('dropcap', !currentDropcap || null)">
          <input
            id="dropcap"
            :disabled="readOnly"
            type="checkbox"
            name="dropcap"
          />
          <label>Add drop cap</label>
        </res-checkbox>
        <res-checkbox
          :checked="currentLeadgraf"
          class="m-layout-mode__checkbox"
          @change="saveField('leadgraf', !currentLeadgraf || null)">
          <input
            id="leadgraf"
            :disabled="readOnly"
            type="checkbox"
            name="leadgraf"
          />
          <label>Make text large</label>
        </res-checkbox>
        <res-checkbox
          :checked="currentEndMark"
          class="m-layout-mode__checkbox"
          @change="saveField('endmark', !currentEndMark || null)">
          <input
            id="endMark"
            :disabled="readOnly"
            type="checkbox"
            name="endMark"
          />
          <label>Add end mark</label>
        </res-checkbox>
      </div>
      <div v-if="displayAlignmentOptions" class="m-field m-layout-mode__group">
        <label class="is-fixed">Alignment</label>
        <span class="m-radio-button-group">
          <label v-for="option in positionOptions" :key="option.value" :title="option.label">
            <input
              :checked="currentPosition === option.value"
              :value="option.value"
              type="radio"
              @change="saveField('position', option.value)"/>
            <span type="button" class="is-secondary -small">
              <res-icon :title="option.label" :name="option.icon" size="medium"/>
            </span>
          </label>
        </span>
      </div>
      <div v-if="displayGroupingOptions" class="m-field m-layout-mode__group">
        <label class="is-fixed">Grouping</label>
        <span class="m-radio-button-group">
          <label v-for="option in groupingOptions" :key="option.value" :title="option.label">
            <input
              :checked="currentGrouping === option.value"
              :value="option.value"
              type="radio"
              @change="saveField('grouping', option.value)"/>
            <span type="button" class="is-secondary -small">
              <res-icon :title="option.label" :name="option.icon" size="medium"/>
            </span>
          </label>
        </span>
      </div>
      <div v-if="displayGroupingMessage" class="m-layout-mode__grouping-message">
        <span>This {{ objectLabel }} is part of a group.</span>
        <res-button design="default" paddingless>
          <res-icon name="grid"/>
          <button type="button" @click="selectAllInGroup">Select all {{ pluralize(objectLabel) }} in group</button>
        </res-button>
      </div>
      <div v-if="displayLayoutOptions" class="m-field">
        <label class="is-fixed">Layout</label>
        <span class="m-radio-button-group">
          <label v-for="option in productCardLayoutOptions" :key="option.value" :title="option.label">
            <input
              :checked="currentLayout === option.value"
              :value="option.value"
              type="radio"
              @change="saveSubdocLayout(option.value)"/>
            <span type="button" class="is-secondary -small">
              <res-icon :title="option.label" :name="option.icon" size="medium"/>
            </span>
          </label>
        </span>
      </div>
    </form-subsection>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import AutosaveRadio from '../autosave-radio.vue';
import FormSubsection from './form-subsection.vue';
import getOp from '../../../shared/delta/get_op';
import getAttributes from '../../../shared/delta/get_attributes';
import getOffset from '../../../shared/delta/get_offset';
import groupItemsMatch from '../../../shared/delta/group_items_match';
import { uniq, capitalize, intersection } from 'lodash';
import { Delta } from 'rich-text';
import parseGroups from '../../../shared/delta/parse_groups';
import hasMinimalLayout from '../../../shared/has_minimal_layout';
import * as docLabels from '../../../client/config/doc_labels';
import pluralize from '../../../client/lib/pluralize';
import {
  productCardLayoutOptions,
  alignmentOptions,
  groupOptions,
  groupPositions,
  nonWideSubdocs,
  nonFullBleedSubdocs,
  nonAlignedSubdocs,
  nonWideBlocks,
} from '../../config/layoutOptions';
import blockLabels from '../../config/blockLabels';

export default {
  components: {
    AutosaveRadio,
    FormSubsection,
  },
  props: {
    blockIds: {
      type: Array,
      required: true
    },
    postMessage: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      CONFLICTING_SELECTION: Symbol('conflicting'),
      // TODO: Hard coding read only for now
      readOnly: false,
      productCardLayoutOptions,
    };
  },
  computed: {
    ...mapState([
      'autosave'
    ]),
    ...mapGetters([
      'currentDoc',
      'draftBody'
    ]),
    // Only display paragraph options if every block is a paragraph
    displayParagraphOptions() {
      return this.blockIds.every(id => this.getObject(id).hasOwnProperty('paragraph'));
    },
    // Only display alignment options if every block is _not_ a paragraph
    displayAlignmentOptions() {
      return this.blockIds.every(id => this.canSetAlignment(this.getObject(id)));
    },
    displayLayoutOptions() {
      return this.blockIds.every(id => this.canUseLayout(this.getObject(id)));
    },
    displayGroupingOptions() {
      // blocks must be both "adjacent" as well as images in order to group
      if (!this.blocksAreAdjacent) {
        return false;
      }

      const objects = this.blockIds.map(id => this.getObject(id));
      const allObjectsMatch = groupItemsMatch(objects);
      if (!allObjectsMatch) {
        return false;
      }

      const isImages = objects[0].hasOwnProperty('image');
      return isImages ? objects.length > 1 : objects.length === 2;
    },
    displayGroupingMessage() {
      const grouping = this.currentGrouping;
      return (
        grouping &&
        grouping !== this.CONFLICTING_SELECTION &&
        parseInt(grouping) > this.blockIds.length
      );
    },
    blocksAreAdjacent() {
      let previousOffset = null;
      // Loop thru and if any two blocks are separated by a newline or more,
      // they are not inline - must be inline as well as adjacent
      for (let id of this.blockIds) {
        const offset = this.getOffset(id);
        if (previousOffset && offset - previousOffset > 2) {
          return false;
        }
        previousOffset = offset;
      }
      return true;
    },
    currentPosition() {
      return this.getBlockValue('position') || 'default';
    },
    currentGrouping() {
      return this.getBlockValue('grouping');
    },
    currentLayout() {
      return this.getBlockDocData('layout') || 'full_with_square_img';
    },
    currentDropcap() {
      return this.getBlockValue('dropcap');
    },
    currentLeadgraf() {
      return this.getBlockValue('leadgraf');
    },
    currentEndMark() {
      return this.getBlockValue('endmark');
    },
    objectLabel() {
      if (this.blockIds.length > 1) {
        return 'Group';
      } else {
        const object = this.getObject(this.blockIds[0]);

        const key = Object.keys(object)[0];
        if (blockLabels.hasOwnProperty(key)) {
          return blockLabels[key];
        } else if (key === 'doc') {
          return docLabels[object.doc.type];
        } else {
          return key;
        }
      }
    },
    formattedLabel() {
      // Display `HTML` in all caps vs. `Html`
      const label = this.objectLabel === blockLabels.html ? this.objectLabel : capitalize(this.objectLabel);

      return `${label} options`;
    },
    positionOptions() {
      // Available to all entry types + object types
      let result = [alignmentOptions.center];
      const hasGallery = this.blockIds.some(id => {
        const object = this.getObject(id);
        return object.hasOwnProperty('doc') && object.doc.type === 'gallery';
      });
      if (hasGallery) {
        return result;
      }
      result.push(alignmentOptions.alignLeft);

      // Add feature-only layout options
      if (hasMinimalLayout(this.currentDoc.head.contents.draft.entry_layout)) {
        result.push(alignmentOptions.hangLeft);
        // Add additional layout options, if applicable
        if (this.blockIds.every(id => this.canSetWide(this.getObject(id)))) {
          result.push(alignmentOptions.wide);
        }
        if (this.blockIds.every(id => this.canSetFullBleed(this.getObject(id)))) {
          result.push(alignmentOptions.full);
        }
        result.push(alignmentOptions.hangRight);
      }

      result.push(alignmentOptions.alignRight);
      return result;
    },
    groupingOptions() {
      if (this.blockIds.length < 2) {
        return [];
      }
      let result = [groupOptions.none];
      switch(this.blockIds.length) {
      case 2:
        result.push(groupOptions.twoUp);
        break;
      case 3:
        result = result.concat([
          groupOptions.threeUpTop,
          groupOptions.threeUpBottom
        ]);
        break;
      case 4:
        result.push(groupOptions.fourUp);
        break;
      }
      return result;
    }
  },
  methods: {
    pluralize(string) {
      return pluralize(string);
    },
    getOffset(id) {
      return getOffset(this.draftBody, op => op.attributes && op.attributes.id === id);
    },
    getBlockAttributes(id) {
      const offset = this.getOffset(id);
      return getAttributes(this.draftBody, offset);
    },
    getBlockDocData(attr) {
      // get data attributes from (first) doc obj
      const blockObj = this.getObject(this.blockIds[0]);
      const type = blockObj.doc.type;
      if (blockObj.doc[type] && blockObj.doc[type][attr]) {
        return blockObj.doc[type][attr];
      }
      return null;
    },
    getObject(id) {
      const offset = this.getOffset(id);
      const op = getOp(this.draftBody, offset - 1);
      if (op == null || typeof op.insert === 'string') {
        return { paragraph: true };
      } else {
        return op.insert;
      }
    },
    getBlockValue(attr) {
      // Returns the value for the radio-button-group active value for the given
      // attribute:
      // A) If only one item is selected, return that item's attribute value
      // B) If multiple items are selected:
      //    - Return an attribute value if all items share the same value
      //    - Return no valid attribute value if not all share the same value
      if (this.blockIds.length > 1) {
        let values = this.blockIds.map(id => this.getBlockAttributes(id)[attr] || null);
        values = uniq(values);
        return values.length > 1 ? this.CONFLICTING_SELECTION : values[0];
      } else {
        return this.getBlockAttributes(this.blockIds[0])[attr] || false;
      }
    },
    saveField(field, value) {
      // 1) If updates are being applied to only certain members of a grouping,
      //    reset that entire grouping before performing the actual update. // WUT
      const resets = this.resetIncompleteGroups();

      // 2) Compose the layout update, removing invalid layouts if necessary
      const updates = this.updateFieldValue(field, value);

      this.save([{ p: ['draft', 'body'], t: 'rich-text', o: resets.compose(updates) }]);
    },
    saveSubdocLayout(value) {
      const delta = this.blockIds.reduce((result, id) => {
        var blockObj = this.getObject(id);
        var type = blockObj.doc.type;
        blockObj.doc[type] = { layout: value };

        var offset = this.getOffset(id);
        var delta = new Delta()
          .retain(offset - 1)
          .delete(1)
          .insert(blockObj);

        return result.compose(delta);
      }, new Delta());
      this.save([{ p: ['draft', 'body'], t: 'rich-text', o: delta }]);
    },
    async save(delta) {
      try {
        await this.$store.dispatch('resolveOnLocalUpdate', { docId: this.autosave.currentDocId, delta: delta });
        this.postMessage({
          event: 'refetch'
        });
      } catch (er) {
        // resolve on localupdate doesn't reject with an error message
        this.postMessage({
          event: 'error'
        });
      }
    },
    resetIncompleteGroups() {
      return parseGroups(this.draftBody).reduce((result, group) => {
        var groupIds = group.map(line => line.id);
        var selectedGroupIds = intersection(this.blockIds, groupIds);

        // If the group is partially selected...
        if (selectedGroupIds.length > 0 &&
            selectedGroupIds.length !== groupIds.length) {

          // Create reset deltas for all the group members
          for (let line of group) {
            var delta = new Delta().retain(line.offset).retain(1, { grouping: null });
            result = result.compose(delta);
          }
        }

        return result;
      }, new Delta());
    },
    updateFieldValue(field, value) {
      return this.blockIds.reduce((result, id) => {
        var payload = { [field]: value };

        var attrs = this.getBlockAttributes(id);
        // there are only 3 alignment options ("groupPositions") which are "Valid" for groups;
        // if a non valid option is chosen, ungroup blocks--no it is not obvious based on the UI
        if (field === 'position' && groupPositions.indexOf(value) === -1) {
          payload.grouping = null;
        } else if (field === 'grouping' && (groupPositions.indexOf(attrs.position) === -1 || this.currentPosition === this.CONFLICTING_SELECTION)) {
          payload.position = 'default';
        }

        var offset = this.getOffset(id);
        var delta = new Delta().retain(offset).retain(1, payload);
        return result.compose(delta);
      }, new Delta());
    },
    canSetAlignment(object) {
      const isSubdoc = object.hasOwnProperty('doc');
      return !(
        (isSubdoc && nonAlignedSubdocs.includes(object.doc.type)) ||
        object.hasOwnProperty('paragraph')
      );
    },
    canSetWide(object) {
      const isSubdoc = object.hasOwnProperty('doc');
      return !(
        (isSubdoc && nonWideSubdocs.includes(object.doc.type)) ||
        nonWideBlocks.some(type => object.hasOwnProperty(type))
      );
    },
    canSetFullBleed(object) {
      const isSubdoc = object.hasOwnProperty('doc');
      return !(
        (isSubdoc && nonFullBleedSubdocs.includes(object.doc.type)) ||
        nonWideBlocks.some(type => object.hasOwnProperty(type))
      );
    },
    canUseLayout(object) {
      const isSubdoc = object.hasOwnProperty('doc');
      return (isSubdoc && object.doc.type === 'productcard');
    },
    selectAllInGroup() {
      const firstId = this.blockIds[0];
      const group = parseGroups(this.draftBody).find(group => {
        return group.some(item => item.id === firstId);
      });
      const blockIds = group.map(item => item.id);
      this.postMessage({
        event: 'setSelection',
        blockIds: blockIds
      });
    },
  }
};

</script>
