import { has, pick, omit } from 'lodash';
import { Delta } from 'rich-text';
import document from '../../../config/schema/rich-text.json';
const OBJECT_TYPES = Object.keys(document.object.properties);

export default function(contents) {
  let changes = [];
  let delta;

  // story
  if (has(contents, 'draft.body')) {
    delta = convertObjects(contents.draft.body);
    if (delta.length() > 0) {
      changes.push({ p: ['draft', 'body'], t: 'rich-text', o: delta });
    }
  }

  // aside, scorecard
  if (contents.body) {
    delta = convertObjects(contents.body);
    if (delta.length() > 0) {
      changes.push({ p: ['body'], t: 'rich-text', o: delta });
    }
  }

  // scorecard
  if (contents.pros) {
    delta = convertObjects(contents.pros);
    if (delta.length() > 0) {
      changes.push({ p: ['pros'], t: 'rich-text', o: delta });
    }
  }

  // scorecard
  if (contents.cons) {
    delta = convertObjects(contents.cons);
    if (delta.length() > 0) {
      changes.push({ p: ['cons'], t: 'rich-text', o: delta });
    }
  }

  // story (map stack)
  if (has(contents, 'draft.map_points')) {
    const mapPoints = contents.draft.map_points;
    for (let [i, mapPoint] of mapPoints.entries()) {
      delta = convertObjects(mapPoint.body);
      if (delta.length() > 0) {
        changes.push({ p: ['draft', 'map_points', i, 'body'], t: 'rich-text', o: delta });
      }
    }
  }

  return changes;
}

function convertObjects(delta) {
  const fixDelta = new Delta();
  for (let op of delta.ops) {
    if (op.insert === 1) {
      fixDelta.delete(1).insert(pick(op.attributes, OBJECT_TYPES), omit(op.attributes, OBJECT_TYPES));
    } else {
      fixDelta.retain(op.insert.length || 1);
    }
  }
  return fixDelta.chop();
}
