<template>
  <div :style="fullHeight" class="m-community-select__menu-wrap">
    <ul v-if="networks" class="m-community-select__list">
      <template v-for="network in networks">
        <community-cell
          v-if="network.communities.length === 1"
          :community="network.communities[0]"
          :defaultLogoUrl="defaultLogoUrl"
          :selectionId="selectionId"
          :key="network.name"
          @communityId="selectCommunity($event)"
        />
        <ul v-else :key="network.name" class="m-community-select__list">
          <li :class="expandedNetworks[network.id] ? 'is-expanded' : 'is-collapsed'" class="m-community-select__network">
            <div class="m-community-select__item" @click.prevent="expandNetwork(network.id)">
              <div class="m-community-select__avatar">
                <div v-if="network.logo" v-html="network.logo"/>
                <img v-else :src="defaultLogoUrl" class="m-community-select__icon" style="height:20px" />
              </div>
              <span class="m-community-select__name">{{ network.name }}</span>
              <res-icon size="medium" name="add-circle"/>
              <res-icon size="medium" name="minus-circle"/>
            </div>
            <ul class="m-community-select__sub-list">
              <community-cell
                v-for="community in network.communities"
                :key="community.id"
                :community="community"
                :defaultLogoUrl="defaultLogoUrl"
                :selectionId="selectionId"
                @communityId="selectCommunity($event)"
              />
            </ul>
          </li>
        </ul>
      </template>
    </ul>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import CommunityCell from './community-cell.vue';

export default {
  components: {
    CommunityCell
  },
  props: {
    allowedRoles: {
      type: Object,
      required: true
    },
    activeMemberships: {
      type: Array,
      default: () => ([])
    },
    defaultLogoUrl: {
      type: String,
      required: true,
    },
    networks: {
      type: Array,
      default: null
    },
    menuOpen: {
      type: Boolean,
      required: true
    }
  },
  data() {
    return {
      selectionId: null,
      expandedNetworks: {},
      fullHeight: null
    };
  },
  computed: {
    ...mapState(['currentUser'])
  },
  mounted() {
    this.setHeight();
    window.addEventListener('click', this.closeMenu, true);
    window.addEventListener('resize', this.setHeight);
  },
  beforeDestroy() {
    window.removeEventListener('click', this.closeMenu, true);
    window.removeEventListener('resize', this.setHeight);
  },
  methods: {
    closeMenu(e) {
      if (!e) return;
      const communityMenu = document.querySelector('.m-community-select__list');
      const communityMenuTrigger = document.querySelector('.m-community-select__menu-trigger');
      if (this.menuOpen && !communityMenu.contains(e.target) && !communityMenuTrigger.contains(e.target)) {
        this.$emit('closeMenu');
      }
    },
    setHeight() {
      this.fullHeight = `height: ${window.innerHeight}px`;
    },
    expandNetwork(id) {
      this.expandedNetworks[id] = !this.expandedNetworks[id];
      this.expandedNetworks = Object.assign({}, this.expandedNetworks);
    },
    isAuthorized(id) {
      return this.currentUser.networkRoles.some(r => r in this.allowedRoles)
        || this.activeMemberships.some(m => m.community.id === id);
    },
    async selectCommunity(id) {
      if (!id) return;
      if (id && id === this.selectionId) return;
      if (this.activeMemberships.length === 0 && this.currentUser.networkRoles.length === 0) return;
      if (!this.isAuthorized(id)) id = this.activeMemberships[0].community.id;

      this.selectionId = id;
      localStorage.setItem('dashboardCommunityId', id);
      this.$router.push({ query: { community_id: id } });
      this.$emit('closeMenu');
    }
  }
};
</script>
