module.exports = function getEventDatetime(history, eventTypes, { latest } = { latest: true }) {
  // Some older stories may not have a `publish_history` list
  history = (history || []).slice();
  // If we want the latest instance of the event, reverse the array before searching it
  if (latest) {
    history.reverse();
  }
  const event = history.find(event => eventTypes.indexOf(event.event_type) > -1);

  // If the event is 'backdated' use the backdated_date
  if (event) {
    return event.event_type === 'backdated'
      ? event.backdated_date
      : event.date;
  }
};
