<template>
  <div>
    <editor-nav v-if="currentDoc" />
    <component :is="action" :uuid="uuid" />
  </div>
</template>

<script>
import Layout from '../../components/layout-mode/layout.vue';
import EditorNav from '../../components/header/editor-nav.vue';
import CommunityMismatchError from '../../../shared/errors/community_mismatch';
import NotFoundError from '../../../shared/errors/not_found';
import { COMMUNITY_CHANGED, UNSAVED_WARNING } from '../../../client/config/messages';
import moment from 'moment';
import request from '../../../client/lib/request';
import { SET_DOC } from '../../store/commands';
import { getEntryById } from '../../../client/actions/entry';
import { mapState, mapGetters } from 'vuex';

export default {
  components: {
    Layout,
    EditorNav
  },
  props: {
    // action and uuid props are passed from the router
    action: {
      type: String,
      required: true,
      default: '',
    },
    uuid: {
      type: String,
      required: true,
      default: '',
    }
  },
  data() {
    return {
      fatalError: false,
      MESSAGE_RESCUE_VERSION: ['story'],
      beforeUnload: () => {}
    };
  },
  computed: {
    ...mapState(['appName', 'autosave']),
    ...mapGetters(['currentDoc', 'docLabel', 'isPipelineDirty']),
    communityId() {
      return this.currentDoc && this.currentDoc.community_id;
    },
    docIds() {
      return Object.keys(this.autosave.docsById).map((docId) => parseInt(docId));
    },
  },
  watch: {
    ['currentDoc.title']() {
      if (this.currentDoc && this.currentDoc.title) {
        document.title = `${this.currentDoc.title} | ${this.appName}`;
      }
    }
  },
  async created() {
    if (this.uuid) {
      const doc = await this.getDoc(this.uuid);
      await this.haltIfTrashed(doc);
      this.$store.commit(SET_DOC, { doc, path: 'doc' });
      await this.checkCommunityContexts(doc); // makes API request; unlikely to be an error, so dont hold up render for this
    }
  },
  mounted() {
    this.initUnsavedWarning();
    this.initCommunityChangedNotice();
  },
  beforeDestroy() {
    window.removeEventListener('beforeunload', this.beforeUnload);
  },
  methods: {
    async getDoc(uuid) {
      const doc = await request('/api/content/docs/' + uuid, {
        query: { includes: 'owner,updateUser,community,external_ids' },
        opts: { cache: true, force: true, handleError: false }
      });
      return doc;
    },
    // If the doc is marked as trashed over 30 days ago, simply render not found
    haltIfTrashed(doc) {
      var trashedAt = doc.head.contents.trashed_at;
      var threshold = moment().subtract(30, 'days');
      if (trashedAt && moment(trashedAt) < threshold) {
        // Trigger ErrorBoundary to render NotFound
        throw new NotFoundError('Doc was trashed over 30 days ago');
      }
      return Promise.resolve();
    },
    // There's a chance that a doc community change action will fail half way thru,
    // leading to a community mismatch b/t the SBN entry and the Content API doc. If
    // that's the case we should throw a fatal error, prompting us to triage manually.
    async checkCommunityContexts(doc) {
      const entry = await getEntryById(doc.entry_id);

      if (entry && entry.community_id && doc.community_id !== entry.community_id) {
        // Trigger ErrorBoundary to render NotFound
        throw new CommunityMismatchError(doc);
      }
      return Promise.resolve();
    },
    initUnsavedWarning() {
      window.addEventListener('beforeunload', this.beforeUnload = (event) => {
        if (this.isPipelineDirty) {
          event.preventDefault();
          event.returnValue = UNSAVED_WARNING;
          return UNSAVED_WARNING;
        }
      });
    },
    initCommunityChangedNotice() {
      // see changeDocCommunityAndRedirect for how this query param appears
      const params = this.$route.query;
      if (params.communityChanged) {
        this.$store.commit('messageBanner/PUT_NOTICE', {
          type: 'positive',
          message: COMMUNITY_CHANGED.replace('{docLabel}', this.docLabel)
        });
      }
    }
  },
};
</script>
