<template>
  <div>
    <res-banner v-for="notice in notifications"
                :key="notice.id"
                :id="notice.id"
                :design="designForNotice(notice)"
                :persistent="notice.persistent || false"
                :timeout="notice.timeout || false"
                inert
                @close="deleteNotice(notice)">
      <res-icon v-if="notice.icon" :name="notice.icon"/><span v-html="notice.message" />
    </res-banner>
  </div>
</template>

<script>
import store from './store.js';
import { mapState } from 'vuex';

export default {
  name: 'Message',
  computed: {
    ...mapState('messageBanner', ['notifications'])
  },
  created() {
    // check to see if module is already registered before registering again
    // because HMR re-registers this during development
    if (!this.$store.state.messageBanner) this.$store.registerModule('messageBanner', store);
  },
  methods: {
    designForNotice(notice) {
      return notice.type || 'neutral';
    },
    deleteNotice(notice) {
      this.$store.commit('messageBanner/DELETE_NOTICE', notice.id);
    },
  }
};
</script>
