// Returns an array-of-arrays, where each sub-array is a run of elements from
// the collection for which the callback function returns true.
//
// @param collection: An array.
// @param predicate:
//    A function called with the current chunk, and the next element of the
//    collection. Return a boolean indicating if the item should be added to the
//    current chunk (true), or should become the first element of a new chunk (false).
module.exports = function chunkBy(collection, predicate) {
  if (collection.length < 2) {
    return [collection];
  }
  var chunks = [];
  var chunk = [collection[0]];
  for (let next of collection.slice(1)) {
    if (predicate(chunk, next)) {
      chunk.push(next);
    } else {
      chunks.push(chunk);
      chunk = [next];
    }
  }
  // Push hanging values
  chunks.push(chunk);
  return chunks;
};
