export const CurrentUser = `
  query CurrentUser($size: String!) {
    currentUser {
      id
      createdOn
      fullOrUserName
      email
      timeZone
      networkRoles
      memberships: communityMemberships {
        status
        roles
        community {
          id
          name
          faviconUrl
          network {
            id
            name
            svgLogo: siteConfigString(key: "svg_small_mark_full_color")
          }
        }
      }
      networkMemberships {
        networkId
        imageUrl(size: $size)
        network {
          primaryCommunity {
            id
          }
        }
      }
    }
  }
`;

export const UserChorusVideo = `
  query UserChorusVideo($scopeId: Int!) {
    currentUser {
      canAccessChorusVideo(scopeType: COMMUNITY, scopeId: $scopeId)
    }
  }
`;

export const UserTwitterHandle = `
  query UserTwitterHandle($id: Int!) {
    user(id: $id) {
      twitterUsername
    }
  }
`;

export const UserName = `
  query UserName($id: Int!) {
    user(id: $id) {
      id
      fullOrUserName
    }
  }
`;

export const UserNames = `
  query UserNames($ids: [Int!]!) {
    users(ids: $ids) {
      id
      fullOrUserName
    }
  }
`;

export const UserNamesTitles = `
  query UserNamesTitles($ids: [Int!]!, $communityId: Int!) {
    users(ids: $ids) {
      id
      fullOrUserName
      authorProfile(communityId: $communityId) {
        title
      }
    }
  }
`;

export const SearchContributors = `
  query SearchContributors($text: String!, $communityId: Int!) {
    searchUsers(query: {
      text: $text,
      prefix: true,
      communityIds: [$communityId],
      roles: [FREELANCE, AUTHOR, TRUSTED_AUTHOR, EDITOR, MANAGER],
      fallbackNetworkRoles: [NETWORK_ADMIN, NETWORK_EDITOR],
      statuses: [ACTIVE, PROVISIONAL, PERMISSIONS_SUSPENDED],
      perPage: 25
    }) {
      results {
        id
        fullOrUserName
        username
      }
    }
  }
`;
