<template>
  <span class="m-dashboard__table__activity__text">
    <span v-if="!filter">
      <span :class="activityIconClass" />
      {{ docStatus }}
      <span
        v-if="doc.status === 'PUBLISHED' || doc.status === 'UPDATED'"
        class="m-dashboard__table__activity__link"
      >
        <a :href="`https://${network.schemaDomain}/e/${doc.entry_id}`" target="_blank">
          <res-icon size="smaller" name="publish" />
        </a>
      </span>

      {{ datePrefix }}
      {{ statusDate ? (['SCHEDULED', 'PUBLISHED'].includes(doc.status) ? fullDate : timeAgo()) : '' }}
      <res-badge
        v-if="doc.head.contents.embargoed"
        :size="'smaller'"
        :design="'rose'" >
        <span>Embargoed</span>
      </res-badge>
      <res-badge
        v-if="isLivePost"
        :size="'smaller'"
        :design="'apple'"
        class="res-badge-light-apple">
        <span>Live</span>
      </res-badge>
      <res-badge
        v-if="showHiddenBadge"
        :size="'smaller'"
        :design="'gray'">
        <span>Hidden</span>
      </res-badge>
      <res-badge
        v-if="showExpirationBadge"
        :size="'smaller'"
        :design="'gold'">
        <res-icon name="clock"/>
        <span>{{ hideScheduledMessage }}</span>
      </res-badge>
    </span>
    <span v-else>
      {{ timeAgo(doc.updated_at) }}
      {{ doc.updateUser.id ? `by ${doc.updateUser.fullName}` : '' }}
    </span>
  </span>
</template>

<script>
import sentenceCase from '../../shared/sentence_case';
import docStatuses from '../../client/config/doc_statuses';
import isLivePost from '../../shared/is_live_post';
import { mapState } from 'vuex';
import moment from 'moment-timezone';
import getEventDatetime from '../../shared/get_event_datetime';

export default {
  props: {
    doc: {
      type: Object,
      required: true,
      default: () => ({}),
    },
    filter: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      activityIconClass: `m-dashboard__table__activity__icon ${this.doc.status.toLowerCase()}`,
      docStatus: sentenceCase(docStatuses[this.doc.status]),
      isPublishedOrScheduled: ['PUBLISHED', 'UPDATED', 'SCHEDULED'].includes(this.doc.status),
      isLivePost: isLivePost(this.doc.head.contents),
    };
  },
  computed: {
    ...mapState([
      'currentUser',
      'currentCommunity',
      'dateFormat',
      'network'
    ]),
    showHiddenBadge() {
      return this.doc.head.contents.hidden && this.isPublishedOrScheduled;
    },
    showExpirationBadge() {
      return this.doc.head.contents.expiration_scheduled_for
        && !this.doc.head.contents.hidden
        && this.isPublishedOrScheduled;
    },
    userTimezone() {
      return this.currentUser.timeZone || this.currentCommunity.timeZone;
    },
    statusDateTime() {
      return moment.tz(this.statusDate, this.userTimezone);
    },
    fullDate() {
      const dateFormat = `${this.dateFormat}[ at ]h:mma z`;
      return this.statusDateTime.format(dateFormat);
    },
    now() {
      return moment.tz(moment(), this.userTimezone);
    },
    datePrefix() {
      const status = this.doc.status;
      const diff = this.now.diff(this.statusDateTime, 'days');

      if (!this.statusDate) {
        return '';
      } else if (status === 'SCHEDULED') {
        return 'for';
      } else if (status === 'UPDATED') {
        return 'from';
      } else if (status === 'DRAFT') {
        return diff < 8 ? 'modified' : 'modified on';
      } else if (status === 'PUBLISHED' || diff > 8) {
        return 'on';
      }

      return '';
    },
    statusDate() {
      const history = this.doc.head.contents.publish_history;

      switch (this.doc.status) {
      case 'DRAFT':
      case 'UPDATED':
        return getEventDatetime(history, ['legacy_updated_at']) ||
              this.doc.updated_at;
      case 'APPROVED':
        return getEventDatetime(history, ['approved']);
      case 'SCHEDULED':
        return this.doc.head.contents.publish_scheduled_for;
      case 'SUBMITTED':
        return getEventDatetime(history, ['submitted']);
      case 'PUBLISHED':
        return getEventDatetime(history, [
          'published',
          'published_via_schedule',
          'backdated',
        ]);
      }
    },
    hideScheduledMessage() {
      const hidingDate = this.doc.head.contents.expiration_scheduled_for;
      const now = moment.tz(moment(), this.userTimezone);
      const diffMinutes = (now.diff(hidingDate, 'minutes')) * -1;
      const diffHours = (now.diff(hidingDate, 'hours')) * -1;
      const diffDays = (now.diff(hidingDate, 'days')) * -1;
      const diffYears = (now.diff(hidingDate, 'years')) * -1;
      if (diffYears >= 1) {
        const year = diffYears === 1 ? 'year' : 'years';
        return `Hide in ${diffYears} ${year}`;
      } else if (diffDays >= 1) {
        const day = diffDays === 1 ? 'day' : 'days';
        return `Hide in ${diffDays} ${day}`;
      } else if (diffHours >= 1) {
        const hour = diffHours === 1 ? 'hour' : 'hours';
        return `Hide in ${diffHours} ${hour}`;
      } else {
        const minute = diffMinutes === 1 ? 'minute' : 'minutes';
        return `Hide in ${diffMinutes} ${minute}`;
      }
    }
  },
  methods: {
    timeAgo(time = null) {
      const datetime = time ? moment.tz(time, this.userTimezone) : this.statusDateTime;

      if (this.displayRelative(datetime)) {
        let diffUnit = this.getDiffUnit(datetime);
        if (diffUnit === 'now') {
          return 'just now';
        } else {
          const diff = this.now.diff(datetime, diffUnit);
          // Ensure we don't show a plural unit for a singular count
          diffUnit = diff === 1 ? diffUnit.substring(0, diffUnit.length - 1) : diffUnit;
          return `${diff} ${diffUnit} ago`;
        }
      }

      return datetime.format(`${this.dateFormat}[ at ]h:mma z`);
    },
    displayRelative(datetime) {
      const forceRelative = Boolean(this.filter);
      const diff = this.now.diff(datetime, 'days');
      return forceRelative || diff < 8;
    },
    getDiffUnit(datetime) {
      if (this.now.diff(datetime, 'minutes') === 0) {
        return 'now';
      } else if (this.now.diff(datetime, 'minutes') < 60) {
        return 'minutes';
      } else if (this.now.diff(datetime, 'hours') < 24) {
        return 'hours';
      }

      return 'days';
    }
  }
};
</script>
