import moment from 'moment-timezone';
import uuid from 'uuid';
import toRichText from '../../../shared/to_rich_text';
import { isCommunityPostAuthor } from '../authorization';
import isCustomstory from '../../../shared/is_customstory';

export default function(doc, user) {
  switch (doc.schema_name) {
  case 'story':
    if (isCustomstory(doc.head.contents)) {
      return customstoryContents();
    }
    return storyContents(user, doc.community);
  case 'gallery':
    return galleryContents();
  case 'aside':
    return asideContents();
  case 'table':
    return tableContents();
  case 'poll':
    return pollContents();
  case 'scorecard':
    return scorecardContents(doc);
  case 'imageslider':
    return imagesliderContents();
  case 'productcard':
    return productcardContents();
  default:
    return {};
  }
}

function storyContents(user, community) {
  return {
    draft: {
      hed: toRichText('Untitled'),
      dek: toRichText(''),
      slug: toRichText(''),
      body: toRichText(''),
      teaser: toRichText(''),
      entry_password: toRichText(''),
      autotweets: [
        { body: toRichText('') },
        { body: toRichText('') }
      ],
      disable_ads: false,
      do_not_distribute: isCommunityPostAuthor(user, parseInt(community.id)),
      hide_from_amp: false,
      is_restricted_in_privacy_consent_region: false,
      noindex: false,
      nofollow: false,
      paywalled: false,
      affiliate_link_rewriting_disabled: false,
      styles: '',
      site_groups: [],
      super_groups: [],
      campaign_groups: [],
      whitelisted_network_ids: [],
      contributors: [],
      additional_byline: toRichText(''),
      internal_title: toRichText(''),
      internal_description: toRichText(''),
      social_title: toRichText(''),
      social_description: toRichText(''),
      seo_title: toRichText(''),
      seo_description: toRichText(''),
      canonical_url_override: toRichText(''),
      structured_tags: {}
    },
    embargoed: false,
    hidden: false,
    publish_history: [],
    image_history: [],

    ...(community.printSlugEnabled ? {
      print_slug: toRichText('')
    } : {})
  };
}

function customstoryContents() {
  const contents = storyContents();
  contents.draft.hide_from_amp = true;

  return contents;
}

function galleryContents() {
  return {
    title: toRichText('Untitled'),
    images: [],
    image_history: []
  };
}

function asideContents() {
  return {
    title: toRichText('Untitled'),
    body: toRichText('')
  };
}

function tableContents() {
  return {
    title: toRichText('Untitled'),
    credit: toRichText(''),
    caption: toRichText(''),
    columns: [],
    rows: [],
    sort_column: 0,
    sort_descending: false,
    searchable: false,
    sortable: false,
    per_page: 0,
    google_spreadsheet_url: '',
    upload_filename: '',
    style: 'table'
  };
}

function pollContents() {
  return {
    title: toRichText(''),
    options: [{
      uuid: uuid.v4(),
      value: toRichText('')
    }],
    close_date: moment().add(2, 'weeks').toISOString()
  };
}

function scorecardContents(doc) {
  var contents = {
    title: toRichText('Untitled')
  };

  var type = doc.head.contents.type;
  switch (type) {
  case 'product':
    contents.pros = toRichText('');
    contents.cons = toRichText('');
    break;
  case 'venue':
    contents.google_critic_snippet = toRichText('');
    contents.body = toRichText('');
    break;
  case 'game':
    contents.platform_scores = [];
    contents.body = toRichText('');
    break;
  }

  return contents;
}

function imagesliderContents() {
  return {
    caption: toRichText('')
  };
}

function productcardContents() {
  return {
    title:        toRichText('Untitled'),
    description:  toRichText(''),
    retailers:    []
  };
}
