const { merge, omit } = require('lodash');
const qs = require('qs');
const wait = require('./wait');
const HttpStatusError = require('./errors/http_status_error');
const messageActions = require('../actions/messages');
const { reportError } = require('../actions/sentry');
const hash = require('../../shared/content-hash');

const STORAGE = {};

function checkStatus(response) {
  if (response.ok) {
    return response;
  } else {
    var error = new HttpStatusError(`Response status not within range: ${response.status}`, response);
    return Promise.reject(error);
  }
}

function parseBody(response, accepts, withHeaders) {
  if (response.status === 204) {
    return Promise.resolve();
  }
  if (accepts === 'text/html') {
    return response.text().then(data => returnWithHeaders(response, data, withHeaders));
  }
  return response.json().then(data => returnWithHeaders(response, data, withHeaders));
}

function returnWithHeaders(response, data, withHeaders) {
  if (withHeaders) {
    return {
      data,
      headers: response.headers
    };
  }
  return data;
}

// Handles error based on supplied options
function handleError(error, url, reqOpts, opts) {
  var status = error.response && error.response.status;
  if (
    opts.retry &&
    opts.retry.delay < opts.retry.maxDelay &&
    (!status || status === 502 || status === 503)
  ) {
    var { retry } = opts;
    var delay = Math.min(retry.delay + 1000, retry.maxDelay);
    retry.delay = delay;
    return wait(delay).then(() => request(url, reqOpts, opts));
  } else {
    if (opts.handleError) {
      if (window.app && window.app.state) {
        // Show a flash message, but don't send to sentry. Uses `window.top` so
        // that app is accessible by requests from within object iframes
        messageActions.showError();
      } else {
        console.warn('An unexpected error occurred while making a request, but the app is not able to display it');
        reportError(error, false);
      }
    }
    return Promise.reject(error);
  }
}

function request(url, reqOpts, opts) {
  let cacheKey = getCacheKey(url, reqOpts);
  if (opts.cache && !opts.force && cacheKey in STORAGE) {
    return Promise.resolve(STORAGE[cacheKey]);
  }

  return fetch(url, reqOpts)
    .then(checkStatus)
    .then(res => parseBody(res, reqOpts.headers.Accept, opts.withHeaders))
    .then(body => {
      if (opts.cache) {
        STORAGE[cacheKey] = body;
      }
      return body;
    })
    .catch(err => handleError(err, url, reqOpts, opts));
}

// For GET requests, all the unique request data should be in the URL
// For non-GET requests with body, also hash that to get a unique cache key
function getCacheKey(url, { method, body }) {
  let cacheKey = url;
  if (method && method !== 'GET' && body) {
    cacheKey += hash(body);
  }

  return cacheKey;
}

function loadOpts(url, reqOpts) {
  // Defaults
  var defaultReqOpts = {
    credentials: 'include',
    headers: { Accept: 'application/json' }
  };
  var defaultOpts = {
    handleError: true,
    cache: false,
    retry: { delay: 0, maxDelay: 3000 }
  };

  // Pull out non-request options, if any
  var opts = reqOpts.opts || {};
  var requestOpts = omit(reqOpts, 'opts');

  // Parse the request options
  if (requestOpts.method && requestOpts.method.toUpperCase() !== 'GET') {
    defaultReqOpts.headers['Content-Type'] = 'application/json';
  }
  if (requestOpts.query) {
    url = url + '?' + qs.stringify(requestOpts.query);
    delete requestOpts.query;
  }
  if (requestOpts.body && typeof requestOpts.body !== 'string') {
    requestOpts.body = JSON.stringify(requestOpts.body);
  }
  if (requestOpts.credentials === false) {
    delete defaultReqOpts.credentials;
    delete requestOpts.credentials;
  }

  // Parse additional options
  if (!opts.retry) {
    defaultOpts.retry = false;
  }
  if (typeof opts.retry === 'boolean') { delete opts.retry; }

  return {
    url,
    opts: merge(defaultOpts, opts),
    reqOpts: merge(defaultReqOpts, requestOpts)
  };
}

/**
 * Makes AJAX requests and handles errors based on supplied options
 *
 * @param {string} url
 * @param {object} reqOpts: fetch request object
 * @param {object} opts: handler options, with the shape:
 *   {
 *    {boolean} handleError: defaults to true
 *    {boolean} cache: defaults to false
 *    {boolean} withHeaders: optional
 *    {boolean} retry: optional
 *    {object} retry: optional, custom retry params of `delay` and `maxDelay`
 *   }
 */
export default function(url, reqOpts = {}) {
  var parsed = loadOpts(url, reqOpts);
  return request(parsed.url, parsed.reqOpts, parsed.opts);
}
