import getDocLabel from '../../../client/lib/get_doc_label';

export default {
  currentDoc(state) {
    return state.docsById[state.currentDocId];
  },
  docLabel(state, getters) {
    const { currentDoc } = getters;
    return getDocLabel(currentDoc);
  },
  contents(state, getters) {
    const { currentDoc } = getters;
    return currentDoc.head.contents;
  },
  draftBody(state, getters) {
    const { currentDoc } = getters;
    return currentDoc.head.contents.draft.body;
  },
  docIds(state) {
    return Object.keys(state.docsById).map((docId) => parseInt(docId));
  },
  /**
   * Called to get the dirty state for a specific doc by its id OR the whole
   * pipeline
   * @param {Number} optional docId
   * @returns boolean
   */
  isPipelineDirty(state) {
    return Object.keys(state.dirty).length > 0;
  },
  isCurrentDocDirty(state) {
    return state.dirty.hasOwnProperty(state.currentDocId);
  },
};
