<template>
  <div class="m-nav m-nav__root">

    <banner-message />

    <nav class="m-nav m-nav--dashboard m-nav--dashboard__vue">
      <div class="m-nav__inner m-nav--dashboard__inner">

        <community-select
          v-model="communityId"
          :community="currentCommunity"
          :user="currentUser"
          :activeMemberships="activeMemberships"
          :allowedRoles="policyRoles.app.access"
          class="m-community-select"
        />

        <div class="m-nav__right">
          <div class="m-nav--dashboard__subnav">
            <span v-if="canManagePages" class="m-nav--dashboard__links">
              <res-button id="editorial-dropdown">
                <button>Admin</button><res-icon name="down" />
              </res-button>
              <res-dropdown :key="$route.name" for="editorial-dropdown">
                <a :href="`https://${communityDomain}/admin/entry_groups/list?community_id=${communityId}&type=SiteGroup`">Groups</a>
                <a :href="`https://${communityDomain}/admin/hub_layout/show?community_id=${communityId}`">Home &amp; Hubpages</a>
                <a :href="`https://${communityDomain}/admin/link_sets?community_id=${communityId}`">Link sets</a>
                <a :href="`https://${communityDomain}/admin/apple_news_articles/list?community_id=${communityId}`">Apple News</a>
                <router-link :to="{ path: '/pages' }">Pages</router-link>
                <a :href="`http://${communityDomain}/admin/dashboard/hub_editorial?community_id=${communityId}`">Legacy Chorus</a>
                <a href="https://www.chorus.help/hc/en-us">Help center</a>
              </res-dropdown>
            </span>
            <span v-if="canManageAssets" class="m-nav--dashboard__links">
              <res-button>
                <a :href="`/assets?community_id=${communityId}`" rel="external">Assets</a>
              </res-button>
            </span>

            <new-story-button :isMobile="isMobile" :communityId="communityId" />

            <help-toggle />
          </div>
        </div>
      </div>
    </nav>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { hasPolicyRole } from '../../lib/authorization';
import BannerMessage from './banner-message.vue';
import CommunitySelect from '../../../shared/vue/community-select/index.vue';
import NewStoryButton from './new-story-button.vue';
import HelpToggle from './help-toggle.vue';

export default {
  components: {
    BannerMessage,
    CommunitySelect,
    NewStoryButton,
    HelpToggle
  },
  computed: {
    ...mapState([
      'currentCommunity',
      'currentUser',
      'isMobile',
      'network',
      'policyRoles'
    ]),
    ...mapGetters(['activeMemberships']),
    communityId() {
      return parseInt(this.currentCommunity.id);
    },
    canManagePages() {
      return hasPolicyRole(this.currentUser, this.communityId, this.policyRoles.doc.managePages);
    },
    canManageAssets() {
      return hasPolicyRole(this.currentUser, this.communityId, this.policyRoles.editor.accessAssets);
    },
    communityDomain() {
      return this.network.schemaDomain;
    }
  }
};
</script>
