<template>
  <message />
</template>

<script>
import Message from '../../../shared/vue/banner-messages/message.vue';
import { mapState } from 'vuex';
export default {
  components: {
    Message
  },
  computed: {
    ...mapState(['messageBanner', 'message']),
    systemMessage() {
      return Object.assign({}, this.message);
    }
  },
  mounted() {
    this.displaySystemMessage(this.systemMessage);
  },
  methods: {
    messageShown(storageKey) {
      return window.localStorage.getItem(storageKey) === 'shown';
    },
    displaySystemMessage(bannerMessage={}) {
      let storageKey;
      if (bannerMessage.id) storageKey = `message-${bannerMessage.id}`;
      if (bannerMessage && !this.messageShown(storageKey)) {
        let messageType = bannerMessage.type;
        if (messageType === 'primary') messageType = 'neutral';
        this.$store.commit('messageBanner/PUT_NOTICE', {
          type: messageType || 'neutral',
          message: bannerMessage.message,
          timeout: bannerMessage.timeout || false,
          icon: bannerMessage.icon || null,
        });
        window.localStorage.setItem(storageKey, 'shown');
      }
    }
  }
};
</script>
