<template>
  <div>
    <dashboard-nav />
    <div class="m-dashboard m-dashboard--has-search-panel">
      <div :style="contentStyles" class="l-content p-flex is-compose">
        <div class="l-content__inner m-dashboard__inner">
          <search-panel
            ref="searchPanel"
            :class="searchPanelClass"
            :isCollapsed="searchPanelCollapsed"
            :key="mobileKey"
            :updateQuery="updateQuery"
            :formattedQuery="formattedQuery"
            :isSearching="isSearching"
            :defaultQuery="baseQuery"
            :setSavedSearchName="setSavedSearchName"
            @collapse="toggleSearchPanel()" />
          <div
            v-if="!isMobile"
            :class="`m-dashboard__search-panel ${searchPanelClass}`"
            @click="toggleSearchPanel(!searchPanelCollapsed)">
            <div class="m-dashboard__search-panel-actions">
              <span class="m-dashboard__search-panel-icons">
                <res-icon name="search" size="small" />
                <res-icon v-if="!searchPanelCollapsed" name="left" size="small" />
                <res-icon v-else name="right" size="small" />
              </span>
            </div>
          </div>
          <main class="m-dashboard__content m-dashboard__content--has-search-panel">
            <res-button
              v-if="isMobile"
              :key="mobileKey"
              class="m-dashboard__mobile-search-toggle"
              design="secondary"
              @click="toggleSearchPanel()">
              <res-icon name="search" />
              <button>Open search</button>
            </res-button>

            <header class="m-dashboard__header">
              <div
                v-if="filter || isPagesViewMode"
                class="m-dashboard__sub-head">
                <div class="m-dashboard__sub-head__title">
                  <h1>{{ title }}</h1>
                  <res-button design="default" paddingless class="m-l-1" @click="back">
                    <res-icon name="back"/>
                    <button>
                      {{ titleButtonText }}
                    </button>
                  </res-button>
                </div>
                <p v-if="filter === 'trash'">
                  Items in the trash are automatically deleted after 30 days.
                </p>
              </div>
              <res-badge v-if="savedSearchName" size="smaller" class="m-dashboard__sub-head__saved-search">
                Saved search: {{ savedSearchName }}
              </res-badge>
              <div
                v-if="displayResultsInfo"
                class="m-dashboard__sub-head m-dashboard__sub-head__summary">
                <span class="m-dashboard__sub-head--bold">
                  {{ resultsCount }}
                </span>
                {{ resultsFor }}
                <span class="m-dashboard__sub-head--bold">
                  {{ searchPhrase }}
                </span>
                <div class="m-chip__container">
                  <div v-for="(group, name) in chipGroups" :key="name">
                    <span class="m-chip__label">{{ name }}:</span>
                    <chip v-for="(searchItem, index) in group"
                          :key="`${index}-${searchItem}`"
                          :chipId="`${name}:${searchItem}`"
                          :chipLabel="searchItem"
                          @removeSelection="removeChipFromQuery"
                    />
                  </div>
                </div>
              </div>
              <div class="m-dashboard__header__table-filters">
                <res-switch
                  v-if="canListAllStories"
                  :checked="myDocs"
                  class="m-dashboard__header__my-stories"
                  @change="onMyDocsChange">
                  <input
                    type="checkbox"
                    name="myDocs" />
                  <label>Show my {{ storyLabel }}</label>
                </res-switch>
                <res-select size="small">
                  <label>Sort by:</label>
                  <select
                    :value="sortBy"
                    name="order"
                    @change="onOrderChange">
                    <option v-if="canSortByScore" value="score" role="option" aria-selected="true">
                      Relevance
                    </option>
                    <option value="date" role="option" aria-selected="true">
                      Last modified
                    </option>
                    <option value="last_published" role="option">
                      Last published
                    </option>
                    <option value="first_published" role="option">
                      Original publish date
                    </option>
                    <option value="updated" role="option">Latest activity</option>
                    <option value="title" role="option">Title</option>
                  </select>
                </res-select>
              </div>
            </header>

            <div
              v-show="canArchiveOrTrashStories && selectedIndices.length > 0"
              class="m-dashboard__table-actions">
              <res-button
                id="dashboard-actions-btn"
                ref="actionsBtn"
                design="secondary">
                <button>Actions</button>
              </res-button>
              <res-dropdown for="dashboard-actions-btn">
                <!-- Filter is empty -->
                <button
                  v-if="!filter"
                  @click="setDocAction('updateArchived', 'archive')">
                  <res-icon size="small" name="versions" title="Archive"/>
                  Archive
                </button>
                <button
                  v-if="!filter"
                  @click="setDocAction('updateTrashedAt', 'trash')">
                  <res-icon size="small" name="trash" title="Move to trash"/>
                  Move to trash
                </button>
                <!-- Archive view -->
                <button
                  v-if="filter === 'archive'"
                  @click="setDocAction('updateArchived', 'dashboard')">
                  <res-icon size="small" name="refresh" title="Restore"/>
                  Restore
                </button>
                <button
                  v-if="filter === 'archive'"
                  @click="setDocAction('updateArchivedAndTrashed', 'trash')">
                  <res-icon size="small" name="trash" title="Move to trash"/>
                  Move to trash
                </button>
                <!-- Trash view -->
                <button
                  v-if="filter === 'trash'"
                  @click="setDocAction('updateTrashedAt', 'dashboard')">
                  <res-icon size="small" name="refresh" title="Restore"/>
                  Restore
                </button>
                <button
                  v-if="filter === 'trash'"
                  @click="setDocAction('updateArchivedAndTrashed', 'archive')">
                  <res-icon size="small" name="versions" title="Move to archive"/>
                  Move to archive
                </button>
              </res-dropdown>
            </div>

            <res-table
              ref="table"
              :mobile="isMobile"
              class="m-dashboard__table"
              zebra
              selectable>
              <table>
                <thead>
                  <tr>
                    <th class="m-dashboard__table__icon" />
                    <th>Title</th>
                    <th>Byline</th>
                    <th v-if="currentCommunity.printSlugEnabled">Print slug</th>
                    <th v-if="entitlements && entitlements.includes('use_send_to_print')" align="center">
                      Print
                    </th>
                    <th>{{ lastActivityLabel }}</th>
                    <th v-if="displayActions" align="center"/>
                  </tr>
                </thead>
                <tbody>
                  <doc-cell
                    v-for="(doc, index) in docs"
                    :ref="`${index}DocCell`"
                    :key="doc.uuid"
                    :doc="doc"
                    :filter="filter"
                    :canArchiveOrTrashStories="canArchiveOrTrashStories"
                    :displayActions="displayActions"
                    :updateTernaryCheckbox="updateTernaryCheckbox" />
                </tbody>
              </table>
            </res-table>

            <footer class="m-dashboard__footer">
              <div v-if="!filter" class="m-dashboard__footer__view-mode-buttons">
                <res-button
                  v-if="canArchiveOrTrashStories"
                  design="secondary"
                  @click="setFilter('archive')">
                  <button type="button">Show archived {{ storyLabel }}</button>
                </res-button>
                <res-button
                  v-if="canArchiveOrTrashStories"
                  design="secondary"
                  @click="setFilter('trash')">
                  <button type="button">Show trash</button>
                </res-button>
              </div>
              <div v-if="pagination" class="m-dashboard__footer__pagination">
                <res-pagination
                  :count="pagination.total"
                  :disabled="isSearching"
                  :display="pagination.perPage"
                  :page="pagination.page"
                  :pagination="isMobile ? 'nav-only-toggle' : 'text-only'"
                  indicator="page"
                  navigation="text"
                  @change="paginate" />
              </div>
            </footer>
          </main>
        </div>
      </div>

      <scroll-lock v-if="!searchPanelCollapsed" />
    </div>
  </div>
</template>

<script>
import DashboardNav from '../../components/header/dashboard-nav.vue';
import DocCell from '../../components/dashboard-doc-cell.vue';
import SearchPanel from '../../components/search-panel/dashboard-search-panel.vue';
import ScrollLock from '../../components/utils/scroll-lock.vue';
import Chip from '../../components/chip.vue';
import messages from '../../../client/config/messages';
import { hasPolicyRole } from '../../lib/authorization';
import { isEqual, compact, capitalize } from 'lodash';
import { mapState } from 'vuex';
import { reportError } from '../../../client/actions/sentry';
import { searchDocs } from '../../../client/actions/docs';

export default {
  components: {
    DashboardNav,
    DocCell,
    SearchPanel,
    ScrollLock,
    Chip,
  },
  data() {
    return {
      docs: [],
      formattedQuery: [],
      isSearching: false,
      myDocs: false,
      pagination: null,
      selectedIndices: [],
      searchPanelCollapsed: false,
      mobileKey: 1,
      defaultOrder: 'date',
      savedSearchName: '',
    };
  },
  computed: {
    ...mapState([
      'appName',
      'currentCommunity',
      'currentUser',
      'entitlements',
      'isMobile',
      'windowSize',
      'messageBanner',
      'policyRoles',
    ]),
    communityId() {
      return parseInt(this.currentCommunity.id);
    },
    bannerMessageOverride() {
      const messageHeight = 44;
      const numberOfMessages = this.messageBanner.notifications.length;
      const navHeightWithMessages = (messageHeight * numberOfMessages) + 49; // 49 here should match $dash-navbar-height sass variable
      return navHeightWithMessages;
    },
    contentStyles() {
      const marginTop = this.bannerMessageOverride;

      const height = this.isMobile && !this.searchPanelCollapsed
        ? `${this.windowSize.height - marginTop}px` : 'auto';

      return {
        'margin-top': `${marginTop}px`,
        height: height
      };
    },
    canListAllStories() {
      return hasPolicyRole(this.currentUser, this.currentCommunity.id, this.policyRoles.doc.accessAny);
    },
    canArchiveOrTrashStories() {
      return hasPolicyRole(this.currentUser, this.currentCommunity.id, this.policyRoles.doc.archiveOrTrash);
    },
    filter() {
      return this.$route.params.filter || '';
    },
    isPagesViewMode() {
      return this.$route.name.includes('pages');
    },
    displayActions() {
      return this.filter !== 'trash' || this.canArchiveOrTrashStories;
    },
    sortBy() {
      return this.$route.query.order || this.defaultQuery.order;
    },
    canSortByScore() {
      return Boolean(this.$route.query.q);
    },
    lastActivityLabel() {
      switch (this.filter) {
      case '':
        return 'Status';
      case 'archive':
        return 'Last Activity';
      case 'trash':
        return 'Added to Trash';
      }
    },
    title() {
      const title = compact([this.storyLabel, this.filter]).join(' ');
      return capitalize(title);
    },
    storyLabel() {
      return this.isPagesViewMode ? 'pages' : 'stories';
    },
    titleButtonText() {
      let text = 'Back';
      if (this.isPagesViewMode && !this.filter) {
        text += ' to dashboard';
      }
      return text;
    },
    displayResultsInfo() {
      return Boolean(this.$route.query.q) && this.formattedQuery.length > 0;
    },
    resultsCount() {
      return this.docs.length === 0 ? 'No results' : this.pagination.total;
    },
    resultsFor() {
      if (this.pagination.total === 0) {
        return ' for';
      }
      return `${this.pagination.total > 1 ? ' results' : ' result'} for`;
    },
    baseQuery() {
      return {
        page: 1,
        order: this.defaultOrder,
        community_id: this.currentCommunity.id,
        q: '',
      };
    },
    defaultQuery() {
      return {
        page: this.$route.query.page || 1,
        order: this.$route.query.order || this.defaultOrder,
        community_id: this.currentCommunity.id || this.$route.query.community_id,
        q: this.$route.query.q || '',
      };
    },
    missingQueryParams() {
      // is missing some of the default params
      return Object.keys(this.defaultQuery).some(key => !this.$route.query.hasOwnProperty(key));
    },
    searchPanelClass() {
      let className = 'm-dashboard__search-panel';
      let modifier = this.searchPanelCollapsed ? '--is-collapsed' : '--is-open';
      return `${className}${modifier}`;
    },
    searchPhrase() {
      const phrase = [];
      this.formattedQuery.forEach(item => {
        if (item.type === 'tokens' || item.type === 'phrase') phrase.push(item.value);
      });
      return phrase.join(' ');
    },
    chipGroups() {
      // update "resultsFor" to include search string (tokens,phrase)
      let chipGroups = {};
      this.formattedQuery.forEach(item => {
        if (item.type === 'tokens' || item.type === 'phrase') return;
        const [field, value] = item.value.split(':');
        if (chipGroups[field]) {
          chipGroups[field].push(value);
        } else {
          chipGroups[field] = [value];
        }
      });
      return chipGroups;
    }
  },
  watch: {
    currentCommunity() {
      // set default url params and trigger search when community is loaded in Vuex
      this.updateQuery(this.defaultQuery);
    },
    $route(to, from) {
      // run a new search anytime route changes
      if (!isEqual(to, from)) {
        this.checkParamsAndSearch(to, from);
      }
    },
    isMobile() {
      this.mobileKey++;
      if (this.isMobile) {
        this.searchPanelCollapsed = true;
      }
    },
    isSearching() {
      if (this.isSearching) {
        this.$store.commit('appLoader/SET_LOADING', true);
      } else {
        this.$store.commit('appLoader/SET_LOADING', false);
      }
    }
  },
  created() {
    document.title = `Dashboard | ${this.appName}`;
  },
  beforeMount() {
    if (this.isMobile) {
      this.searchPanelCollapsed = true;
    }
    if (!this.isMobile && localStorage.getItem('preferredCollapse')) {
      // Storage converts the true/false Boolean value to a string when it is stored, so we need to convert it back to Boolean here
      const preference = JSON.parse(localStorage.getItem('preferredCollapse'));
      this.searchPanelCollapsed = preference;
    }
  },
  mounted() {
    this.$store.commit('appLoader/SET_LOADING', true);
    this.checkParamsAndSearch(this.$route);

    // Keep track of table `selectedIndices` (checkboxes) with a MutationObserver
    // because it cannot be tracked with watchers or computed properties
    // (`refs` are non-reactive)
    const observer = new MutationObserver(() => {
      this.selectedIndices = this.$refs.table && this.$refs.table.selectedIndices || [];
    });
    observer.observe(this.$refs.table, {
      attributes: true,
      childList: true,
      subtree: true
    });
    if (this.isMobile) {
      this.searchPanelCollapsed = true;
    }
    window.addEventListener('keypress', this.keyHandler);
  },
  beforeDestroy() {
    window.removeEventListener('keypress', this.keyHandler);
  },
  methods: {
    handleError(error) {
      reportError(error, false);
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED
      });
    },
    resetOrder(toRoute, fromRoute) {
      // if there was a query and now there's not, and the order isn't the default, reset it
      if (fromRoute && fromRoute.query.q && !toRoute.query.q && toRoute.query.order !== this.defaultOrder) {
        return { ...toRoute.query, order: this.defaultOrder };
      }
      // if there's no search q and order is set to relevance, reset it
      if (!toRoute.query.q && toRoute.query.order === 'score') {
        return { ...toRoute.query, order: this.defaultOrder };
      }
      return null;
    },
    checkParamsAndSearch(toRoute, fromRoute) {
      let updatedQuery;

      updatedQuery = this.resetOrder(toRoute, fromRoute);
      // set missing url params
      if (this.missingQueryParams) {
        updatedQuery = { ...this.defaultQuery, ...toRoute.query, ...updatedQuery };
      }
      if (updatedQuery) {
        this.updateQuery(updatedQuery);
      } else {
        // trigger search if all url params are there
        this.search();
      }
      // reset controlled inputs
      this.myDocs = Boolean(toRoute.query.q) &&
        (toRoute.query.q.includes('by:me') || toRoute.query.q.includes('by:"me"'));
    },
    async search() {
      const searchParams = this.getSearchParams();
      // don't search if community hasn't been loaded in Vuex
      // watcher will run search when community is loaded
      if (!searchParams.community_id) {
        return;
      }

      this.isSearching = true;
      try {
        const {
          docs,
          pagination,
          formattedQuery,
        } = await searchDocs(searchParams, { handleError: false });
        this.docs = docs;
        this.pagination = pagination;
        this.formattedQuery = formattedQuery;

        // rebuild res-table checkboxes when docs change
        await this.$nextTick();
        this.$refs.table && this.$refs.table.rebuildCheckboxes();
        this.isSearching = false;
      } catch (error) {
        this.handleError(error);
        this.isSearching = false;
      }
    },
    getSearchParams() {
      // eslint-disable-next-line
      const { q, order, page, community_id } = this.$route.query;
      const searchParams = {
        q: this.isPagesViewMode ? `${q} type:page` : q,
        order: order || this.defaultOrder,
        page: page || 1,
        schema_name: 'story',
        community_id,
        per_page: 36,
        archived: this.filter === 'archive',
        trashed: this.filter === 'trash',
        story_type_blocklist: this.isPagesViewMode ? [] : ['page'],
      };

      return searchParams;
    },
    paginate(e) {
      const query = { ...this.$route.query, page: e.detail.page };
      // use $router.push for pagination
      return this.updateQuery(query, true);
    },
    onOrderChange(e) {
      const query = { ...this.$route.query, order: e.target.value, page: 1 };
      this.updateQuery(query);
    },
    async removeChipFromQuery(chip) {
      // by:person
      let q;
      q = this.$route.query.q.replace(chip, '').trim();
      const query = { ...this.$route.query, q, page: 1 };
      await this.updateQuery(query);
      if (this.$refs.searchPanel) {
        this.$refs.searchPanel.clearSavedSearch();
        this.$refs.searchPanel.goToSearchView();
      }
    },
    async onMyDocsChange(e) {
      let q, query;
      if (e.target.checked) {
        this.myDocs = true;
        q = `by:me ${this.$route.query.q}`.trim();
        query = { ...this.$route.query, q, order: 'score', page: 1 };
      } else {
        this.myDocs = false;
        q = this.$route.query.q.replace(/by:"?me"?\s*/, '').trim();
        query = { ...this.$route.query, q, page: 1 };
      }
      await this.updateQuery(query);
    },
    async updateQuery(query, usePush = false) {
      const path = this.$route.path || '/';
      try {
        // Updating the query params triggers a new search, per watch method above
        if (usePush) {
          await this.$router.push({ path, query });
        } else {
          await this.$router.replace({ path, query });
        }
      } catch (error) {
        // Swallow the Vue router redundant navigation error
        // https://github.com/vuejs/vue-router/issues/2872#issuecomment-519073998
      }
    },
    async setDocAction(method, destination) {
      this.$refs.actionsBtn.toggleDropdown();
      const promises = this.selectedIndices.map(index => {
        const docCell = this.$refs[`${index}DocCell`][0];
        const action = docCell.$refs.docActions[method];
        return action(destination);
      });
      await Promise.all(promises);
    },
    updateTernaryCheckbox() {
      const checkbox = this.$refs.table.querySelector('.res-checkbox[ternary]');
      checkbox.setupTernaryFollowers();
    },
    setFilter(filter) {
      const path = this.isPagesViewMode ? `/pages/${filter}` : filter;
      const query = { ...this.defaultQuery, page: 1 };
      this.$router.push({ path, query });
      document.body.scrollTo(0, 0);
    },
    back() {
      const path = this.isPagesViewMode && this.filter ? '/pages' : '/';
      this.$router.push({ path, query: this.defaultQuery });
      document.body.scrollTo(0, 0);
    },
    toggleSearchPanel(manualClick=null) {
      if (manualClick !== null) { // check for not-null because we want to capture this value whether its true or false
        localStorage.setItem('preferredCollapse', manualClick);
      }
      this.searchPanelCollapsed = !this.searchPanelCollapsed;
    },
    keyHandler(evt) {
      switch (evt.key) {
      case '/':
        if (this.searchPanelCollapsed) {
          this.searchPanelCollapsed = false;
          evt.preventDefault();
        } else if (document.activeElement.tagName !== 'INPUT') {
          this.$refs.searchPanel.focusKeywordInput();
          evt.preventDefault();
        }
        break;
      }
    },
    setSavedSearchName(name) {
      this.savedSearchName = name;
    }
  }
};
</script>
