<template>
  <error-boundary :key="errorBoundaryKey">
    <app-modal />
    <app-overlay />
    <app-sidebar />
    <router-view />
    <app-loader iconName="more" animation="static" />
  </error-boundary>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import { debounce } from 'lodash';
import * as cmd from './store/commands';
import messages from '../client/config/messages';
import moment from 'moment-timezone';
import request from '../client/lib/request';
import AppSidebar from './components/app-sidebar.vue';
import AppModal from './components/app-modal.vue';
import AppOverlay from './components/app-overlay.vue';
import AppLoader from '../shared/vue/app-loader/app-loader.vue';
import ErrorBoundary from './components/errors/error-boundary.vue';
import { updateAnthemUser } from '../client/actions/users';
import { CurrentUser } from '../shared/vue/queries/currentUser.graphql';
import { CurrentCommunity } from '../shared/vue/queries/currentCommunity.graphql';

export default {
  components: {
    AppSidebar,
    AppModal,
    AppOverlay,
    AppLoader,
    ErrorBoundary,
  },
  data() {
    return {
      debouncedComputeSizes: null,
      entitlements: [],
    };
  },
  computed: {
    ...mapState(['user', 'isMobile', 'errorBoundaryKey', 'appName']),
    ...mapGetters(['activeMemberships', 'currentDoc']),
    communityId() {
      if (this.loadDoc) {
        return this.currentDoc && this.currentDoc.community_id;
      } else if (this.$route.query.community_id) {
        return parseInt(this.$route.query.community_id);
      } else {
        return this.getExternalCommunityId();
      }
    },
  },
  beforeMount() {
    this.computeSizes();
    this.debouncedComputeSizes = debounce(this.computeSizes, 500);
    window.addEventListener('resize', this.debouncedComputeSizes);
  },
  mounted() {
    window.addEventListener('keyup', this.overlayEscapeHandler);
  },
  beforeDestroy() {
    window.removeEventListener('resize', this.debouncedComputeSizes);
    window.removeEventListener('keyup', this.overlayEscapeHandler);
  },
  apollo: {
    currentUser: {
      query: CurrentUser,
      result({ data }) {
        this.$store.commit(cmd.SET_CONTEXT, { user: data.currentUser });
        this.newUserWelcome();
      }
    },
    community: {
      query() {
        return CurrentCommunity;
      },
      result({ data }) {
        this.$store.commit(cmd.SET_CONTEXT, { community: data.community });
        this.getEntitlements();
      },
      skip() {
        return !this.communityId;
      },
      variables() {
        return { id: this.communityId };
      }
    }
  },
  methods: {
    computeSizes() {
      // get the browser-settings font size in pixel measure
      const fontSizePx = window.getComputedStyle(document.documentElement).getPropertyValue('font-size');
      // remove the pixel unit, return just the number
      const fontSize = +(fontSizePx.substring(0, fontSizePx.indexOf('px')));
      // multiply this fontSize by 40, which is equal to 40em, which is our $break-mobile size from _settings.scss
      const MOBILE_WIDTH = fontSize * 40;
      this.$store.commit(cmd.SET_IS_MOBILE, window.innerWidth <= MOBILE_WIDTH);

      this.$store.commit(cmd.SAVE_WINDOW_SIZE, {
        fontSize,
        width: window.innerWidth,
        height: window.innerHeight
      });
    },
    getExternalCommunityId() {
      // check in URL
      const url = location.search.match(/(?:\?|&)community_id=(\d+)/);
      if (url) return parseInt(url[1]);

      // check in localStorage
      const storage = localStorage.getItem('dashboardCommunityId');
      if (storage) return parseInt(storage);

      // check user memberships
      if (this.activeMemberships.length) {
        return parseInt(this.activeMemberships[0].community.id);
      }

      return null;
    },
    async getEntitlements() {
      this.entitlements = await request(`/api/entitlements?communityId=${this.communityId}`);
      this.$store.commit(cmd.SET_CONTEXT, { entitlements: this.entitlements });
    },
    newUserWelcome() {
      const NEW_USER_MARK = moment('2018-03-29', 'YYYY-MM-DD').utc();
      const isNewUser = Boolean(!this.user.hasLoggedIn && moment(this.currentUser.createdOn).utc().isAfter(NEW_USER_MARK));
      if (isNewUser) {
        this.$store.commit('messageBanner/PUT_NOTICE', {
          type: 'positive',
          message: messages.NEW_USER_MESSAGE.replace(
            '{url}',
            'https://www.chorus.help/hc/en-us/articles/360009073453-Getting-started-with-the-story-editor'
          )
        });
        // Flag this user as having logged on if they haven't been loggedIn before
        updateAnthemUser({ hasLoggedIn: true });
      }
    },
    overlayEscapeHandler(event) {
      if (event.key === 'Escape' && !this.isMobile) {
        this.$store.commit(cmd.SET_OVERLAY, {
          childProps: null,
          component: null,
        });
        this.$store.commit(cmd.SET_SIDEBAR, null);
      }
    }
  }
};
</script>
