<template>
  <div class="m-dashboard__modal">
    <p class="m-dashboard__modal__text">Delete this search?</p>
    <div class="m-dashboard__modal__actions">
      <res-button :disabled="disableButtons" :res-modal-close="modalId" design="secondary" @click="deleteSearch">
        <button>Delete</button>
      </res-button>
      <res-button :disabled="disableButtons" :res-modal-close="modalId">
        <res-icon class="m-dashboard__modal__icon" name="x" />
        <button>Cancel</button>
      </res-button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    childProps: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {};
  },
  computed: {
    ...mapState(['modalId']),
    disableButtons() {
      return this.childProps.saving;
    }
  },
  methods: {
    deleteSearch() {
      if (this.disableButtons) {
        return;
      }
      const { deleteSavedSearch } = this.childProps;
      deleteSavedSearch();
    }
  }
};
</script>
