<template>
  <span class="m-radio-button-group">
    <label v-for="option in options" :key="option.value" :title="option.label">
      <input
        :id="option.id"
        v-model="radioValue"
        :value="option.value"
        type="radio"
        @change="saveValue"
      />
      <span type="button" class="is-secondary -small">
        <res-icon v-if="option.icon" :title="option.label" :name="option.icon" size="medium" />
        <span v-else>{{ option.label }}</span>
      </span>
    </label>
  </span>
</template>

<script>
import { Delta } from 'rich-text';
import { get } from 'lodash';
import { mapGetters } from 'vuex';

export default {
  props: {
    options: {
      type: Array,
      default: () => []
    },
    richText: {
      type: Boolean,
      default: false,
    },
    path: {
      type: Array,
      default: () => []
    },
    value: {
      type: String,
      default: '',
    },
    onResolve: {
      type: Function,
    },
    onError: {
      type: Function
    },
  },
  data() {
    return {
      radioValue: null,
    };
  },
  computed: {
    ...mapGetters(['currentDoc', 'contents']),
    docId() {
      return this.currentDoc.id;
    }
  },
  mounted() {
    this.radioValue = this.value;
  },
  methods: {
    saveValue() {
      const path = this.path;
      const previous = get(this.contents, path);
      const insert = this.richText ? new Delta().insert(this.radioValue) : this.radioValue;
      const delta = [{
        p: path,
        od: previous,
        oi: insert
      }];
      if (this.onResolve && this.onError) {
        this.asyncSave(delta);
      } else {
        this.$store.dispatch('localUpdate', { docId: this.docId, delta: delta });
      }
    },
    async asyncSave(delta) {
      try {
        await this.$store.dispatch('resolveOnLocalUpdate', { docId: this.docId, delta: delta });
        this.onResolve();
      } catch (error) {
        this.onError(error);
      }
    }
  },
};
</script>
