<template>
  <router-link :to="{ path: '/', query: { community_id: communityId } }" :class="lockupClass">
    <div :class="avatarWrapClass">
      <img :src="faviconUrl || defaultLogoUrl" class="m-community-select__icon" />
    </div>
    <span v-if="firstLine && !isMobile">
      <span :class="`m-community-select__name m-community-select__name--first ${firstLineTruncatedClass} ${hasSecondLineClass}`">{{ firstLine }}</span>
      <span v-if="secondLine" class="m-community-select__name m-community-select__name--second m-community-select__name--truncated">{{ secondLine }}</span>
    </span>
    <span v-else :class="`m-community-select__name ${mobileTruncatedClass}`">{{ communityName }}</span>
  </router-link>
</template>

<script>
import { mapState } from 'vuex';
export default {
  props: {
    communityId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      small: false,
      firstLine: '',
      secondLine: '',
      firstLineTruncated: false
    };
  },
  computed: {
    ...mapState([
      'currentCommunity',
      'defaultLogoUrl',
      'isMobile'
    ]),
    communityName() {
      return this.currentCommunity.name;
    },
    faviconUrl() {
      return this.currentCommunity.faviconUrl;
    },
    lockupClass() {
      // I don't love this, but I'm following class names and markup from the ractive version -- M.C.
      return this.isMobile ? 'm-community-select__lockup' : 'm-nav__lockup';
    },
    avatarWrapClass() {
      let className;
      if (this.isMobile) {
        className = 'm-community-select__avatar is-mobile';
        if (this.communityName === 'Vox') {
          className += ' m-community-select__avatar--vox';
        }
      } else {
        className = 'm-nav__icon';
        if (this.communityName === 'Vox') {
          className += ' m-nav__icon--vox';
        }
      }
      return className;
    },
    mobileTruncatedClass() {
      return this.isMobile ? 'm-community-select__name--truncated' : '';
    },
    firstLineTruncatedClass() {
      return this.firstLineTruncated ? 'm-community-select__name--firstLineTruncated' : '';
    },
    hasSecondLineClass() {
      return this.secondLine ? 'm-community-select__name--has-second' : '';
    }
  },
  mounted() {
    this.fitCommunityName();
  },
  updated() {
    // this supports some weird state where the `fitCommunityName` thing
    // would not persist when a page was reloaded
    this.fitCommunityName();
  },
  methods: {
    fitCommunityName() { // @TODO: discuss a redesign of this component so that we can use CSS to manage fitting the communityName
      if (this.isMobile) return;
      const CONTAINER_WIDTH = 108;
      const offscreen = document.createElement('div');
      offscreen.setAttribute('style', 'width: 20rem; height: 10rem; position: absolute; left: -9999px; top: 0;');

      const container = document.createElement('div');
      container.setAttribute('style', 'width: 7rem; text-align: center;');

      const el = document.createElement('span');
      el.setAttribute('style', 'font-size: 0.889rem; display: inline-block;');
      el.innerText = this.communityName;

      container.appendChild(el);

      offscreen.appendChild(container);
      document.body.appendChild(offscreen);

      const nameWidth = el.clientWidth;

      if (nameWidth > CONTAINER_WIDTH) {
        this.small = true;

        // Mimic actual styles
        el.setAttribute('style', 'font-size: .79rem; display: inline-block; padding: 0 4px;');

        const name = this.communityName;
        const wordsInName = name.split(' ');

        // Actual letter is irrelevant, just need a base one-line height
        el.innerText = 'a';
        const originalHeight = el.clientHeight;
        // See if entire name can now fit on one line
        el.innerText = name;
        if (el.clientHeight === originalHeight) {
          this.firstLine = name;
        } else {
          // If we can't fit the entire community name on one line,
          // We'll display the first line as-is and use CSS to truncate the second line (if need be)
          let firstLine = '';
          el.innerText = '';
          // Loop through the words in the name; at each word,
          // stop to see if we've increased the height. If we have, we're done.
          for (let [i, currentWord] of wordsInName.entries()) {
            if (i === 0) {
              el.innerText = currentWord;
            } else {
              el.innerText += ' ' + currentWord;
            }
            if (el.clientHeight === originalHeight) {
              firstLine = el.innerText;
            } else {
              break;
            }
          }
          this.firstLine = firstLine;
          this.secondLine = name.split(firstLine + ' ')[1];
        }
        // If the first word is extremely long we will use CSS to truncate the first line
        el.innerText = wordsInName[0];
        if (el.clientWidth > CONTAINER_WIDTH) {
          this.firstLineTruncated = true;
        }
      }
      document.body.removeChild(offscreen);
    }
  }
};
</script>
