import * as cmd from './commands';

export default {
  [cmd.SET_CONTEXT](state, { community, user, entitlements }) {
    // currentCommunity
    if (community && community.id && community.network) {
      state.currentCommunity = community;

      // store network at the same level as currentCommunity to
      // avoid deeply nested data in global state
      if (Object.keys(community.network).length) {
        state.network = community.network;
      }
    }

    // currentUser
    if (user) {
      state.currentUser = user;
    }

    // entitlements
    if (entitlements !== undefined) {
      state.entitlements = entitlements;
    }
  }
};
