import * as cmd from '../commands';
import messages from '../../../client/config/messages';
import io from 'socket.io-client';

export default function capiSocket() {
  return function(store) {

    function createSocket() {
      const socketUrl = `${(store.state.ssl ? 'wss' : 'ws')}://${window.location.host}/`;
      const socket = io(socketUrl, {
        transports: ['websocket'],
        path: '/api/content/socket.io/',
        reconnection: true,
        reconnectionAttempts: store.state.sockets.reconnectAttempts
      });
      store.commit(cmd.CAPI_SOCKET, socket);
    }

    function initContentApiPresence() {
      const room = store.state.autosave.currentDocId;
      store.state.autosave.contentApiSocket.on('reconnect', () => store.state.autosave.contentApiSocket.emit('presence.join', room));
      store.state.autosave.contentApiSocket.emit('presence.join', room);
    }

    function connectListeners() {

      store.state.autosave.contentApiSocket.on('connect', () => {
        initContentApiPresence();
      });

      store.state.autosave.contentApiSocket.on('autosave.remote-update', (docId, packet) => {
        store.dispatch('remoteUpdate', { docId, packet });
      });

      store.state.autosave.contentApiSocket.on('autosave.doc-update', (docId, path, value) => {
        if (path.includes('schema_version')) {
          store.commit('messageBanner/PUT_NOTICE', {
            type: 'negative',
            message: messages.RELOAD_REQUIRED,
            persistent: true
          });
          // send a disconnect event to the general socket, which should
          // put the current browser tab into a read-only state until the user reloads
          store.state.anthemSocket.emit('disconnect', 'read-only');

          // @TODO: clear any active intervals or timeouts for the current doc on load
          // stateMatchObserver.stopAll();
        }
        store.dispatch('updateDocFlag', { docId, path, value });
      });

      store.state.autosave.contentApiSocket.on('autosave.error', () => {
        store.commit('messageBanner/PUT_NOTICE', {
          type: 'negative',
          message: messages.REALTIME_ERROR,
          persistent: true
        });
      });

      store.state.autosave.contentApiSocket.once('autosave.error', () => {
        // Stop receiving remote updates, they may cause unwanted duplicate
        // reports of 'missed a sequence' errors
        store.state.autosave.contentApiSocket.off('autosave.remote-update');

        // send a disconnect event to the general socket, which should
        // put the current browser tab into a read-only state until the user reloads
        store.state.anthemSocket.emit('disconnect', 'read-only');
      });
    }

    function leaveRoom() {
      const room = store.state.autosave.currentDocId;
      store.state.autosave.contentApiSocket.emit(`presence.${room}.leave`);
      store.state.autosave.contentApiSocket.disconnect();
      store.commit(cmd.CAPI_SOCKET, null);
      store.state.anthemSocket.emit(`presence.${room}.leave`);
    }

    store.subscribe((mutation) => {
      if (mutation.type === cmd.SET_DOC) {
        createSocket();
        connectListeners();
      }
      if (mutation.type === cmd.REMOVE_DOC_FROM_STATE) {
        leaveRoom();
      }
    });
  };
}
