import * as cmd from '../commands';
import Vue from 'vue';
import OT from '../../../client/lib/anthem0';
import { set } from 'lodash';

export default {
  // SET_DOC kicks of init process for autosave plugin
  [cmd.SET_DOC](state, { doc, path }) {
    Vue.set(state.docsById, doc.id, doc);
    if (path === 'doc') {
      state.ready = false;
      state.currentDocId = doc.id;
    } else if (path === 'subdoc') {
      state.subdocs.push(doc.id);
    }
  },
  [cmd.REMOVE_DOC_FROM_STATE](state, docId) {
    if (state.currentDocId === docId) state.currentDocId = {};
    if (state.subdocs.includes(docId)) state.subdocs.splice(state.subdocs.indexOf(docId), 1);
    Vue.delete(state.docsById, docId);
  },
  // PIPELINE DONE // TODO
  [cmd.CLEAR_DIRTY](state) {
    state.dirty = {};
  },
  [cmd.SET_DIRTY](state, docId) {
    Vue.set(state.dirty, docId, true);
  },
  [cmd.SET_CLEAN](state, docId) {
    Vue.delete(state.dirty, docId);
  },
  [cmd.SET_INLINE](state, { docId, userId, delta }) {
    if (!state.inLine.hasOwnProperty(docId)) {
      state.inLine[docId] = {};
    }
    if (userId) {
      Vue.set(state.inLine[docId], 'userId', userId);
    }
    if (delta) {
      Vue.set(state.inLine[docId], 'delta', delta);
    }
  },
  [cmd.UNSET_INLINE](state, docId) {
    Vue.delete(state.inLine, docId);
  },
  [cmd.SET_INFLIGHT](state, docId) {
    Vue.set(state.inFlight, docId, state.inLine[docId]);
  },
  [cmd.UPDATE_INLINE_DELTA](state, { docId, delta }) {
    state.inLine[docId].delta = delta;
  },
  [cmd.UNSET_INFLIGHT](state, docId) {
    Vue.delete(state.inFlight, docId);
  },
  [cmd.UPDATE_INFLIGHT_DELTA](state, { docId, delta }) {
    state.inFlight[docId].delta = delta;
  },
  [cmd.SET_SENT_ID](state, packetUuid) {
    Vue.set(state.sentIds, packetUuid, true);
  },
  [cmd.REMOVE_SENT_ID](state, packetUuid) {
    Vue.delete(state.sentIds, packetUuid);
  },
  [cmd.UPDATE_SEQUENCE](state, { docId, sequence }) {
    state.docsById[docId].head.end_sequence = sequence;
  },
  [cmd.UPDATE_LAST_MODIFIED_DATE](state, { docId, date }) {
    state.docsById[docId].updated_at = date;
  },
  [cmd.UPDATE_DOC](state, { docId, delta }) {
    state.docsById[docId].head.delta = OT.compose(state.docsById[docId].head.delta, delta);
    state.docsById[docId].head.contents = OT.apply(state.docsById[docId].head.contents, delta);
  },
  [cmd.UPDATE_DOC_BY_PATH](state, { docId, path, value }) {
    set(state.docsById[docId], path, value);
  },
  [cmd.SET_FATAL_ERROR](state) {
    state.error = true;
  },
  [cmd.ADD_RECONNECTING_DOC](state, docId) {
    state.reconnectingIds.push(docId);
  },
  [cmd.REMOVE_RECONNECTING_DOC](state, docId) {
    state.reconnectingIds.splice(state.reconnectingIds.indexOf(docId), 1);
  },
  [cmd.CAPI_SOCKET](state, socket) {
    state.contentApiSocket = socket;
  },
  [cmd.SET_AUTOSAVE_READY](state, isReady) {
    // the currentDoc (schema = story) has loaded, autosave is ready to save things
    state.ready = isReady;
  }
};
