<template>
  <div class="m-nav m-nav__root">
    <banner-message />
    <nav class="m-nav m-nav--editor m-nav--editor__vue">
      <editor-mobile-subnav
        v-if="isMobile"
        :navLinks="navLinks"
        :pageActive="pageActive"
      />
      <editor-subnav
        v-else
        :navLinks="navLinks"
        :pageActive="pageActive"
      />
    </nav>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import BannerMessage from './banner-message.vue';
import EditorSubnav from './editor-subnav.vue';
import EditorMobileSubnav from './editor-mobile-subnav.vue';

export default {
  components: {
    BannerMessage,
    EditorSubnav,
    EditorMobileSubnav
  },
  computed: {
    ...mapState(['isMobile']),
    ...mapGetters(['currentDoc', 'customFieldsEnabled']),
    navLinks() {
      const docLabel = this.docLabel;

      // commenting this configure link option out temporarily as we regroup on how to store siteConfig data
      // we won't need this link for the Duet Layout project as it currently only serves STrib
      // const configurationLink = { name: 'configure', label: 'Configure' };

      let links = [];
      if (docLabel === 'package') {
        links = [
          { name: 'package', label: 'Package' },
          { name: 'compose', label: 'Compose' },
          { name: 'docs', label: 'Layout', params: { action: 'layout' } },
          { name: 'preview', label: 'Preview' },
          { name: 'preflight', label: 'Finalize' }
        ];
      } else if (docLabel === 'link') {
        links = [
          { name: 'compose', label: 'Compose' },
          { name: 'preflight', label: 'Finalize' }
        ];
      } else if (docLabel === 'custom story') {
        links =  [
          { name: 'customstory', label: 'Project' },
          { name: 'compose', label: 'Compose' },
          { name: 'preview', label: 'Preview' },
          { name: 'preflight', label: 'Finalize' }
        ];
      } else if (docLabel === 'mapstack' || docLabel === 'stream') { // no layout option
        links = [
          { name: 'compose', label: 'Compose' },
          { name: 'preview', label: 'Preview' },
          { name: 'preflight', label: 'Finalize' }
        ];
      } else {
        links = [
          { name: 'compose', label: 'Compose' },
          { name: 'docs', label: 'Layout', params: { action: 'layout' } },
          { name: 'preview', label: 'Preview' },
          { name: 'preflight', label: 'Finalize' }
        ];
      }

      // @TODO: re-enable this when siteConfig data is available
      // if (this.siteConfig.headlessModeEnabled && !this.siteConfig.externalLayoutEnabled) {
      //   links = links.filter(link => link.name !== 'preview');
      //   links = links.filter(link => link.name !== 'layout');
      // }
      // if (this.customFieldsEnabled) {
      //   links.splice(1, 0, configurationLink);
      // }
      return links;
    }
  },
  methods: {
    pageActive(tabName, tabLabel) {
      if (tabName !== 'docs') {
        return this.$route.name === tabName ? 'is-active' : '';
      } else {
        return this.$route.params.action === tabLabel ? 'is-active' : '';
      }
    }
  }
};
</script>
