const util = require('util');

function CommunityMismatchError(doc) {
  Error.call(this);
  Error.captureStackTrace(this, CommunityMismatchError);
  this.docId = doc.id;
  this.entryId = doc.entry_id;
  this.message = 'Community mismatch between Content API doc and SBN entry';
}

util.inherits(CommunityMismatchError, Error);

CommunityMismatchError.prototype.name = 'CommunityMismatchError';
CommunityMismatchError.prototype.status = 500;

module.exports = CommunityMismatchError;
