var request = require('../lib/request');
var parsePagination = require('../lib/parse_chorus_pagination');
var isShortlink = require('../lib/is_shortlink');

export function searchEntries(query) {
  return request('/api/chorus/entries', { query }).then(response => {
    var entries = response._embedded.entry;
    var pagination = parsePagination(response);
    return [entries, pagination];
  });
}

export function searchEntriesByTitle(q, type, perPage, published = false) {
  return request('/api/chorus/entries/title_autocomplete', {
    query: {
      q,
      type,
      per_page: perPage,
      community_id: app.state.get(['community', 'id']),
      published
    }
  });
}

export function getEntryById(id) {
  if (!id) {
    return Promise.resolve(null);
  }
  return request(`/api/chorus/entries/${id}`);
}

export function getEntryByUrl(url) {
  // shortcut to get the slug of the url
  const a = document.createElement('a');
  a.href = url;
  const slugOrId = isShortlink(url)
    ? a.pathname.substring(3) // gets the entry ID
    : a.pathname.substring(1); // gets the entry slug

  // If slugOrId is an empty string, do not request to SBN
  if (!slugOrId) {
    throw new Error('invalid Chorus url');
  }

  return request(`/api/chorus/entries/${slugOrId}`, {
    query: { community_id: app.state.get(['community', 'id']) },
    opts: { handleError: false }
  });
}

export function updateEntry(doc, params) {
  return request(`/api/chorus/entries/${doc.entry_id}`, {
    method: 'PUT',
    query: { community_id: doc.community_id },
    body: params,
    opts: { handleError: false }
  });
}

export function updateEntryFlags(doc, params, trackProgress = true) {
  trackProgress && app.state.set(['requestInProgress'], true);
  if (doc.entry_id == null) {
    return Promise.reject(new Error('Cannot update flags on a doc without an entry_id'));
  }

  return request(`/api/chorus/entries/${doc.entry_id}/flags`, {
    method: 'PUT',
    query: { community_id: doc.community_id },
    body: params
  }).then(() => {
    trackProgress && app.state.set(['requestInProgress'], false);
  }).catch(() => {
    trackProgress && app.state.set(['requestInProgress'], false);
    return Promise.reject();
  });
}
