import * as cmd from '../commands';
import OT from '../../../client/lib/anthem0';
import * as sentryActions from '../../../client/actions/sentry';
import messages from '../../../client/config/messages';
import request from '../../../client/lib/request';

export default {
  reconnectDocs({ state, commit, dispatch }) {
    for (let docId of state.docIds) {
      if (state.inFlight.hasOwnProperty(docId) || state.reconnectingIds.hasOwnProperty(docId)) {
        // if there is an autosave in progress for this doc,
        // we will get any ops we missed as part of a 409 response (if applicable)
        continue;
      }

      commit(cmd.ADD_RECONNECTING_DOC, docId);
      const sequence = state.docsById[docId].head.end_sequence;

      request(`/api/content/docs/${docId}/ops?sequence__gt=${sequence}`, {
        opts: {
          retry: {
            delay: 0,
            maxDelay: 20000
          }
        }
      }).then(function(ops) {
        for (let op of ops) {
          dispatch('remoteUpdate', { 'docId': docId, 'packet': op });
        }
        commit(cmd.REMOVE_RECONNECTING_DOC, docId);
      }).catch(err => {
        dispatch('markError', { error: err, sendToSentry: false });
      });
    }
  },

  localUpdate({ state, commit, rootState, dispatch }, { docId, delta, userId }) {
    userId = userId || rootState.currentUser.id;

    const doc = state.docsById[docId];
    if (!doc) {
      const error = new Error('An invalid docId was supplied to autosave.');
      error.name = 'AutosaveReferenceError';
      error.doc_id = docId;
      dispatch('markError', { error });
      return;
    }
    commit(cmd.UPDATE_LAST_MODIFIED_DATE, { docId, date: new Date().toISOString() });

    if (!state.inLine.hasOwnProperty(docId)) {
      // if there's not already a delta in line for this doc, add it
      commit(cmd.SET_INLINE, { docId, delta: [], userId });
    } else {
      // if there is, ensure we are using the last used userID
      commit(cmd.SET_INLINE, { docId, userId });
    }

    // Remove now obsolete `legacy_updated_at` publish history event if the first
    // local update is being made for a synced legacy story
    const publishHistory = doc.head.contents.publish_history;
    if (publishHistory && publishHistory.length > 0) {
      var legacyPubEventIndex = publishHistory.findIndex(e => e.event_type === 'legacy_updated_at');
      if (legacyPubEventIndex > -1) {
        var legacyPubEvent = publishHistory[legacyPubEventIndex];
        delta.push({
          p: ['publish_history', legacyPubEventIndex],
          ld: legacyPubEvent
        });
      }
    }

    try {
      const oldDelta = state.inLine[docId].delta;
      commit(cmd.SET_INLINE, { docId, delta: OT.compose(oldDelta, delta) });
      commit(cmd.UPDATE_DOC, { docId, delta });
      // setDirty kicks off pipeline
      commit(cmd.SET_DIRTY, docId);
    } catch (error) {
      dispatch('markError', { error });
    }
  },

  /**
   * Called to parse a remote update from the server. Hooked up to an event listener
   * in client/initializers/editor/autosave
   * @param {Number} docId A valid docId for which the update applies
   * @param {Object} packet The server side packet
   * @param {Delta} packet.delta The update itself
   */

  remoteUpdate({ state, commit, dispatch }, { docId, packet }) {
    // If the docId is invalid, simply don't apply the update
    if (state.error || !state.docsById.hasOwnProperty(docId)) {
      return;
    }

    // Ignore packets that we sent
    if (state.sentIds.hasOwnProperty(packet.uuid)) {
      commit(cmd.REMOVE_SENT_ID, packet.uuid);
      return;
    }

    const sequenceCursor = state.docsById[docId].head.end_sequence;

    // Ignore packets we've already applied
    if (packet.sequence <= sequenceCursor) {
      return;
    }

    // If we missed a sequence, throw an error to lock down the editor
    if (packet.sequence > sequenceCursor + 1) {
      dispatch('markError', { error: new Error('Missed a sequence'), sendToSentry: false });
      return;
    }

    // Make sure we have the latest contents recorded for all active docs
    // this.$root.$emit('autosave.check-update', docId); // for Quill

    try {
      let left;
      let right;

      if (state.inFlight.hasOwnProperty(docId)) {
        left = OT.transform(packet.delta, state.inFlight[docId].delta, 'left');
        right = OT.transform(state.inFlight[docId].delta, packet.delta, 'right');
        packet.delta = left;
        commit(cmd.UPDATE_INFLIGHT_DELTA, { docId, delta: right });
      }

      if (state.inLine.hasOwnProperty(docId)) {
        left = OT.transform(packet.delta, state.inLine[docId].delta, 'left');
        right = OT.transform(state.inLine[docId].delta, packet.delta, 'right');
        packet.delta = left;
        commit(cmd.UPDATE_INLINE_DELTA, { docId, delta: right });
      }

      commit(cmd.UPDATE_SEQUENCE, { docId, sequence: packet.sequence });
      commit(cmd.UPDATE_DOC, { docId, delta: packet.delta });
    } catch (err) {
      dispatch('markError', { error: err });
    }

    state.contentApiSocket.emit('autosave.remote-update', docId, packet);
  },

  resolveOnLocalUpdate({ dispatch, getters, state }, { docId, delta, userId }) {
    dispatch('localUpdate', { docId, delta, userId });
    return new Promise((resolve, reject) => {
      if (!getters.isPipelineDirty) {
        return resolve();
      }
      const unsubscribe = this.subscribe((mutation) => {
        if (mutation.type === cmd.SET_CLEAN || mutation.type === cmd.CLEAR_DIRTY) {
          unsubscribe();
          if (!getters.isPipelineDirty && !state.error) {
            return resolve();
          } else {
            return reject();
          }
        }
      });
    });
  },

  resolveOnAutosaveReady({ commit }, { doc, path }) {
    commit(cmd.SET_DOC, { doc, path });
    return new Promise((resolve, reject) => {
      const unsubscribe = this.subscribe((mutation) => {
        if (mutation.type === cmd.SET_AUTOSAVE_READY) {
          unsubscribe();
          return resolve();
        }
        if (mutation.type === cmd.SET_FATAL_ERROR) {
          unsubscribe();
          return reject();
        }
      });
    });
  },

  /**
   * Creates a Promise that resolves when the current pipeline is marked clean
   */
  resolveOnClean({ state }, docId) {
    return new Promise((resolve, reject) => {
      if (Object.keys(state.dirty).length < 1) {
        return resolve();
      }
      const unsubscribe = this.subscribe((mutation) => {
        if (mutation.type === cmd.SET_FATAL_ERROR) {
          unsubscribe();
          return reject();
        }
        if (mutation.type === cmd.SET_CLEAN) {
          unsubscribe();
          return resolve();
        }
      });
    });
  },

  /**
   * Updates top-level doc values as they are emitted from the server. Currently used
   * for status and trashed_at updates. Hooked up to an event listener in the capiSocket.js store plugin
   * @param {Number} docId A valid docId for which the update applies
   * @param {Array} path A path for the value
   * @param {Any} value The value to be set
   */
  updateDocFlag({ state, commit }, { docId, path, value }) {
    // If the docId is invalid, don't apply the update
    if (state.error || !state.docsById.hasOwnProperty(docId)) {
      return;
    }
    commit(cmd.UPDATE_DOC_BY_PATH, { docId, path, value });
  },

  /**
   * Called to remove a doc from state for autosave tracking
   * @param {Number} docId A valid docId to remove from state
   */
  teardownDoc({ commit, state, dispatch }, docId) {
    if (!state.docsById.hasOwnProperty(docId)) {
      return Promise.resolve();
    }

    return dispatch('resolveOnClean', docId).then(() => {
      commit(cmd.REMOVE_DOC_FROM_STATE, docId);

      // if the doc was initialized with greetUsers set to false,
      // this presence.leave just tears down the socket.io room for
      // receiving remote updates

      // app.socket.emit(`presence.${docId}.leave`);
    });
  },

  markError({ commit, state }, { error, reportToSentry = true }) {
    if (!state.error) {
      // only do the below one time ...
      commit('messageBanner/PUT_NOTICE', {
        message: messages.REALTIME_ERROR,
        type: 'negative',
      });
      // unsubscribe from remote updates
      // contentApiSocket.off('autosave.remote-update', this.remoteUpdate);
    }
    commit(cmd.SET_FATAL_ERROR);
    commit(cmd.CLEAR_DIRTY);
    if (reportToSentry) {
      error.from = 'autosave';
      sentryActions.reportError(error, false);
    }
  }

};
