<template>
  <tr
    ref="row"
    :class="[isLeaving ? 'is-leaving' : '']"
    class="m-dashboard__table__row">
    <td class="m-dashboard__table__icon">
      <res-icon v-if="isPackage" size="small" name="package" title="Package" />
      <res-icon v-if="isMapstack" size="small" name="mapstacks" title="Mapstack" />
      <res-icon v-if="isStream" size="small" name="storystream" title="Stream" />
      <res-icon v-if="isLinkPost" size="small" name="linkpost" title="Link post" />
      <res-icon v-if="isCustomstory" size="small" name="grid" title="Custom story" />
    </td>

    <td class="m-dashboard__table__title" data-heading="Title">
      <a :href="docUrl" rel="external">
        <span>{{ title }}</span>
      </a>
    </td>

    <td class="m-dashboard__table__byline" data-heading="Byline">
      {{ contributorsForDoc }}
    </td>

    <td
      v-if="community.printSlugEnabled"
      class="m-dashboard__table__print-slug"
      data-heading="Print slug"
    >
      {{ printSlug }}
    </td>

    <td
      v-if="entitlements && entitlements.includes('use_send_to_print')"
      :class="[lastSentToPrintAt ? 'is-active' : '', 'is-disabled']"
      class="m-dashboard__table__print"
      data-heading="Sent to print"
    >
      <res-icon
        v-if="lastSentToPrintAt"
        :title="'Sent to print on ' + lastSentToPrintAt"
        name="check-circle"
        size="large"
      />
      <res-icon
        v-else
        name="check-circle"
        size="large"
        title="Not sent to print"
      />
    </td>

    <td
      :data-heading="tableActivityHeading"
      class="m-dashboard__table__activity"
      style="text-align: left"
    >
      <doc-cell-activity :doc="doc" :filter="filter" />
    </td>

    <!--
      the `--align-center` class comes from resonance. adding here manually since resonance only
      adds it on render, but not when new rows are injects (which happens in ractive)
    -->
    <td v-if="displayActions" class="m-dashboard__table__actions">
      <doc-cell-actions
        ref="docActions"
        :doc="doc"
        :filter="filter"
        :onMoveCompleted="onMoveCompleted"
        :canArchiveOrTrashStories="canArchiveOrTrashStories" />
    </td>
  </tr>
</template>

<script>
import DocCellActions from './dashboard-doc-cell-actions.vue';
import DocCellActivity from './dashboard-doc-cell-activity.vue';
import getEventDatetime from '../../shared/get_event_datetime';
import isCustomstory from '../../shared/is_customstory';
import isLinkPost from '../../shared/is_link_post';
import isMapstack from '../../shared/is_mapstack';
import isPackage from '../../shared/is_package';
import isStream from '../../shared/is_stream';
import moment from 'moment-timezone';
import { mapState } from 'vuex';
import { toPlaintext } from 'convert-rich-text';

export default {
  components: {
    DocCellActivity,
    DocCellActions,
  },
  props: {
    doc: {
      type: Object,
      required: true,
      default: () => ({}),
    },
    filter: {
      type: String,
      default: '',
    },
    displayActions: {
      type: Boolean,
      default: false,
    },
    canArchiveOrTrashStories: {
      type: Boolean,
      default: false,
    },
    updateTernaryCheckbox: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      isPackage: isPackage(this.doc.head.contents),
      isMapstack: isMapstack(this.doc.head.contents),
      isStream: isStream(this.doc.head.contents),
      isLinkPost: isLinkPost(this.doc.head.contents),
      isCustomstory: isCustomstory(this.doc.head.contents),
      title: this.doc.title,
      tableActivityHeading: 'Actions',
      isLeaving: false,
    };
  },
  computed: {
    ...mapState({
      user: (state) => state.currentUser,
      community: (state) => state.currentCommunity,
      network: (state) => state.network,
      entitlements: (state) => state.entitlements,
      dateFormat: (state) => state.dateFormat,
    }),
    docUrl() {
      return this.isCustomstory ? `/customstory/${this.doc.uuid}` : `/compose/${this.doc.uuid}`;
    },
    contributorsForDoc() {
      // Logic adapted from client/actions/contributors -> contributorsForDoc
      if (this.isPackage) {
        return `${this.network.name} staff`;
      }

      const contributors = this.doc.contributors || [];
      if (!contributors.length) {
        return this.doc.owner.fullName;
      }
      // We only want to display the names of the first two contributors. If there are
      // any extra, display `+N` where N is the number of extra contributors.
      const head = contributors.slice(0, 2);
      const tail = contributors.slice(2);
      let result = head.map((c) => c.fullOrUserName).join(', ');
      if (tail.length > 0) {
        result = `${result} +${tail.length}`;
      }

      return result;
    },
    printSlug() {
      return toPlaintext(this.doc.head.contents.print_slug);
    },
    lastSentToPrintAt() {
      const date = getEventDatetime(this.doc.head.contents.publish_history, 'sent_to_print');
      if (date) {
        const timezone = this.user.timeZone || this.community.timeZone;
        return moment.tz(date, timezone).format(`${this.dateFormat}[ at ]h:mma`);
      }
      return null;
    },
  },
  watch: {
    filter() {
      this.isLeaving = false;
    },
  },
  methods: {
    onMoveCompleted(isLeaving) {
      this.isLeaving = isLeaving;
      const checkbox = this.$refs.row.querySelector('res-checkbox');
      if (this.isLeaving) {
        checkbox.checked = false;
        checkbox.style.display = 'none';
      } else {
        checkbox.style.display = '';
      }
      this.updateTernaryCheckbox();
    }
  }
};
</script>
