var querystring = require('querystring');
var request = require('../lib/request');
var graphqlRequest = require('../lib/graphql_request');
var communityQueries = require('../graphql/queries/communities');
var { getChorusVideoAccess } = require('./users');

export function loadCommunity(communityId, context) {
  return Promise.all([
    getCommunity(communityId, context),
    getEntitlements(communityId),
    getChorusVideoAccess(communityId)
  ]).then(([community, entitlements, canAccessChorusVideo]) => {
    if (community.autoTagging && entitlements.includes('use_no_geo_tags')) {
      community.autoTagging.autotagTagTypes = community.autoTagging.autotagTagTypes.filter(t => !t.includes('geo'));
    }
    app.state.set(['community'], community);
    app.state.set(['entitlements'], entitlements);
    app.state.set(['user', 'canAccessChorusVideo'], canAccessChorusVideo || false);
  });
}

export async function getCommunity(communityId, context) {
  var { community } = await graphqlRequest('/api/tower/graphql', {
    query: communityQueries[`${context}Community`],
    variables: { communityId }
  });
  if (!community) {
    throw new Error('Community not found.');
  }

  // Convert CONSTANT_CASE enums to expected case
  if (context === 'Editor') {
    community.autoTagging.autolinkTagTypes = community.autoTagging.autolinkTagTypes.map(tagType => tagType.toLowerCase());
    community.autoTagging.autotagTagTypes = community.autoTagging.autotagTagTypes.map(tagType => tagType.toLowerCase());
  }

  return community;
}

export async function getPrintOptions(communityId) {
  var { community } = await graphqlRequest('/api/tower/graphql', {
    query: `query PrintOptions($communityId: Int!) {
      community(id: $communityId) {
        desk: siteConfigString(key: "print_desks")
        category: siteConfigString(key: "print_categories")
        article_type: siteConfigString(key: "print_article_types")
        placement: siteConfigString(key: "print_placements")
      }
    }`,
    variables: { communityId }
  });
  if (!community) {
    throw new Error('Community not found.');
  }

  return Object.keys(community).reduce((acc, key) => {
    if (!community[key]) {
      return acc;
    }

    return {
      ...acc,
      // split on new lines and clean up
      [key]: (community[key] ? community[key].split('\n') : [])
        .map(item => item.trim())
        .filter(Boolean),
    };
  }, {});
}

export function getEntitlements(communityId) {
  // .slice(1) removes the leading ? from the first query item
  var query = querystring.parse(window.location.search.slice(1));
  return request('/api/entitlements', {
    query: {
      userId: app.state.get(['user', 'id']),
      communityId: communityId,
      entitlements: query.entitlements ? query.entitlements.split(',') : []
    }
  });
}
