<template>
  <section class="m-chorus-help">
    <res-button design="secondary" class="m-chorus-help__button">
      <res-icon name="publish" />
      <a href="https://chorussupport.zendesk.com/hc/en-us" target="_blank" rel="noopener noreferrer">Open documentation</a>
    </res-button>
    <fieldset v-if="announcementEntries.length > 0" class="m-chorus-help__announcements">
      <legend>What&#8217;s new</legend>
      <div v-for="(announcement, index) in announcementEntries" :key="index" class="m-chorus-help__entry" @click="openEntry(announcement)">
        <p><a>
          <res-badge v-if="announcement.isNew" size="smallest" design="grass">New</res-badge>
          {{ announcement.title }}
        </a></p>
        <p v-if="announcement.blurb">{{ announcement.blurb }}</p>
      </div>
    </fieldset>
    <p class="m-chorus-help__feedback">You can also <a type="button" @click="openContactForm()">contact us</a>.</p>
  </section>
</template>

<script>
import { mapState } from 'vuex';
import request from '../../../client/lib/request';
import messages from '../../../client/config/messages';
import parsePagination from '../../../client/lib/parse_chorus_pagination';
import { SET_OVERLAY } from './../../store/commands';

export default {
  data() {
    return {
      announcementEntries: [],
      page: 1
    };
  },
  computed: {
    ...mapState([
      'announcements',
      'currentUser'
    ]),
  },
  mounted() {
    this.getAnnouncementEntries();
  },
  methods: {
    handleError() {
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED
      });
    },
    async getAnnouncementEntries() {
      const query = {
        community_id: this.announcements.community_id,
        entry_group_ids: this.announcements.entry_group_id,
        draft: false,
        hidden: false,
        per_page: 3,
        order: 'date'
      };
      try {
        const response = await request('/api/chorus/entries', { query });
        const entries = response._embedded.entry; // returns an Array
        const pagination = parsePagination(response);
        this.announcementEntries = entries;
        this.page = pagination.page;
      } catch(error) {
        this.handleError();
      }
    },
    openEntry(entry) {
      this.$store.commit(SET_OVERLAY, {
        childProps: {
          dek: entry.dek,
          body: entry.body,
          body_extended: entry.body_extended,
          title: entry.title,
          url: entry.url
        },
        component: 'chorus-help-announcement'
      });
      // update timestamp for lastAnnouncementReadAt
      const lastRead = new Date().toISOString(); // today/now
      // store it in the vuex state...
      this.currentUser.lastAnnouncementReadAt = lastRead;
      // store it in the user session...
      sessionStorage.setItem('lastAnnouncementReadAt', lastRead);
    },
    openContactForm() {
      this.$store.commit(SET_OVERLAY, {
        childProps: {},
        component: 'chorus-help-contact-form'
      });
    }
  }
};
</script>
