<template>
  <transition name="vue-transition--slide" @after-enter="isCollapsed = false" @after-leave="isCollapsed = true">
    <div v-show="!isCollapsed" class="m-search-panel">
      <section class="m-search-panel__content">
        <res-button
          v-if="isMobile"
          :key="mobileKey"
          class="m-search-panel__close"
          paddingless
          @click.prevent="closeSearchPanel()">
          <res-icon name="x"/><button>Close</button>
        </res-button>
        <div class="m-search-panel__heading">
          <h2>{{ heading }}</h2>
          <res-button
            v-if="searchView"
            :disabled="$refs.searchForm && $refs.searchForm.emptyForm"
            class="m-search-panel__clear"
            paddingless
            @click="clearSearch">
            <res-icon size="smaller" name="refresh"/>
            <button>Clear</button>
          </res-button>
          <res-toggle-buttons v-if="showSavedSearches" :key="toggleKey">
            <res-button
              :active="searchView"
              value="1"
              @click="() => setSearchView(true)">
              <res-icon name="search"/><button/>
            </res-button>
            <res-button
              :active="!searchView"
              value="2"
              @click="() => setSearchView(false)">
              <res-icon name="list"/><button/>
            </res-button>
          </res-toggle-buttons>
        </div>
        <search-form
          v-show="searchView"
          ref="searchForm"
          :isCollapsed="isCollapsed"
          :isSearching="isSearching"
          :updateQuery="updateQuery"
          :closeSearchPanel="closeSearchPanel"
          :defaultQuery="defaultQuery"
          :formattedQuery="formattedQuery"
          :openCreateModal="openCreateModal"
          :setSavedSearchName="setSavedSearchName"
          :currentSavedSearch="currentSavedSearch"
          :showSaveButton="showSavedSearches" />
        <saved-search-list
          v-show="!searchView && showSavedSearches"
          :searches="savedSearches"
          :goToSearchView="goToSearchView"
          :renameSavedSearch="renameSavedSearch"
          :openDeleteModal="openDeleteModal"
          :runSavedSearch="runSavedSearch"
          :currentSavedSearch="currentSavedSearch" />
      </section>
    </div>
  </transition>
</template>

<script>
import SavedSearchList from './saved-search-list.vue';
import SearchForm from './search-form.vue';
import messages from '../../../client/config/messages';
import { SET_MODAL } from '../../store/commands';
import { getSearches, addSearch, renameSearch, deleteSearch, runSearch } from '../../actions/saved_search';
import { mapState } from 'vuex';
import { reportError } from '../../../client/actions/sentry';

export default {
  components: {
    SearchForm,
    SavedSearchList,
  },
  props: {
    isCollapsed : {
      type: Boolean,
      default: false,
      required: true
    },
    isSearching : {
      type: Boolean,
      default: false,
      required: true
    },
    updateQuery: {
      type: Function,
      default: () => {},
      required: true,
    },
    defaultQuery: {
      type: Object,
      default: () => ({}),
      required: true,
    },
    formattedQuery: {
      type: Array,
      default: () => []
    },
    setSavedSearchName: {
      type: Function,
      default: () => {},
      required: true,
    },
  },
  data() {
    return {
      mobileKey: 1,
      toggleKey: 1,
      searchView: true,
      saving: false,
      savedSearches: [],
      currentSavedSearch: {},
    };
  },
  computed: {
    ...mapState(['isMobile', 'currentCommunity']),
    heading() {
      return this.searchView ? 'Search' : 'Saved searches';
    },
    showSavedSearches() {
      // Only show saved searches view on main dashboard
      // (exclude archive, trash, and pages)
      return this.$route.name === 'dashboard';
    }
  },
  watch: {
    currentCommunity() {
      // If the community was not available on mount, fetch the searches
      // once the community is loaded in app state
      if (this.currentCommunity.id && this.showSavedSearches) {
        this.fetchSearches();
      }
    },
    ['currentSavedSearch.name'](value) {
      const name = value || '';
      this.setSavedSearchName(name);
    },
    ['$route.name'](name) {
      if (name === 'dashboard') {
        // If navigating from pages, archive, or trash to the main dashboard
        // fetch the searches if they have not been already fetched
        !this.savedSearches.length && this.fetchSearches();
      } else {
        // Ensure search form is always visible on pages, archive, and trash
        this.searchView = true;
      }
      // Clear searches when dashboard route name changes
      this.clearSearch();
      if (this.isMobile) {
        this.closeSearchPanel();
      }
    }
  },
  mounted() {
    if (this.currentCommunity.id && this.showSavedSearches) {
      this.fetchSearches();
    }
  },
  methods: {
    focusKeywordInput() {
      this.$refs.searchForm.$refs.keywordInput.focus();
    },
    closeSearchPanel() {
      this.$emit('collapse');
    },
    clearSearch() {
      this.$refs.searchForm.clearSearch();
      this.clearSavedSearch();
    },
    setSearchView(value) {
      this.searchView = value;
    },
    goToSearchView() {
      if (!this.searchView) {
        this.searchView = true;
        this.toggleKey++;
      }
    },
    async fetchSearches() {
      const params = { communityId: String(this.currentCommunity.id) };
      try {
        const searches = await getSearches(params);
        this.savedSearches = searches;
      } catch (error) {
        this.handleError(error);
      }
    },
    async addSavedSearch(params) {
      this.saving = true;
      try {
        const searchId = await addSearch(params);
        this.currentSavedSearch = { ...params, searchId };
        this.fetchSearches();
        this.searchView = false;
        this.toggleKey++;
      } catch (error) {
        this.handleError(error);
      }
      this.saving = false;
    },
    openCreateModal(saveButtonRef, keyword) {
      this.$store.commit(SET_MODAL, {
        childProps: {
          addSavedSearch: this.addSavedSearch,
          saving: this.saving,
          keyword,
        },
        component: 'saved-search-create-modal',
      });
      // Remove focus from the `Save search` button to allow autofocus in the
      // create modal input
      saveButtonRef.blur();
    },
    async renameSavedSearch(search, name) {
      this.saving = true;
      const params = {
        communityId: String(this.currentCommunity.id),
        searchId: search.searchId,
        name,
      };
      try {
        await renameSearch(params);
        this.fetchSearches();
        if (this.currentSavedSearch.searchId === search.searchId) {
          const updatedSearch = { ...search, name };
          this.currentSavedSearch = updatedSearch;
        }
      } catch (error) {
        this.handleError(error);
      }
      this.saving = false;
    },
    async deleteSavedSearch(searchId) {
      this.saving = true;
      const params = {
        communityId: String(this.currentCommunity.id),
        searchId,
      };
      try {
        await deleteSearch(params);
        this.fetchSearches();
        // Clear search if it's the one being deleted
        if (this.currentSavedSearch.searchId === searchId) {
          this.clearSearch();
        }
      } catch (error) {
        this.handleError(error);
      }
      this.saving = false;
    },
    openDeleteModal(searchId) {
      this.$store.commit(SET_MODAL, {
        childProps: {
          deleteSavedSearch: () => this.deleteSavedSearch(searchId),
          saving: this.saving,
        },
        component: 'saved-search-delete-modal',
      });
    },
    async runSavedSearch(search) {
      const { searchId, query } = search;
      const params = {
        communityId: String(this.currentCommunity.id),
        searchId,
      };
      try {
        this.updateQuery({ ...this.defaultQuery, q: query });
        await runSearch(params); // increments the search count
        this.currentSavedSearch = search;
        if (this.isMobile) {
          this.closeSearchPanel();
        }
      } catch (error) {
        // Swallow errors from incrementing the search count
        // as we are not doing anything with search counts right now.
      }
    },
    handleError(error) {
      reportError(error, false);
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED,
      });
    },
    clearSavedSearch() {
      this.currentSavedSearch = {};
    }
  }
};
</script>
