
<template>
  <transition name="vue-transition--slide">
    <div v-if="currentSidebarTag" :class="`m-sidebar m-sidebar--${pageType} ${orientationClass} ${pushContentClass}`">
      <header :class="`m-${currentSidebarTag}`">
        <h2 v-if="currentSidebar.title">{{ currentSidebar.title }}</h2>
        <res-button class="m-sidebar__close" @click.prevent="closeSidebar()">
          <res-icon name="x"/><button>Close</button>
        </res-button>
      </header>
      <div class="m-sidebar__content">
        <component :is="currentSidebarTag" :id="currentSidebar.id" :childProps="currentSidebar.childProps" />
      </div>
    </div>
  </transition>
</template>

<script>
import { SET_SIDEBAR, SET_OVERLAY } from '../store/commands';
import { mapState } from 'vuex';
import { getPageType } from '../lib/get_page_type';

import ChorusHelp from './sidebars/chorus-help.vue';

export default {
  components: {
    ChorusHelp
  },
  computed: {
    ...mapState([
      'currentSidebar',
      'currentOverlay'
    ]),
    currentSidebarTag() {
      return this.currentSidebar.type || null;
    },
    orientationClass() {
      return this.currentSidebar.orientation === 'left' ? 'm-sidebar--orientation-left' : 'm-sidebar--orientation-right';
    },
    pushContentClass() {
      return this.currentSidebar.pushContent ? 'm-sidebar--push-content' : '';
    },
    pageType() {
      return getPageType(this.$route.name);
    }
  },
  watch: {
    currentSidebarTag() {
      if (!this.currentSidebarTag) {
        this.$store.commit(SET_SIDEBAR, null);
      }
    }
  },
  methods: {
    closeSidebar() {
      if (this.currentSidebarTag === 'chorus-help' && (this.currentOverlay.component === 'chorus-help-announcement' || this.currentOverlay.component === 'chorus-help-contact-form')) {
        this.$store.commit(SET_OVERLAY, {
          childProps: null,
          component: null
        });
      }
      this.$store.commit(SET_SIDEBAR, null);
    }
  }
};
</script>
