import request from '../../client/lib/request';

/**
 * Fetches searches by a user on a community;
 * returns newest first
 * @param params {object}
 * @param params.communityId {string}
 * @returns {array} of searches
 */
export async function getSearches(params) {
  return await request('/api/saved_search', {
    method: 'GET',
    query: params,
    opts: { handleError: false }
  });
}

/**
 * Adds a saved search
 * @param params {object}
 * @param params.communityId {string}
 * @param params.name {string} display name of search
 * @param params.query {string} query string which appears in route (q)
 * @returns {string} searchId
 */
export async function addSearch(params) {
  return await request('/api/saved_search', {
    method: 'POST',
    body: params,
    opts: { handleError: false }
  });
}

/**
 * Deletes a single search
 * @param params {object}
 * @param params.communityId {string}
 * @param params.searchId {string}
 */
export async function deleteSearch(params) {
  return await request('/api/saved_search', {
    method: 'DELETE',
    body: params,
    opts: { handleError: false }
  });
}

/**
 * Edit the name of a saved search
 * @param params {object}
 * @param params.communityId {string}
 * @param params.searchId {string}
 * @param params.name {string} new name for search
 */
export async function renameSearch(params) {
  return await request('/api/saved_search', {
    method: 'PUT',
    body: params,
    opts: { handleError: false }
  });
}

/**
 * Increments search count and last searched date;
 * should be called every time user runs the search from saved panel
 * @param params {object}
 * @param params.communityId {string}
 * @param params.searchId {string}
 */
export async function runSearch(params) {
  return await request('/api/saved_search/touch/', {
    method: 'PUT',
    body: params,
    opts: { handleError: false }
  });
}

/**
 * Deletes all searches for the user on this community
 * @param params {object}
 * @param params.communityId {string}
 */
export async function deleteAll(params) {
  return await request('/api/saved_search/deleteAll', {
    method: 'PUT',
    body: params,
    opts: { handleError: false }
  });
}
