<template>
  <div class="m-layout-mode__tools">
    <form-subsection label="Stories">
      <div class="m-layout-mode__group">
        <label class="is-fixed">Card size</label>
        <autosave-radio
          :value="packageStoryLayout"
          :options="packageStoryLayoutOptions"
          :path="packageStoryLayoutPath"
          :key="packageStoryLayout"
          :onResolve="sendFetch"
          :onError="sendError"
        />
        <!-- stuck a key on there so it will re-render ... it only updates the checked value on mounted -->
      </div>
    </form-subsection>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import AutosaveRadio from '../autosave-radio.vue';
import FormSubsection from './form-subsection.vue';
import { get } from 'lodash';
import { packageStoryLayoutOptions } from '../../config/layoutOptions';

export default {
  components: {
    AutosaveRadio,
    FormSubsection,
  },
  props: {
    coverIndex: {
      type: Number,
      required: true
    },
    postMessage: {
      type: Function,
      required: true
    },
  },
  data() {
    return {
      packageStoryLayoutOptions,
    };
  },
  computed: {
    ...mapGetters([
      'currentDoc',
    ]),
    packageStoryLayoutPath() {
      return ['draft', 'package_cover', this.coverIndex, 'story', 'layout'];
    },
    packageStoryLayout() {
      return get(this.currentDoc.head.contents, this.packageStoryLayoutPath);
    },
  },
  methods: {
    sendFetch() {
      this.postMessage({
        event: 'refetch'
      });
    },
    sendError(error) {
      this.postMessage({
        event: 'error',
        message: error
      });
    }
  }
};

</script>
