const { sortBy } = require('lodash');

// Takes a list of user community memberships and returns a list grouped by
// network and sorted alphabetically by network name. This transformation is used
// by both the Editorial and Asset dashboard community pickers.
module.exports = function groupSortNetworkMemberships(memberships = []) {
  let grouped = memberships.reduce((grouped, membership) => {
    let community = membership.community;
    let network = community.network;

    // if the network grouping exists in the result, add the membership community
    // otherwise, create a new network grouping with the current membership community
    let networkIndex = grouped.findIndex(({ id }) => id === network.id);
    if (networkIndex > -1) {
      grouped[networkIndex].communities.push(community);
    } else {
      grouped.push({
        id: network.id,
        name: network.name,
        logo: network.svgLogo,
        communities: [community]
      });
    }

    return grouped;
  }, []);

  // sort alphabetically by network name and return
  return sortBy(grouped, 'name');
};
