<template>
  <div :id="chipId" :title="chipLabel" class="m-chip">
    <res-badge size="medium" class="m-chip__selection">
      <span>{{ truncateString(chipLabel) }}</span><res-icon name="x" title="remove selection" @click="removeChip(chipId)" />
    </res-badge>
  </div>
</template>

<script>
export default {
  props: {
    chipLabel: {
      type: String,
      required: true
    },
    chipId: {
      type: String,
      required: true
    },
    stringLimit: {
      type: Number,
      default: 40
    }
  },
  methods: {
    removeChip(id) {
      this.$emit('removeSelection', id);
    },
    truncateString(stringItem) {
      if (stringItem.length <= this.stringLimit) {
        return stringItem;
      }
      // Return stringItem truncated with '...' concatenated to the end of stringItem.
      return stringItem.slice(0, this.stringLimit) + '...';
    },
  }
};
</script>
