var { omit } = require('lodash');
var Sentry = require('@sentry/browser');
var messageActions = require('./messages');
var HttpStatusError = require('../lib/errors/http_status_error');

export function handleError(err, emit = true) {
  reportError(err, emit);
  return Promise.reject(err);
}

export function reportError(err, emit = true) {
  // Log out stack for debugging, but not in test mode to stop polluting the
  // test output
  let env;
  try {
    env = app.env; // get from app for Ractive pages
  } catch (error) {
    env = process.env.NODE_ENV; // get from node for Vue pages
  }

  if (env !== 'test') {
    console.error(err);
    console.error(err.stack || err.message);
  }

  if (!(err instanceof HttpStatusError)) {
    Sentry.captureException(err, {
      tags: {
        error_name: err.name,
        from: err.from
      },
      extra: omit(err, 'name', 'message', 'stack', 'from')
    });
  }

  if (emit) {
    messageActions.showError();
  }
}

export function reportMessage(message, opts) {
  if (app.env !== 'test') {
    console.warn(message, opts);
  }

  Sentry.captureMessage(message, opts);
}
