import getPackageStories from '../../shared/get_package_stories';
import isPackage from '../../shared/is_package';
import isPublished from '../../client/lib/is_published';
import { scheduleDoc } from '../../client/actions/publish';
import { searchDocs } from '../../client/actions/docs';

// Adapted from client/actions/publish.js > publishOrSchedule
export async function unscheduleDoc(doc, userId) {
  const publishScheduledFor = null;
  const contents = doc.head.contents;

  // If it's a package, unschedule its unpublished member docs first
  if (isPackage(contents)) {
    const toc = contents.draft.package_toc;
    const uuids = getPackageStories(toc).map(story => story.uuid).join(',');
    const { docs: memberDocs } = await searchDocs({ uuids });
    const unpublished = memberDocs.filter(memberDoc => !isPublished(memberDoc.status));
    const requests = unpublished.map(memberDoc => scheduleDoc(memberDoc, publishScheduledFor, { userId }));

    await Promise.all(requests);
  }

  const updatedDoc = await scheduleDoc(doc, publishScheduledFor, { userId });
  return updatedDoc;
}
