const hasStarted = ({ now, start }) => now > start;
const hasEnded = ({ now, end }) => end && now > end;

module.exports = function isLivePost(contents) {
  let now = Date.now();
  let start = Date.parse(contents.draft.coverage_start_time);
  let end = Date.parse(contents.draft.coverage_end_time);
  return hasStarted({ now, start }) && !hasEnded({ now, end });
};
