var chunkBy = require('../chunk_by');
var groupIsComplete = require('./group_is_complete');

// Loop through the provided delta and return an array of { offset, grouping }
// line objects, grouped together into sub-arrays of adjacent lines with the
// same grouping value.
//
// e.g.
// [
//   [{ id: '123', offset: 5, grouping: '3-up' }],
//   [{ id: 'abc', offset: 7, grouping: '2-up' }, { id: 'def', offset: 9, grouping: '2-up' }],
//   [{ id: 'xyz', offset: 23, grouping: '2-up' }]
// ]
//
// The item is isolated to its own subgroup, since the next item has a different 'grouping' value
// The last item is also isolated, because it is non-adjacent (i.e. diff from previous offset != 2)
module.exports = function parseGroups(delta) {
  var offset = 0;
  var lines = [];

  // Find each line with a 'grouping' attribute, and add it to the lines array
  for (let op of delta.ops) {
    if (op.attributes && op.attributes.grouping) {
      lines.push({
        id: op.attributes.id,
        offset: offset,
        grouping: op.attributes.grouping
      });
    }
    offset += (typeof op.insert === 'string') ? op.insert.length : 1;
  }

  // Chunk up the lines into groups of consecutive lines with the same grouping
  // value, with the corresponding max length (2-up => max length 2).
  return chunkBy(lines, (chunk, next) => {
    var prev = chunk[chunk.length - 1];
    return (
      next.grouping === prev.grouping &&
      next.offset - prev.offset === 2 &&
      !groupIsComplete(chunk)
    );
  });
};
