import { throttle } from 'lodash';
import request from '../../../client/lib/request';
import OT from '../../../client/lib/anthem0';
import uuid from 'uuid';
import hash from '../../../shared/content-hash';
import messages from '../../../client/config/messages';
import { autosaveThrottle, rescueTimeout } from '../../config/timings';
import contentsDefaults from '../../lib/initialize/contents_defaults';
import convertObjectFormat from '../../lib/initialize/convert_object_format';
import {
  SET_CLEAN,
  SET_DIRTY,
  SET_INFLIGHT,
  UPDATE_INFLIGHT_DELTA,
  SET_SENT_ID,
  UPDATE_SEQUENCE,
  SET_DOC,
  UNSET_INLINE,
  UNSET_INFLIGHT,
  SET_AUTOSAVE_READY,
} from '../commands';

// let initializingIds = {}; // TODO - state
const MESSAGE_RESCUE_VERSION = ['story'];

export default function autosavePipeline() {
  return function(store) {

    // ##### Helper Methods #####
    function rescueKey(docId) {
      return `rescue:${docId}`;
    }
    function messageKey(docId) {
      return `message:${docId}`;
    }

    async function initDoc({ doc, path }) {
      try {
        await checkForRescue(doc.id);
        await setBaseContents(doc.id);
        if (path === 'doc') {
          // initSubdocs(doc.id); TODO
          store.commit(SET_AUTOSAVE_READY, true);
        }
      } catch (error) {
        store.dispatch('markError', { error });
      }
    }

    function setBaseContents(docId) {
      const doc = store.getters.currentDoc;
      const contents = store.getters.contents;
      const defaults = contentsDefaults(doc, store.state.currentUser);
      let delta = [];
      for (let [key, value] of Object.entries(defaults)) {
        if (contents[key] == null && value != null) {
          delta.push({ p: [key], oi: value });
        }
      }
      // Set defaults on nested draft object
      if (defaults.draft) {
        if (contents.draft) {
          for (let [key, value] of Object.entries(defaults.draft)) {
            if (contents.draft[key] == null && value != null) {
              delta.push({ p: ['draft', key], oi: value });
            }
          }
        } else {
          delta.push({ p: ['draft'], oi: defaults.draft });
        }
      }
      delta = delta.concat(convertObjectFormat(contents));
      return delta.length > 0 ? store.dispatch('resolveOnLocalUpdate', { docId, delta, userId: -1 }) : Promise.resolve();
    }

    // ##### Request Logic #####
    // Sends autosave requests, handles initial error parsing (including retry flow)

    // Throttled function that, when executed, sends updates for all inline docs
    var sendIfReady = throttle(function sendIfReady() {
      for (let docId of store.getters.docIds) {
        if (!store.state.autosave.inFlight.hasOwnProperty(docId) && store.state.autosave.inLine.hasOwnProperty(docId)
          && !store.state.autosave.error) {
          store.commit(SET_INFLIGHT, docId);
          store.commit(UNSET_INLINE, docId);
          storeRescueVersion(docId);
          sendUpdate(docId);
        }
      }
    }, autosaveThrottle, { leading: false, trailing: true });

    // Called by `sendIfReady` to actually send inLine data as a single packet
    function sendUpdate(docId) {
      // If an error has occurred prior, don't send
      if (store.state.autosave.error) {
        return;
      }

      // If we have anything else waiting, need to send that too
      // in order for contentHash to be accurate
      if (store.state.autosave.inLine.hasOwnProperty(docId)) {
        try {
          const composed = OT.compose(store.state.autosave.inFlight[docId].delta, store.state.autosave.inLine[docId].delta);
          store.commit(UPDATE_INFLIGHT_DELTA, { docId, delta: composed });
          store.commit(UNSET_INLINE, docId);
          storeRescueVersion(docId);
        } catch (error) {
          store.dispatch('markError', { error: error });
          return;
        }
      }

      const docCursor = store.state.autosave.docsById[docId];
      const sequenceCursor = docCursor.head.end_sequence;
      const contentsCursor = docCursor.head.contents;
      const delta = store.state.autosave.inFlight[docId].delta;
      const packetUuid = uuid.v4();
      const sequence = sequenceCursor + 1;

      store.commit(SET_SENT_ID, packetUuid);
      return request('/api/content/docs/' + docCursor.id + '/contents', {
        method: 'PATCH',
        body: {
          user_id: store.state.autosave.inFlight[docId].userId,
          delta,
          sequence,
          uuid: packetUuid,
          content_hash: hash(contentsCursor),
          schema_version: docCursor.schema_version,
        },
        opts: {
          handleError: false,
          retry: {
            delay: 0,
            maxDelay: 20000
          }
        }
      }).then(() => {
        window.localStorage.removeItem(rescueKey(docId));
        store.commit(UNSET_INFLIGHT, docId);
        store.commit(UPDATE_SEQUENCE, { docId, sequence });
        if (store.state.autosave.inLine.hasOwnProperty(docId)) {
          store.commit(SET_DIRTY, docId);
        } else {
          store.commit(SET_CLEAN, docId);
        }
        sendIfReady();
      }).catch(err => {
        const status = err.response && err.response.status;
        if (status === 409) {
          // the rebase case, call remoteUpdate on all the packets to catch up then try again
          return retryAfterRebase(docId, err);
        } else {
          // reject to let the next catch block handle the error
          return Promise.reject(err);
        }
      }).catch(err => {
        // some other status code or non-network error happened, shut down the
        // editor and save the latest rescue version
        store.dispatch('markError', { error: err, sendToSentry: false });
        // removed 5s delay on saving the rescue version < unsure its purpose
        saveRescueVersion(docId, false);
      });
    }

    // If we get a 409 conflict error, do:
    // A) Rebase and retry if we've missed data
    // B) Shut it down if it's any other kind of conflict error (i.e. too far ahead)
    function retryAfterRebase(docId, err) {
      // must clone the response so that reportError can also read the body,
      // should this fail
      var response = err.response.clone();
      return response.json().then(function(data) {
        if (/too low/.test(data.message) && data.packets) {
          for (let packet of data.packets) {
            store.dispatch('remoteUpdate', { docId, packet });
          }
          return sendUpdate(docId);
        } else {
          return Promise.reject(err);
        }
      });
    }

    // ##### Rescue Logic #####
    function storeRescueVersion(docId) {
      var delta = [];
      var contents = store.state.autosave.docsById[docId].head.contents;

      try {
        if (store.state.autosave.inFlight.hasOwnProperty(docId)) {
          delta = OT.compose(delta, store.state.autosave.inFlight[docId].delta);
        }
        if (store.state.autosave.inLine.hasOwnProperty(docId)) {
          delta = OT.compose(delta, store.state.autosave.inLine[docId].delta);
        }
        // TODO:
        // if (window.quills[docId]) {
        //   var quill = window.quills[docId];
        //   var path = quillContentPaths.get([docId, 'path']);
        //   if (quill && path) {
        //     setPath(contents, path, quill.getContents());
        //   }
        // }
      } catch (error) {
        store.dispatch('markError', { error: error });
        return;
      }
      var docCursor = store.state.autosave.docsById[docId];
      const json = {
        doc_id: docCursor.id,
        user_id: store.state.currentUser.id,
        schema_version: docCursor.schema_version,
        schema_name: docCursor.schema_name,
        end_sequence: null,
        contents: contents,
        delta: delta
      };
      window.localStorage.setItem(rescueKey(docId), JSON.stringify(json));
    }

    // On a true error, try to save a rescue version server-side
    function saveRescueVersion(docId, flashNow) {
      // Don't save a rescue version if one doesn't exist (i.e. due to a local error)
      const version = window.localStorage.getItem(rescueKey(docId));
      if (!version) {
        return;
      }
      return request('/api/content/versions', {
        method: 'POST',
        body: JSON.parse(version),
        opts: { handleError: false }
      }).then(resp => {
        const isPackageDashboard = window.location.pathname.indexOf('package') > -1;
        if (!isPackageDashboard && MESSAGE_RESCUE_VERSION.indexOf(resp.schema_name) > -1) {
          var docUuid = store.state.autosave.docsById[docId].uuid;
          var message =
            messages.RESCUE_SAVED_REASSURE.replace(
              '{url}',
              `/compose/${docUuid}/version/${resp.id}`
            );

          // If we are saving this rescue version on a page reload, flash a message
          // and report to Sentry. Otherwise don't, as it's already been handled.
          if (flashNow) {
            store.commit('messageBanner/PUT_NOTICE', {
              message: message
            });
          } else {
            setNextFlash(docId, message);
          }
        }
        window.localStorage.removeItem(rescueKey(docId));
      }).catch(err => {
        store.dispatch('markError', { error: err, sendToSentry: false });
      });
    }

    // When called, checks to see if a rescue version is saved in storage for the
    // given doc and saves it to the server if it exists
    function checkForRescue(docId) {
      setTimeout(() => getNextFlash(docId), rescueTimeout);
      if (window.localStorage.hasOwnProperty(rescueKey(docId))) {
        // Allow error handler to be registered before emitting
        setTimeout(() => {
          return saveRescueVersion(docId, false);
        }, rescueTimeout/2);
      } else {
        return Promise.resolve();
      }
    }

    function setNextFlash(docId, message) {
      window.localStorage.setItem(messageKey(docId), JSON.stringify(message));
    }

    function getNextFlash(docId) {
      var key = messageKey(docId);
      if (window.localStorage.hasOwnProperty(key)) {
        var message = JSON.parse(window.localStorage.getItem(key));
        window.localStorage.removeItem(key);
        store.commit('messageBanner/PUT_NOTICE', {
          message: message
        });
      }
    }

    store.subscribe((mutation, state) => {
      switch (mutation.type) {
      case SET_DIRTY:
        sendIfReady();
        break;
      case SET_DOC:
        initDoc(mutation.payload);
        break;
      }
    });

  };
}
