var { get: getPath } = require('lodash');

module.exports = function getWordCount(delta) {
  var text = delta.ops.reduce(function(text, op) {
    if (typeof op.insert === 'string' && !getPath(op, ['attributes', 'mark'])) {
      return text + op.insert;
    } else {
      return text;
    }
  }, '');
  var words = text.trim().match(/\S+/g);
  return words ? words.length : 0;
};
