<template>
  <a :href="href" @click.prevent="$emit('communityId', community.id)">
    <li :class="{ 'is-active': selectionId === community.id }" class="m-community-select__item">
      <div class="m-community-select__avatar">
        <img :src="community.faviconUrl || defaultLogoUrl" class="m-community-select__icon" />
      </div>
      <span class="m-community-select__name">{{ community.name }}</span>
      <res-icon v-if="selectionId === community.id" name="check-circle" size="medium"/>
    </li>
  </a>
</template>

<script>
export default {
  props: {
    community: {
      type: Object,
      required: true
    },
    defaultLogoUrl: {
      type: String,
      required: true
    },
    selectionId: {
      type: Number,
      default: null
    },
  },
  computed: {
    href() {
      // @todo  support a URL format.
      return '#';
    }
  }
};
</script>
