import { updateDoc } from '../../client/actions/docs';
import { updateEntryFlags } from '../../client/actions/entry';
import { unscheduleDoc } from './publish';

export async function toggleArchived(doc) {
  const archived = !doc.archived;
  const updatedDoc = await updateDoc(doc.id, { archived });
  return updatedDoc;
}

// Adapted from client/actions/docs.js > setTrashed
export async function updateTrashedAt(doc, userId) {
  const prevTrashedAt = doc.head.contents.trashed_at;
  const nextTrashedAt = prevTrashedAt ? null : new Date().toISOString();

  if (nextTrashedAt && doc.status === 'SCHEDULED') {
    await unscheduleDoc(doc, userId);
  }

  const updatedDoc = await updateDoc(doc.id, {
    delta: [{
      p: ['trashed_at'],
      od: prevTrashedAt,
      oi: nextTrashedAt
    }]
  });
  await updateEntryFlags(doc, { trashed_at: nextTrashedAt }, false);
  return updatedDoc;
}
