const LOOKS_LIKE_URL = /\w\.[^\s]+/;

export default function isValidUrl(url) {
  var host = url.replace(/^(https?:\/\/|mailto:)/, '').split('/')[0];
  var containsComma = /,/.test(host);
  var containsSpaceEncoding = /(%20)/.test(host);
  var isLocalFile = /^file:\/\//.test(url);
  var looksValid = LOOKS_LIKE_URL.test(url) && !/\s/g.test(url);

  return looksValid && !containsComma && !containsSpaceEncoding && !isLocalFile;
}

// checks for period and backslash-- example.com/
export function isValidUrlWithSlash(url) {
  return /\w\.[^\s]+\//.test(url);
}
