<template>
  <div v-if="names.length > 1">
    <div v-for="name in names"
         :key="name.fullOrUserName"
         :alt="name.fullOrUserName"
         :title="name.fullOrUserName"
         class="m-avatar">
      {{ name.initials }}
    </div>

    <div v-if="hiddenNames.length > 0">
      <div :alt="hiddenNames.join(', ')" :title="hiddenNames.join(', ')" class="m-avatar">
        +{{ hiddenNames.length }}
      </div>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import initials from '../../../client/lib/initials';

export default {
  computed: {
    ...mapState({
      connectedUsers: state => state.autosave.connectedUsers
    }),
    maxVisible() {
      // Ensure we always display a maximum of 4 icons, either:
      // A) Up to 4 initials avatars
      // B) 3 initials avatars and one +N avatar
      return this.connectedUsers.length > 4 ? 3 : 4;
    },
    names() {
      const users = this.connectedUsers;
      const maxVisible = this.maxVisible;

      return users.slice(0, maxVisible).map(user => {
        return {
          fullOrUserName: user.fullOrUserName,
          initials: initials(user.fullOrUserName)
        };
      });
    },
    hiddenNames() {
      const users = this.connectedUsers;
      const maxVisible = this.maxVisible;

      // Will be an empty array when there are less than 4 active users
      return users.slice(maxVisible).map(user => user.fullOrUserName);
    }
  }
};
</script>
