const GROUPED_SUBDOCS = ['scorecard'];

// Checks to determine if all objects in the given group are of the same supported type
module.exports = function groupItemsMatch(group) {
  return (
    group.every(item => item.hasOwnProperty('image')) ||
    GROUPED_SUBDOCS.some(type =>
      group.every(item => item.hasOwnProperty('doc') && type === item.doc.type)
    )
  );
};
