var { uniq } = require('lodash');

module.exports = function(contents) {
  const ids = [];

  const body = contents.draft.body;
  if (body) {
    for (let op of body.ops) {
      if (typeof op.insert === 'object' && 'doc' in op.insert) {
        ids.push(op.insert.doc.id);
      }
    }
  }

  const gallery = contents.draft.main_gallery;
  if (gallery) {
    ids.push(gallery.id);
  }

  return uniq(ids);
};
