import Vue from 'vue';
import VueRouter from 'vue-router';
import Dashboard from './views/dashboard.vue';
import Doc from './views/doc.vue';

Vue.use(VueRouter);

const rootUrl = window.location.origin;

const beforeEnter = (to, from, next) => {
  // if /layout/ route is being accessed...
  // check for the lack of presence of ?testing=true in the URL
  if (!to.query.testing) {
    if (from.name) {
      // if the user navigated to here from a previous Vue route, send them back
      next({ name: from.name });
    } else {
      // otherwise send them to the domain root
      window.location = rootUrl;
    }
  } else {
    // ?testing=true IS in the URL, so let the user through
    next();
  }
};

const router = new VueRouter({
  base: '/',
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'dashboard',
      component: Dashboard,
    },
    {
      path: '/pages',
      name: 'pages',
      component: Dashboard,
    },
    {
      path: '/pages/:filter',
      name: 'pages-filtered',
      component: Dashboard,
    },
    {
      path: '/:filter',
      name: 'dashboard-filtered',
      component: Dashboard,
    },
    // wrapper for all doc management routes (currently only layout,
    // but eventually will include compose, finalize, etc)
    {
      path: '/:action(layout)/:uuid',
      name: 'doc',
      component: Doc,
      props: true,
      beforeEnter
    },
  ]
});
export default router;
