<template>
  <div>
    <p v-if="!searches.length" class="m-search-panel__no-searches">
      No saved searches. Try creating a saved search.
    </p>
    <ul class="m-search-panel__saved-searches">
      <editable-list-item
        v-for="search in searches"
        :key="search.searchId"
        :itemName="search.name"
        :onNameClick="() => runSavedSearch(search)"
        :onDeleteClick="() => openDeleteModal(search.searchId)"
        :onSaveClick="(name) => renameSavedSearch(search, name)"
        :active="search.searchId === currentSavedSearch.searchId"
        :disabled="readOnly" />
    </ul>
  </div>
</template>

<script>
import EditableListItem from './editable-list-item.vue';
import { mapGetters, mapState } from 'vuex';

export default {
  components: {
    EditableListItem,
  },
  props: {
    searches: {
      type: Array,
      default: () => ([]),
      required: true,
    },
    goToSearchView: {
      type: Function,
      default: () => {},
      required: true,
    },
    renameSavedSearch: {
      type: Function,
      default: () => {},
      required: true,
    },
    openDeleteModal: {
      type: Function,
      default: () => {},
      required: true,
    },
    runSavedSearch: {
      type: Function,
      default: () => {},
      required: true,
    },
    currentSavedSearch: {
      type: Object,
      default: () => ({}),
      required: true,
    },
  },
  computed: {
    ...mapState(['currentCommunity']),
    ...mapGetters(['readOnly']),
  },
};
</script>
