<template>
  <div class="m-nav__inner m-nav--editor__inner">

    <community-lockup :communityId="communityId" />

    <div class="m-nav__right">
      <div class="m-nav--editor__subnav">
        <div class="m-nav__top">
          <h1 class="m-nav__title">{{ hed }}</h1>

          <help-toggle />

        </div>
        <div class="m-nav__bottom">
          <nav>
            <a v-for="link in navLinks"
               :key="link.label"
               :href="`/${link.label.toLowerCase()}/${currentDoc.uuid}`"
               :class="pageActive(link.name, link.label.toLowerCase())"
               type="button">
              {{ link.label }}
            </a>
          </nav>
          <div class="m-nav__statuses">
            <div class="m-nav__user-status">
              <user-status />
            </div>

            <status-icon wrapperClass="m-nav__story-status" />

            <span :class="`m-status m-status--${currentDoc.status.toLowerCase()}`">{{ formattedDocStatus }}</span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';
import sentenceCase from '../../../shared/sentence_case';
import docStatuses from '../../../client/config/doc_statuses';
import { toPlaintext } from 'convert-rich-text';
import CommunityLockup from './community-lockup.vue';
import UserStatus from './user-status.vue';
import StatusIcon from './status-icon.vue';
import HelpToggle from './help-toggle.vue';

export default {
  components: {
    CommunityLockup,
    UserStatus,
    StatusIcon,
    HelpToggle
  },
  props: {
    navLinks: {
      type: Array,
      required: true
    },
    pageActive: {
      type: Function,
      required: true
    }
  },
  computed: {
    ...mapGetters(['currentDoc']),
    communityId() {
      return this.currentDoc && this.currentDoc.community_id;
    },
    hed() {
      const hed = this.currentDoc.head.contents.draft.hed;
      return toPlaintext(hed);
    },
    formattedDocStatus() {
      const status = this.currentDoc.status;
      return sentenceCase(docStatuses[status]);
    }
  }
};
</script>
