<template>
  <div class="m-dashboard__modal">
    <res-input class="m-dashboard__modal__input">
      <label>Share link</label>
      <p v-if="notPublished">
        Before the story is published, this link will redirect to the homepage. Once it's published, it will redirect to the public URL.
      </p>
      <input :value="shortlink" readonly type="text" />
    </res-input>
    <div class="m-dashboard__modal__actions">
      <res-button design="secondary" @click="copyShortlink">
        <button>Copy link</button>
      </res-button>
      <span v-if="copied">
        <res-icon class="m-dashboard__modal__icon" name="check-circle" />
        Copied!
      </span>
      <res-button :res-modal-close="modalId">
        <res-icon class="m-dashboard__modal__icon" name="x" />
        <button>Cancel</button>
      </res-button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import isPublished from '../../../client/lib/is_published';

export default {
  props: {
    childProps: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {
      copied: false,
    };
  },
  computed: {
    ...mapState(['network', 'modalId']),
    shortlink() {
      const domain = this.network.schemaDomain;
      const entryId = this.doc.entry_id;
      return `https://${domain}/e/${entryId}`;
    },
    notPublished() {
      return !isPublished(this.doc.status);
    },
    doc() {
      return this.childProps.doc;
    }
  },
  watch: {
    doc(newDoc, prevDoc) {
      if (newDoc !== prevDoc) {
        this.copied = false;
      }
    }
  },
  methods: {
    async copyShortlink() {
      await navigator.clipboard.writeText(this.shortlink);
      this.copied = true;
    }
  }
};
</script>
