var OT = require('../../shared/ottypes/anthem0');
var sentryActions = require('../actions/sentry');

// This library warps the three OT methods we use in the client in try/catch
// guards. We assume all OT errors are "fatal" in that they probably break
// autosave, so we log & emit an error prompting to refresh. Still, we pass
// the error back up the stack as well to be handled where the OT method was
// called (e.g. autosave)

export default {
  compose(op1, op2) {
    try {
      return OT.compose(op1, op2);
    } catch (error) {
      sentryActions.reportError(error, false);
      throw error;
    }
  },
  apply(snapshot, op) {
    try {
      return OT.apply(snapshot, op);
    } catch (error) {
      sentryActions.reportError(error, false);
      throw error;
    }
  },
  transform(op1, op2, side) {
    try {
      return OT.transform(op1, op2, side);
    } catch (error) {
      sentryActions.reportError(error, false);
      throw error;
    }
  }
};
