<template>
  <div ref="autocompleteInputWrap" class="m-autocomplete">
    <res-input>
      <label :for="trimString(fieldLabel)">{{ fieldLabel }}</label>
      <input
        ref="autocompleteInput"
        v-model.trim="currentInput"
        :placeholder="inputPlaceholder || fieldLabel"
        :id="trimString(fieldLabel)"
        type="text"
        autocomplete="off"
      />
      <res-icon v-if="searching" name="more" size="small" />
    </res-input>
    <ul v-show="mappedResults.length" ref="autocompleteInputResults" role="listbox" class="m-autocomplete__input__menu">
      <!-- use a title attribute here because of long contributor groups' names that will likely be cutoff in the general UI -->
      <li v-for="(option, i) in mappedResults"
          :key="`${i}-${option.id}`"
          :id="option.id"
          :disabled="option.id === 0"
          :class="focusIndex === i ? 'm-autocomplete__result--focused' : ''"
          :title="option.name"
          :tabindex="focusIndex === i ? 0 : -1"
          role="option"
          @click="addOpt(option)"
      >
        {{ option.name }}{{ option.type === 'SUPER_GROUP' ? ' (Super group)' : '' }}
      </li>
    </ul>
    <div v-if="currentSelections.length" class="m-chip__container">
      <chip v-for="(selection, index) in currentSelections"
            :key="`${index}-${trimString(selection.name)}`"
            :chipId="selection.id"
            :chipLabel="selection.name"
            @removeSelection="removeSelection"
      />
    </div>
  </div>
</template>

<script>
import gql from 'graphql-tag';
import { SearchContributors } from '../queries/searchContributors.graphql';
import { SearchGroups } from '../queries/searchGroups.graphql';
import { mapState } from 'vuex';
import Chip from './chip.vue';

export default {
  components: {
    Chip
  },
  props: {
    fieldLabel: {
      type: String,
      required: true
    },
    inputPlaceholder: {
      type: String,
      default: null
    },
    queryType: {
      type: String,
      required: true
    },
    value: {
      type: Array,
      default: () => []
    },
    minSearchTextLength: {
      type: Number,
      default: 3
    }
  },
  data() {
    return {
      mappedResults: [],
      currentInput: '',
      searchInput: '',
      currentSelections: [],
      searching: false,
      focusIndex: -1
    };
  },
  computed: {
    ...mapState(['currentCommunity']),
    communityId() {
      return this.currentCommunity.id;
    }
  },
  apollo: {
    currentOptions: {
      query() {
        switch (this.queryType) {
        case 'contributors':
          return gql`${SearchContributors}`;
          // leaving the switch case here for historical purpose.
          // When other search endpoints are ready to be used, this logic is available
        case 'groups':
          return gql`${SearchGroups}`;
        case 'packages':
          return;
        }
      },
      variables () {
        switch (this.queryType) {
        case 'contributors':
        case 'groups':
          return {
            text: this.searchInput,
            communityId: this.communityId
          };
          // leaving the switch case here for historical purpose.
          // When other search endpoints are ready to be used, this logic is available
        case 'packages':
          return;
        }
      },
      skip() {
        return !this.searchInput;
      },
      debounce: 500,
    }
  },
  watch: {
    currentOptions() {
      if (this.currentOptions.results) {
        this.mapResults();
      }
    },
    currentInput() {
      if (!this.currentInput) {
        this.searching = false;
        this.clearInput();
      } else if (this.currentInput.length < this.minSearchTextLength) {
        if (this.currentInput === 'me' || this.currentInput === 'Me') {
          this.mappedResults = [{ id: 'me', name: 'me' }];
        }
        this.searching = false;
      } else {
        this.searching = true;
        this.searchInput = this.currentInput;
      }
    },
    mappedResults() {
      if (this.mappedResults.length) {
        this.searching = false;
        setTimeout(this.addClickListeners(), 200);
      } else {
        this.removeClickListeners();
      }
    },
    value() {
      this.currentSelections = this.value.map(this.formatOption);
    }
  },
  beforeDestroy() {
    this.removeClickListeners();
  },
  methods: {
    // because of 2 way data-binding, we must call the update when
    // a user interacts; if we use a watcher we get an infinite loop
    updateValue() {
      var contribs = this.currentSelections.map(selection => {
        return selection.name;
      });
      this.$emit('input', contribs);
    },
    trimString(stringItem) {
      // remove special characters & whitespace from different field strings to use as for/id attributes
      const trimmedString = stringItem.replace(/[^\w\s]/gi, '').replace(/\s+/g, '');
      return trimmedString.toLowerCase();
    },
    clearInput() {
      this.currentInput = '';
      this.currentOptions = {};
      this.searchInput = '';
      this.mappedResults = [];
      this.focusIndex = -1;
      this.removeClickListeners();
    },
    addClickListeners() {
      window.addEventListener('click', this.onClickOut);
      this.$refs.autocompleteInputWrap.addEventListener('keydown', this.handleKeydown);
    },
    removeClickListeners() {
      window.removeEventListener('click', this.onClickOut);
      this.$refs.autocompleteInputWrap.removeEventListener('keydown', this.handleKeydown);
    },
    addOpt(option) {
      if (option.id === 0) return;

      const newSelection = !this.currentSelections.find(item => item.id === option.id);

      if (newSelection) {
        this.currentSelections.push(option);
        this.updateValue();
      }

      this.clearInput();
    },
    removeSelection(id) {
      let arr = this.currentSelections;
      let i;
      for (i = 0; i < arr.length; i++) {
        if (arr[i].id === id) {
          arr.splice(i, 1);
          i--;
        }
      }
      this.updateValue();
    },
    onClickOut(evt) {
      if (!this.$refs.autocompleteInput.contains(evt.target) && !this.$refs.autocompleteInputResults.contains(evt.target)) {
        this.searching = false;
        this.clearInput();
      }
    },
    mapResults() {
      if (this.currentOptions.results && this.currentOptions.results.length) {
        this.mappedResults = this.currentOptions.results.map(this.formatOption);
      } else {
        this.mappedResults = [{ id: 0, name: 'No results found' }];
      }
    },
    formatOption(item) {
      let option;

      if (typeof item === 'string') {
        // If the item is populated from the URL
        option = { name: item.replaceAll('"', '') };
      } else {
        // If the item is populated from autocomplete search results
        option = { ...item };
      }

      option.id = this.trimString(option.name);
      return option;
    },
    handleKeydown(evt) {
      const halt = () => evt.preventDefault();
      switch (evt.which) {
      case 9: // TAB
        halt();
        this.searching = false;
        this.clearInput();
        break;
      case 13: // ENTER
        halt();
        this.selectItemByIndex(this.focusIndex);
        break;
      case 27: // ESC
        halt();
        this.searching = false;
        this.clearInput();
        break;
      case 38: // UP
        halt();
        this.setFocus(this.focusIndex - 1);
        break;
      case 40: // DOWN
        halt();
        this.setFocus(this.focusIndex + 1);
        break;
      }
    },
    selectItemByIndex(index) {
      // if the user presses ENTER while the search input is focused, we expect the focusIndex value to be -1,
      // so reset the index here  to `0` such that the first option in the list is selected on ENTER
      if (index === -1) index = 0;
      const option = this.mappedResults[index];
      if (option) {
        this.addOpt(option);
        this.$refs.autocompleteInput.focus();
      }
    },
    setFocus(index) {
      // set focus on the appropriate list item as the list is traversed via keydown
      if (this.mappedResults.length) {
        this.focusIndex = Math.max(0, Math.min(index, this.mappedResults.length-1));
        const listItems = Array.from(this.$refs.autocompleteInputResults.children);
        listItems[this.focusIndex].focus();
      } else {
        this.focusIndex = -1;
      }
    }
  }
};
</script>
