<template>
  <div>
    <div class="m-community-select__menu-trigger" @click.prevent="toggleMenu">
      <div :class="{ 'is-open': menuOpen && hasMenu, 'm-community-select__lockup--openable': hasMenu }" class="m-community-select__lockup">
        <div class="m-community-select__avatar">
          <img :src="community.faviconUrl || defaultLogoUrl" class="m-community-select__icon" />
        </div>
        <span class="m-community-select__name">{{ community.name }}</span>
      </div>
    </div>
    <community-menu
      v-if="networks && networks.length"
      ref="communityMenu"
      :allowedRoles="allowedRoles"
      :activeMemberships="activeMemberships"
      :defaultLogoUrl="defaultLogoUrl"
      :networks="networks"
      :menuOpen="menuOpen"
      :class="{ 'is-active': menuOpen }"
      class="m-community-select__menu"
      @closeMenu="closeMenu"
    />
  </div>
</template>

<script>
import { mapState } from 'vuex';
import groupSortNetworkMemberships from '../../group_sort_network_memberships';
import CommunityMenu from './components/community-menu.vue';

export default {
  components: {
    CommunityMenu
  },
  props: {
    value: {
      type: Number,
    },
    community: {
      type: Object,
      required: true
    },
    user: {
      type: Object,
      required: true
    },
    allowedRoles: {
      type: Object,
      required: true
    },
    activeMemberships: {
      type: Array,
      default: () => ([])
    },
  },
  data() {
    return {
      menuOpen: false,
    };
  },
  computed: {
    ...mapState(['defaultLogoUrl']),
    networks() {
      return groupSortNetworkMemberships(this.activeMemberships);
    },
    hasMenu() {
      // only allow community menu to open when user is a member
      // of > 1 network OR > 1 communities within a network
      return this.networks.length > 1 || this.networks.some(n => n.communities.length > 1);
    },
  },
  watch: {
    // External value changes internal selection state.
    value() {
      if (this.$refs.communityMenu) this.$refs.communityMenu.selectCommunity(this.value);
    }
  },
  methods: {
    closeMenu() {
      this.menuOpen = false;
    },
    toggleMenu() {
      this.menuOpen = !this.menuOpen;
    }
  }
};
</script>
