/**
 * Removes `http[s]://` from a url before using to query for a doc. Elasticsearch
 * indices of docs have a public_url that does not contain a protocol therefore
 * we can't query those docs with the URL protocol
 * @param {String} url a valid URL
 * @return {String} The URL without the protocol
 */
export default function stripProtocol(url) {
  return url.replace(/http[s]?:\/\//, '');
}
