import * as cmd from '../commands';
import messages from '../../../client/config/messages.js';
import io from 'socket.io-client';

export default function anthemSocket() {
  let unloading = false;
  let user;
  let promptReload;
  let room;

  return function(store) {

    function createSocket() {
      const socketUrl = `${(store.state.ssl ? 'wss' : 'ws')}://${window.location.host}/`;
      const socket = io(socketUrl, {
        transports: ['websocket'],
        reconnection: true,
        reconnectionAttempts: store.state.sockets.reconnectAttempts
      });
      store.commit(cmd.ANTHEM_SOCKET_INIT, socket);
    }

    function connectListeners() {

      store.state.anthemSocket.on('flash-message', params => {
        if (!params.type) params.type = 'neutral';
        if (params.type === 'cautionary') params.type = 'neutral';
        if (params.type === 'primary') params.type = 'positive';
        store.commit('messageBanner/PUT_NOTICE', {
          type: params.type,
          message: params.message,
          persistent: params.forceRefresh,
          icon: params.icon
        });

        if (params.forceRefresh) {
          // Disconnect socket when we want to force a refresh.
          store.state.anthemSocket.disconnect();
        }
      });

      // Socket disconnect/reonnect bindings...
      store.state.anthemSocket.on('connect', () => {
        // 'connect' is a socket.io reserved event
        // anytime a successful connection emits, delete any error banners in the message store
        clearErrorBanners();
        promptReload = startTimer();
        if (store.state.autosave.currentDocId) {
          room = store.state.autosave.currentDocId;
          user = store.state.currentUser;
          initAnthemServerPresence(room, user);
        }
      });

      store.state.anthemSocket.on('reconnect', () => {
        // reconnection has been established, reset stale-tab timer.
        resetTimer();
        clearErrorBanners();
        // reconnection has been established, display the "Reconnected." banner, which has a 5-second timeout to remove itself.
        displayBanner('reconnected');
        if (store.state.autosave.currentDocId) {
          room = store.state.autosave.currentDocId;
          user = store.state.currentUser;
          store.state.anthemSocket.emit('presence.join', room, true, user, addUser);
        }
      });

      store.state.anthemSocket.on('connect_error', (error) => {
        // 'connect_error' is a socket.io reserved event
        handleDisconnect(error);
      });

      store.state.anthemSocket.on('disconnect', (reason) => {
        // 'disconnect' is a socket.io reserved event
        if (store.state.autosave.currentDocId) {
          room = store.state.autosave.currentDocId;
          store.state.anthemSocket.emit('presence.leave', room);
        }
        handleDisconnect(reason);
      });

      store.state.anthemSocket.on('reconnect_attempt', () => {
        // This event happens when socket is trying to reconnect.
        // The socket will automatically make a limited number of attempts to reconnect,
        // but we should only show one banner.
        displayOnce('reconnecting', 'reconnecting-banner');
      });

      store.state.anthemSocket.on('reconnect_failed', () => {
        // after the limited number of attempts to reconnect have failed,
        // the socket will recieve a failure event.
        handleDisconnect('reconnect_failed');
      });

      store.state.anthemSocket.on('autosave.local-update', resetTimer);

      store.state.anthemSocket.on('autosave.remote-update', resetTimer);

    }

    function initAnthemServerPresence(room, user) {
      // emit the presence of the current user
      store.state.anthemSocket.emit('presence.join', room, true, user, addUser);

      // add user to other socket users' states
      store.state.anthemSocket.on(`presence.${room}.greet`, addUser);

      store.state.anthemSocket.on(`presence.${room}.left`, (oldUser) => {
        store.commit(cmd.ANTHEM_SOCKET_REMOVE_USER, { oldUser: oldUser });
      });

      // listen for the joining of other users in the same room
      store.state.anthemSocket.on(`presence.${room}.joined`, (newUser) => {
        addUser(newUser);
        store.state.anthemSocket.emit(`presence.${room}.greet`, newUser.socketId);
      });
    }

    function addUser(newUser) {
      if (!store.state.autosave.connectedUsers.find(user => user.id === newUser.id)) {
        store.commit(cmd.ANTHEM_SOCKET_ADD_USER, { newUser: newUser });
      }
    }

    function startTimer() {
      // When a tab becomes stale, trigger a socket disconnect
      return setTimeout(() => {
        store.state.anthemSocket.emit('disconnect', 'stale-tab');
      }, store.state.sockets.inactivePeriod);
    }

    function resetTimer() {
      clearTimeout(promptReload);
      promptReload = startTimer();
    }

    function bindActiveTabEventListeners() {
      window.addEventListener('keypress', resetTimer, false);
      window.addEventListener('click', resetTimer, false);
      window.addEventListener('beforeunload', () => {
        // prevent firefox from showing error banner messages when navigating away
        unloading = true;
        removeActiveTabEventListeners();
      });
    }

    function removeActiveTabEventListeners() {
      window.removeEventListener('keypress', resetTimer);
      window.removeEventListener('click', resetTimer);
    }

    function displayOnce(bannerType, bannerId) {
      const bannerFound = store.state.messageBanner.notifications.find(banner => banner.id === bannerId);
      if (!bannerFound) {
        displayBanner(bannerType);
      } else { return; }
    }

    function displayBanner(type) {
      switch(type) {

      case 'reload-stale-tab':
        store.commit('messageBanner/PUT_NOTICE', {
          id: 'stale-tab-banner',
          type: 'negative',
          persistent: true,
          message: messages.RELOAD_STALE_TAB
        });
        break;

      case 'reconnecting':
        store.commit('messageBanner/PUT_NOTICE', {
          id: 'reconnecting-banner',
          type: 'neutral',
          persistent: true,
          message: messages.RECONNECTING
        });
        break;

      case 'reconnected':
        store.commit('messageBanner/PUT_NOTICE', {
          type: 'positive',
          persistent: false,
          timeout: true,
          message: 'Reconnected'
        });
        break;

      case 'reconnect-error':
        store.commit(cmd.SOCKET_ERROR, true);
        store.commit('messageBanner/PUT_NOTICE', {
          id: 'reconnect-error-banner',
          type: 'negative',
          persistent: true,
          message: messages.RECONNECT_ERROR
        });
        break;

      case 'unexpected-error':
        store.commit(cmd.SOCKET_ERROR, true);
        store.commit('messageBanner/PUT_NOTICE', {
          id: 'error-banner',
          type: 'negative',
          persistent: true,
          message: messages.UNEXPECTED
        });
        break;

      case 'request-reload':
        store.commit('messageBanner/PUT_NOTICE', {
          id: 'reload-banner',
          type: 'neutral',
          persistent: true,
          message: messages.RELOAD_REQUIRED
        });
        break;
      }
    }

    function clearErrorBanners() {
      store.commit('messageBanner/DELETE_NOTICE', 'error-banner');
      store.commit('messageBanner/DELETE_NOTICE', 'reconnecting-banner');
      store.commit('messageBanner/DELETE_NOTICE', 'reconnect-error-banner');
    }

    function handleDisconnect(reason=null) {
      switch (reason) {

      case 'io client disconnect':
        // manually disconnected, editor should go into read-only mode
        store.commit(cmd.SOCKET_ERROR, true);
        // assume maintenance mode or other deploy has caused this,
        // if we don't already have a persistent message showing, show this one:
        if (!this.messageBanner.notifications.some(m => m.persistent)) {
          this.displayOnce('request-reload', 'reload-banner');
        }
        break;

      case 'read-only':
        // put the editor into read-only state
        store.commit(cmd.SOCKET_ERROR, true);
        break;

      case 'stale-tab':
        // stale-tab timer has hit its limit, ask the user to manually reload.
        displayOnce('reload-stale-tab', 'stale-tab-banner');
        break;

      case 'reconnect_failed':
        // put the editor into read-only state
        store.commit(cmd.SOCKET_ERROR, true);
        // remove the 'Reconnecting...' banner
        store.commit('messageBanner/DELETE_NOTICE', 'reconnecting-banner');
        // display the banner that tells the user to manually reload (but only show it once).
        displayOnce('reconnect-error', 'error-banner');
        break;

      default:
        // socket connection is lost, ask the user to manually reload.
        // disconnect reason could be 'transport close', 'ping timeout', or something else.
        // don't show this banner if the websocket disconnects because the user is navigating away or manually reloading the page (as seen in Firefox)
        if (!unloading) displayOnce('unexpected-error', 'error-banner');
      }
    }

    const unsubscribe = store.subscribe((mutation) => {
      // run these functions when SET_CONTEXT runs,
      // but SET_CONTEXT runs three times per interface load,
      // so `unsubscribe` from SET_CONTEXT after all the connection functions run.
      if (mutation.type === cmd.SET_CONTEXT && store.state.currentUser.fullOrUserName) {
        // make sure the SET_CONTEXT mutation that ran was the * user * context
        createSocket();
        connectListeners();
        bindActiveTabEventListeners();
        unsubscribe();
      }
    });
  };
}
