<template>
  <div class="m-search-panel__search">
    <form class="m-search-panel__form" @submit.prevent="onSearchSubmit">
      <res-input class="m-search-panel__field" @keyup.enter.prevent="onSearchSubmit">
        <input
          ref="keywordInput"
          v-model="keyword"
          type="search"
          placeholder="Story title or keywords" />
        <res-icon
          slot="inner-left"
          name="search" />
      </res-input>

      <autocomplete-input
        v-if="!$route.name.includes('pages')"
        v-model="groupList"
        :minSearchTextLength="2"
        queryType="groups"
        class="m-search-panel__form__field"
        fieldLabel="Group"
        inputPlaceholder="Group name"
      />

      <div v-if="(currentPage !== 'archive' && currentPage !== 'trash')" class="m-search-panel__field-wrapper">
        <p class="m-search-panel__field-label">Status</p>
        <div v-if="currentPage === 'pages'" class="m-search-panel__checkbox-group">
          <res-checkbox v-for="status in pageStatuses" :key="status.name + checkboxKey" >
            <input v-model="status.checked" :name="status.name" type="checkbox" />
            <label>{{ status.label }}</label>
          </res-checkbox>
        </div>
        <div v-else class="m-search-panel__checkbox-group">
          <res-checkbox v-for="status in statuses" :key="status.name + checkboxKey" >
            <input v-model="status.checked" :name="status.name" type="checkbox" />
            <label>{{ status.label }}</label>
          </res-checkbox>
        </div>
      </div>

      <div class="m-search-panel__field-wrapper">
        <p class="m-search-panel__field-label">Date</p>
        <div class="m-search-panel__date-operator">
          <span>Show stories</span>
          <res-radio>
            <input ref="on"
                   v-model="dateOperator"
                   type="radio"
                   name="dateOperator"
                   value="on" />
            <label>on</label>
          </res-radio>
          <res-radio>
            <input ref="before"
                   v-model="dateOperator"
                   type="radio"
                   name="dateOperator"
                   value="before" />
            <label>before</label>
          </res-radio>
          <res-radio>
            <input ref="after"
                   v-model="dateOperator"
                   type="radio"
                   name="dateOperator"
                   value="after" />
            <label>after</label>
          </res-radio>
        </div>
      </div>

      <res-datepicker>
        <label />
        <input v-model="dateString" type="text" placeholder="Date last updated" />
      </res-datepicker>

      <autocomplete-input
        ref="autocompleteContributors"
        v-model="contributorList"
        queryType="contributors"
        class="m-search-panel__form__field"
        fieldLabel="Byline"
        inputPlaceholder="Author byline"
      />

      <div v-if="currentPage !== 'pages'" class="m-search-panel__field-wrapper">
        <p class="m-search-panel__field-label">Story type</p>
        <div class="m-search-panel__checkbox-group">
          <template v-for="storyType in storyTypes">
            <res-checkbox v-if="typeAccess.includes(storyType.name)" :key="storyType.name + checkboxKey">
              <input v-model="storyType.checked" :name="storyType.name" type="checkbox" />
              <label>{{ storyType.label }}</label>
            </res-checkbox>
          </template>
        </div>
      </div>
    </form>

    <footer v-show="!isCollapsed">
      <res-button
        v-if="showSaveButton"
        :disabled="disableSaveButton"
        :res-modal-open="disableSaveButton ? null : modalId"
        design="secondary"
        @click.prevent="() => openCreateModal($refs.saveButton, keyword)">
        <button ref="saveButton">Save search</button>
      </res-button>
      <res-button :disabled="disableSearchButton" design="primary" @click.prevent="onSearchSubmit">
        <res-icon name="search" /><button>Search</button>
      </res-button>
    </footer>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import AutocompleteInput from '../autocomplete-input.vue';
import { isEqual, isEmpty } from 'lodash';

export default {
  components: {
    AutocompleteInput
  },
  props: {
    isCollapsed : {
      type: Boolean,
      default: false,
      required: true
    },
    isSearching : {
      type: Boolean,
      default: false,
      required: true
    },
    updateQuery: {
      type: Function,
      default: () => {},
      required: true,
    },
    closeSearchPanel: {
      type: Function,
      default: () => {},
      required: true,
    },
    defaultQuery: {
      type: Object,
      default: () => ({}),
      required: true,
    },
    formattedQuery: {
      type: Array,
      default: () => []
    },
    openCreateModal: {
      type: Function,
      default: () => {},
      required: true,
    },
    setSavedSearchName: {
      type: Function,
      default: () => {},
      required: true,
    },
    currentSavedSearch: {
      type: Object,
      default: () => ({}),
      required: true,
    },
    showSaveButton: {
      type: Boolean,
      default: true,
    },
  },
  data() {
    return {
      checkboxKey: 1,
      keyword: '',
      groupList: [],
      statuses: [
        { name: 'draft', label: 'Draft', checked: false },
        { name: 'submitted', label: 'Submitted', checked: false },
        { name: 'approved', label: 'Approved', checked: false },
        { name: 'embargoed', label: 'Embargoed', checked: false },
        { name: 'scheduled', label: 'Scheduled', checked: false },
        { name: 'published', label: 'Published', checked: false },
        { name: 'updated', label: 'Unpublished changes', checked: false },
      ],
      pageStatuses: [
        { name: 'draft', label: 'Draft', checked: false },
        { name: 'scheduled', label: 'Scheduled', checked: false },
        { name: 'published', label: 'Published', checked: false },
        { name: 'updated', label: 'Unpublished changes', checked: false },
      ],
      dateOperator: 'on',
      dateString: '',
      storyTypes: [
        { name: 'story', label: 'Story', checked: false },
        { name: 'stream', label: 'Stream', checked: false },
        { name: 'link', label: 'Link', checked: false },
        { name: 'feature', label: 'Feature', checked: false },
        { name: 'package', label: 'Package', checked: false },
        { name: 'mapstack', label: 'Mapstack', checked: false },
        { name: 'customstory', label: 'Custom story', checked: false }
      ],
      // Field list for easy access in computed and methods
      fields: [
        'keyword',
        'group',
        'status',
        'date',
        'contributors',
        'type'
      ],
      contributorList: [],
      lastSearchedFormValues: {},
    };
  },
  computed: {
    ...mapState([
      'entitlements',
      'isMobile',
      'modalId',
      'currentCommunity'
    ]),
    ...mapGetters([
      'readOnly'
    ]),
    emptyForm() {
      return this.fields.every(value => !this[value]);
    },
    group() {
      const groups = this.groupList.map(groupName => `group:"${groupName}"`);
      return groups.join(' ');
    },
    status() {
      const statuses = this.currentPage === 'pages' ? this.pageStatuses : this.statuses;
      return statuses.reduce((acc, el) => {
        el.checked && acc.push(`status:${el.name}`);
        return acc;
      }, []).join(' ');
    },
    type() {
      return this.storyTypes.reduce((acc, el) => {
        el.checked && acc.push(`type:${el.name}`);
        return acc;
      }, []).join(' ');
    },
    date() {
      if (this.dateString) {
        const formattedDate = new Date(this.dateString).toLocaleDateString('en-US');
        return `${this.dateOperator}:${formattedDate}`;
      }
      return null;
    },
    typeAccess() {
      const opts = ['story', 'stream', 'link'];
      if (!this.currentCommunity.headlessModeEnabled) opts.push('feature');
      if (this.entitlements) {
        if (this.entitlements.includes('use_packages')) opts.push('package');
        if (this.entitlements.includes('use_mapstacks')) opts.push('mapstack');
        if (this.entitlements.includes('use_customstories')) opts.push('customstory');
      }
      return opts;
    },
    contributors() {
      if (this.contributorList.length < 1) return '';
      let names = [];
      this.contributorList.forEach((contrib) => {
        names.push(`by:"${contrib}"`);
      });
      return names.join(' ');
    },
    currentPage() {
      return this.$route.path.slice(1);
    },
    currentFormValues() {
      return this.getFormValues();
    },
    // Whether form state has changed from the last time a search was executed
    formStateChanged() {
      return !isEqual(this.lastSearchedFormValues, this.currentFormValues);
    },
    disableSearchButton() {
      return this.emptyForm || this.isSearching || !this.formStateChanged;
    },
    disableSaveButton() {
      return this.emptyForm || this.isSearching || !this.allowSaving || this.formStateChanged || this.readOnly;
    },
    allowSaving() {
      return !Object.keys(this.lastSearchedFormValues).every(key =>
        isEmpty(this.lastSearchedFormValues[key]));
    }
  },
  watch: {
    emptyForm() {
      if (this.emptyForm && this.formattedQuery.length > 0) {
        this.updateQuery(this.defaultQuery);
      }
    },
    formattedQuery() {
      this.populateFields(this.formattedQuery);
    },
    isCollapsed() {
      if (!this.isCollapsed) {
        this.$nextTick(() => this.$refs.keywordInput.focus());
      }
    },
    formStateChanged(changed) {
      if (changed && this.currentSavedSearch.searchId) {
        this.setSavedSearchName('');
      }
    }
  },
  methods: {
    onSearchSubmit() {
      // Workaround for the fact that the child button of the res-button
      // does not inherit the disabled attribute
      if (this.disableSearchButton) {
        return;
      }
      const query = {
        ...this.$route.query,
        q: this.buildSearchQuery(),
        order: 'score',
        page: 1,
      };
      this.updateQuery(query);
      if (this.isMobile) {
        this.closeSearchPanel();
      }
    },
    buildSearchQuery() {
      return this.fields.reduce((q, field) => {
        this[field] && q.push(this[field]);
        return q;
      }, []).join(' ');
    },
    clearSearch() {
      this.clearSearchFields();
      if (this.isMobile) {
        this.closeSearchPanel();
      }
      this.updateQuery(this.defaultQuery);
      this.updateLastSearchedFormValues();
    },
    clearSearchFields() {
      const pageStatuses = this.pageStatuses.map(el => ({ name: el.name, label: el.label, checked: false }));
      const statuses = this.statuses.map(el => ({ name: el.name, label: el.label, checked: false }));
      const storyTypes = this.storyTypes.map(el => ({ name: el.name, label: el.label, checked: false }));
      this.pageStatuses = pageStatuses;
      this.statuses = statuses;
      this.storyTypes = storyTypes;
      this.checkboxKey++;
      this.keyword = '';
      this.groupList = [];
      this.updateDateOperator('on');
      this.dateString = '';
      this.contributorList = [];
    },
    populateFields(query) {
      const searchBoxQuery = [];
      this.clearSearchFields();
      query.forEach(item => {
        if (item.type === 'tokens' || item.type === 'phrase') {
          searchBoxQuery.push(item.value);
        } else { // 'keywordQueries'
          const [field, value] = item.value.split(':');
          switch (field) {
          case 'status':
            if (this.currentPage === 'pages') {
              this.pageStatuses.forEach((el, i) => {
                if (el.name === value) this.$set(this.pageStatuses[i], 'checked', true);
              });
            } else {
              this.statuses.forEach((el, i) => {
                if (el.name === value) this.$set(this.statuses[i], 'checked', true);
              });
            }
            this.checkboxKey++;
            break;
          case 'type':
            this.storyTypes.forEach((el, i) => {
              if (el.name === value) this.$set(this.storyTypes[i], 'checked', true);
            });
            this.checkboxKey++;
            break;
          case 'on':
          case 'before':
          case 'after':
            this.updateDateOperator(field);
            this.dateString = value;
            break;
          case 'by':
            this.contributorList.push(value.replace(/"/gi, ''));
            break;
          case 'group':
            this.groupList.push(value.replace(/"/gi, ''));
            break;
          default:
            // search keywords that have no UI (yet)
            searchBoxQuery.push(item.value);
          }
        }
      });
      this.keyword = searchBoxQuery.join(' ');
      this.updateLastSearchedFormValues();
    },
    // Vue doesn't trigger DOM change events on inputs when updated via data :-)
    updateDateOperator(ref) {
      this.dateOperator = ref;
      this.$refs[ref].dispatchEvent(new Event('change'));
    },
    getFormValues() {
      return this.fields.reduce((acc, el) => {
        acc[el] = this[el];
        return acc;
      }, {});
    },
    updateLastSearchedFormValues() {
      this.lastSearchedFormValues = this.getFormValues();
    },
  }
};
</script>
