function secureRandomHex(size=32) {
  const crypto = window.crypto || window.msCrypto;
  let str = '';

  if (crypto) {
    let arr = new Uint8Array(Math.ceil(size / 2));
    crypto.getRandomValues(arr);
    str = [].map.call(arr, (b) => ('0'+b.toString(16)).slice(-2)).join('');
  } else {
    for (let i=Math.ceil(size / 11); i > 0; i-=1) {
      str += Math.random().toString(36).substr(2);
    }
  }
  return str.substr(0, size);
}

export default {
  namespaced: true,
  state: {
    notifications: []
  },
  mutations: {
    DELETE_NOTICE(state, payload) {
      state.notifications = state.notifications.filter(notice => notice.id !== payload);
    },
    PUT_NOTICE(state, notices ) {
      if (!Array.isArray(notices)) notices = [notices];
      notices.forEach(notice => {
        if (notice && notice.message) {
          state.notifications.push({
            id: notice.id || secureRandomHex(8),
            // remove any graphQL message headers added by apollo...
            message: notice.message.replace(/GraphQL error:\s*/g, ''),
            type: (notice.graphQLErrors ? 'error' : notice.type) || 'neutral',
            timeout: notice.timeout || null,
            persistent: notice.persistent || false,
            icon: notice.icon || null
          });
        }
      });
    }
  }
};
