var { defaults } = require('lodash');
var hash = require('../../shared/content-hash');
var messages = require('../config/messages');

const DEFAULTS = {
  dismissible: true,
  type: 'negative'
};

export function showMessage(data) {
  var id = hash(data.message);
  if (findMessageIndex(id) === -1 && validatePersistence(data)) {
    data.id = id;
    data = defaults(data, DEFAULTS);
    app.state.select(['messages']).push(data);
  }

  return id;
}

function validatePersistence(data) {
  // Non-persistent messages don't already have a `id` attr, we should display
  if (!data.id) {
    return true;
  }

  // This is a persistent message that the user has seen before, don't display
  var storageKey = `message-${data.id}`;
  if (window.localStorage.getItem(storageKey) === 'shown') {
    return false;
  }

  // This is a new persistent message, record + display
  window.localStorage.setItem(storageKey, 'shown');
  return true;
}

export function removeMessage(id) {
  var index = findMessageIndex(id);
  app.state.select(['messages']).splice([index, 1]);
}

function findMessageIndex(id) {
  return app.state.get(['messages']).findIndex(message => message.id === id);
}

export function showError(error = messages.UNEXPECTED) {
  var hasError = app.state.get(['messages']).some(message => message.isError);
  if (!hasError) {
    showMessage({
      message: error,
      type: 'negative',
      isError: true
    });
  }
}
