var request = require('./request');
var GraphQLError = require('./errors/graphql_error');

export default async function graphqlRequest(path, body, opts = {}) {
  var { data, errors } = await request(path, {
    method: 'POST',
    body,
    opts: { cache: true, ...opts }
  });

  // Error out if the service returns no data and also includes errors
  if (!data && errors) {
    throw new GraphQLError(errors);
  }

  return data;
}
