<template>
  <div class="m-layout-mode__tools">
    <form-subsection label="Layout">
      <div v-if="entryLayoutOptions.length" class="m-field m-layout-mode__group">
        <label :class="[{ 'm-layout-mode__has-gallery': Boolean(mainGallery) }]">
          Template
          <tooltip v-if="mainGallery" :id="uuid" placement="top">
            When the lead art is a gallery, only the standard template may be used.
          </tooltip>
        </label>
        <autosave-radio
          :value="entryLayout || 'unison_standard'"
          :options="entryLayoutOptions"
          :path="['draft', 'entry_layout']" />
      </div>
      <div v-if="isPackage && packageCoverLeadStory" class="m-field">
        <label class="is-fixed">Lead story</label>
        <autosave-radio
          :value="packageCoverLeadStory.layout"
          :options="packageCoverLeadStoryLayoutOptions"
          :path="['draft', 'package_cover', 0, 'story', 'layout']" />
      </div>
      <div v-if="isPackage && canEditPackageTOC" class="m-field">
        <label class="is-fixed">Table of contents</label>
        <autosave-radio
          :value="packageTOCLayout"
          :options="packageTOCOrientationOptions"
          :path="['draft', 'package_toc_layout']" />
      </div>
    </form-subsection>
  </div>
</template>

<script>
import AutosaveRadio from '../autosave-radio.vue';
import FormSubsection from './form-subsection.vue';
import Tooltip from '../tooltip.vue';
import { get } from 'lodash';
import { mapState, mapGetters } from 'vuex';
import {
  featureLayoutOptions,
  packageCoverLeadStoryLayoutOptions,
  packageTOCOrientationOptions,
  standardLayoutOption,
  impactLayoutOptions,
  videoFeatureLayoutOption
} from '../../config/layoutOptions';

export default {
  components: {
    AutosaveRadio,
    FormSubsection,
    Tooltip,
  },
  data() {
    return {
      packageCoverLeadStoryLayoutOptions,
      packageTOCOrientationOptions,
    };
  },
  computed: {
    ...mapState(['currentCommunity', 'entitlements']),
    ...mapGetters(['docLabel', 'currentDoc', 'contents']),
    entryLayoutOptions() {
      let options = [];

      if (!this.isPackage && !this.isPage) {
        options.push(standardLayoutOption);

        if (this.currentCommunity.featuresEnabled) {
          options = options.concat(featureLayoutOptions);

          if (this.entitlements.includes('use_feature_head_impact_layouts')) {
            options = options.concat(impactLayoutOptions);
          }
          if (this.entitlements.includes('use_feature_video_layout')) {
            options.push(videoFeatureLayoutOption);
          }
        }
      }
      return options;
    },
    isPackage() {
      return this.docLabel === 'package';
    },
    isPage() {
      return this.docLabel === 'page';
    },
    uuid() {
      return this.currentDoc.uuid;
    },
    entryLayout() {
      return this.contents.draft.entry_layout;
    },
    mainGallery() {
      return this.contents.draft.main_gallery;
    },
    packageCoverLeadStory() {
      return get(this.contents, ['draft', 'package_cover', 0, 'story']);
    },
    packageTOCLayout() {
      return this.contents.draft.package_toc_layout;
    },
    canEditPackageTOC() {
      const toc = this.contents.draft.package_toc;
      const tocHidden = this.contents.draft.package_toc_hidden;
      return !tocHidden && toc && toc.length < 7;
    }
  }
};
</script>
