<template>
  <transition name="vue-transition--slide">
    <div v-if="currentOverlay.component" id="overlay" :class="`m-overlay m-overlay--${pageType} m-overlay--orientation-${currentOverlay.orientation}`">
      <section :class="`m-overlay__content m-${componentClass}`">
        <res-button design="default" class="p-modal__close" @click="closeOverlay()">
          <res-icon name="x" />
          <button type="button">Close</button>
        </res-button>
        <component
          :is="currentOverlay.component"
          :childProps="currentOverlay.childProps"
          :orientation="currentOverlay.orientation" />
      </section>
    </div>
  </transition>
</template>

<script>
import { mapState } from 'vuex';
import { SET_OVERLAY } from './../store/commands';
import { getPageType } from '../lib/get_page_type';
import ChorusHelpAnnouncement from './overlays/chorus-help-announcement.vue';
import ChorusHelpContactForm from './overlays/chorus-help-contact-form.vue';

export default {
  components: {
    ChorusHelpAnnouncement,
    ChorusHelpContactForm
  },
  computed: {
    ...mapState(['currentOverlay']),
    componentClass() {
      return this.currentOverlay.component || null;
    },
    pageType() {
      return getPageType(this.$route.name);
    }
  },
  methods: {
    closeOverlay() {
      this.$store.commit(SET_OVERLAY, {
        childProps: null,
        component: null
      });
    }
  }
};
</script>
