var { CurrentUser, UserChorusVideo, UserTwitterHandle, UserName, UserNames, UserNamesTitles } = require('../graphql/queries/users');
var request = require('../lib/request');
var graphqlRequest = require('../lib/graphql_request');
var moment = require('moment-timezone');
var { get: getPath, uniq } = require('lodash');

export async function getCurrentUser() {
  var { currentUser } = await graphqlRequest('/api/tower/graphql', {
    query: CurrentUser,
    variables: { size: 'small' }
  });
  const accessRoles = getPath(app.config.policyRoles, 'app.access');
  // Filter memberships
  currentUser.memberships = currentUser.memberships.filter((membership) => {
    return membership.roles.some((role) => accessRoles[role]);
  });

  // Add some custom fallback logic for when fullOrUserName is falsey
  currentUser.fullOrUserName = currentUser.fullOrUserName || 'Unknown User';

  return currentUser;
}

export async function getChorusVideoAccess(communityId) {
  const { currentUser } = await graphqlRequest('/api/tower/graphql', {
    query: UserChorusVideo,
    variables: { scopeId: communityId }
  });
  return currentUser ? currentUser.canAccessChorusVideo : false;
}

export async function getTwitterUsername(id) {
  const { user } = await graphqlRequest('/api/tower/graphql', {
    query: UserTwitterHandle,
    variables: { id }
  });
  return user.twitterUsername;
}

export async function getUser(id) {
  var { user } = await graphqlRequest('/api/tower/graphql', {
    query: UserName,
    variables: { id }
  });
  return user;
}

export async function getUsers(ids) {
  if (ids.length === 0) {
    return [];
  }
  var { users } = await graphqlRequest('/api/tower/graphql', {
    query: UserNames,
    variables: { ids: uniq(ids) }
  });
  return users;
}

export async function getUsersWithTitles(ids, communityId) {
  if (ids.length === 0) {
    return [];
  }
  var { users } = await graphqlRequest('/api/tower/graphql', {
    query: UserNamesTitles,
    variables: { ids: uniq(ids), communityId }
  });
  return users;
}

export function updateAnthemUser(params) {
  return request('/api/users/currentUser', { method: 'PUT', body: params });
}

export function getTimezone() {
  return app.state.get('user', 'timeZone') || app.state.get('community', 'timeZone');
}

export function acknowledgeAnnouncements() {
  var lastSeen = moment(app.state.get('user', 'lastAnnouncementSeenAt')).utc();
  var lastPosted = moment(app.config.announcements.last_announcement_posted_at).utc();
  if (lastSeen.isBefore(lastPosted)) {
    var now = moment.utc().toISOString();
    // don't change anything in local state, to allow the bouncing indicator
    // logic to work until next refresh
    return updateAnthemUser({ lastAnnouncementSeenAt: now });
  } else {
    return Promise.resolve();
  }
}

export function readAnnouncements() {
  var lastRead = moment(app.state.get('user', 'lastAnnouncementReadAt')).utc();
  var lastPosted = moment(app.config.announcements.last_announcement_posted_at).utc();

  if (lastRead.isBefore(lastPosted)) {
    var now = moment.utc().toISOString();
    // set lastAnnouncementSeenAt too, since `acknowledgeAnnouncements`
    // didn't change that locally in order to let the indicator bounce
    app.state.set(['user', 'lastAnnouncementSeenAt'], now);
    app.state.set(['user', 'lastAnnouncementReadAt'], now);
    return updateAnthemUser({ lastAnnouncementReadAt: now });
  } else {
    return Promise.resolve();
  }
}

export function userLabel(user) {
  var first = user.first_name;
  var last = user.last_name;
  if (!first && !last) {
    return user.username;
  } else if (!last) {
    return first;
  } else if (!first) {
    return last;
  }
  return first + ' ' + last;
}
