import Vue from 'vue';
import VueApollo from 'vue-apollo';
import { ApolloClient } from 'apollo-client';
import { HttpLink } from 'apollo-link-http';
import { InMemoryCache } from 'apollo-cache-inmemory';

let tower = new URL(window.location);
tower.pathname = '/api/tower/graphql';
tower.search = '';

const httpLink = new HttpLink({
  uri: tower.toString(),
  credentials: 'include'
});

export const towerClient = new ApolloClient({
  link: httpLink,
  cache: new InMemoryCache(),
  connectToDevTools: true,
  shouldBatch: true,
});

Vue.use(VueApollo);

export const apolloProvider = new VueApollo({
  defaultClient: towerClient,
});

let importer = new URL(window.location);
importer.pathname = '/api/chorus-import-manager/graphql';
importer.search = '';

export const importerClient = new ApolloClient({
  link: new HttpLink({
    uri: importer.toString(),
    credentials: 'include'
  }),
  cache: new InMemoryCache(),
  connectToDevTools: true,
  defaultOptions: {
    watchQuery: { fetchPolicy: 'network-only' },
    query: { fetchPolicy: 'network-only' },
  }
});
