<template>
  <div v-if="currentDoc && autosave.ready" class="m-layout-mode">
    <div class="l-content p-flex">
      <div class="p-flex--v m-preview__wrapper">
        <div :style="bannerMessageOverride" class="m-layout-mode__wrapper">
          <section class="m-layout-mode__content">
            <edit-block
              v-if="selectedBlockIds.length"
              :blockIds="selectedBlockIds"
              :postMessage="postMessage" />
            <edit-package-cover
              v-else-if="packageCoverIndex"
              :coverIndex="packageCoverIndex"
              :postMessage="postMessage" />
            <edit-layout v-else />
          </section>
          <div v-if="selectedBlockIds.length || packageCoverIndex" class="m-layout-mode__navigation">
            <res-button design="default" paddingless>
              <button type="button" @click="deselectBlocks">
                <res-icon name="back" size="small" />
                Back to layout
              </button>
            </res-button>
          </div>
        </div>
        <!-- preview below TK -->
        <iframe
          id="layout-iframe"
          ref="layoutIframe"
          :src="iframeurl"
          width="100%"
          height="500px"
          allowfullscreen
          webkitallowfullscreen
          msallowfullscreen
          style="height: 500px;"
        />
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import { HeadlessConfig } from '../../queries/headlessConfig.graphql';
import EditLayout from './edit-layout.vue';
import EditBlock from './edit-block.vue';
import EditPackageCover from './edit-package-cover.vue';

export default {
  components: {
    EditLayout,
    EditBlock,
    EditPackageCover,
  },
  props: {
    uuid: {
      type: String,
      required: true,
      default: '',
    }
  },
  data() {
    return {
      siteConfig: {},
      selectedBlockIds: [],
      packageCoverIndex: null,
    };
  },
  computed: {
    ...mapState([
      'currentCommunity',
      'autosave',
      'messageBanner',
      'isMobile'
    ]),
    ...mapGetters([
      'currentDoc',
      'draftBody',
      'contents'
    ]),
    communityId() {
      return this.currentCommunity.id;
    },
    componentData() {
      if (this.currentDoc) {
        const ids = this.draftBody.ops.filter(op => op.hasOwnProperty('attributes')).map(op => op.attributes.id);
        return ids.join(',');
      }
      return null;
    },
    packageCovers() {
      if (this.currentDoc) {
        return (this.contents.draft.package_cover ? this.contents.draft.package_cover.length : 0);
      }
      return 0;
    },
    iframeurl() {
      // TODO - query params are for testing only
      return `${this.siteConfig.externalLayoutUrl}/?&c=${this.componentData}&p=${this.packageCovers}`;
    },
    bannerMessageOverride() {
      const navHeight = this.isMobile ? 45 : 72; // 72 here should match $editor-navbar-height sass variable, 45 should match the $mobile-navbar-height
      const messageHeight = 44;
      const numberOfMessages = this.messageBanner.notifications.length;
      const navHeightWithMessages = (messageHeight * numberOfMessages) + navHeight;
      return `top: ${navHeightWithMessages}px`;
    }
  },
  mounted() {
    window.addEventListener('message', this.onMessage);
  },
  beforeDestroy() {
    window.removeEventListener('message', this.onMessage);
  },
  methods: {
    deselectBlocks() {
      this.postMessage({
        event: 'setSelection',
        blockIds: []
      });
    },
    postMessage(message) {
      // TODO replace star selector
      this.$refs.layoutIframe.contentWindow.postMessage(message, '*');
    },
    onMessage(event) {
      // TODO check this is coming from the correct iframe
      if (event.data.event === 'selection') {
        this.selectedBlockIds = event.data.blockIds;
        this.packageCoverIndex = event.data.packageCoverIndex;
      }
    }
  },
  apollo: {
    siteConfig: {
      query: HeadlessConfig,
      variables () {
        return {
          id: this.communityId
        };
      },
      skip() {
        return !this.communityId;
      },
      update: data => data.community,
    }
  },
};
</script>
