import { get as getPath, compact, cloneDeep } from 'lodash';
import moment from 'moment-timezone';
import request from '../lib/request';
import parsePagination from '../lib/parse_pagination';
import isPublished from '../lib/is_published';
import stripProtocol from '../lib/strip_protocol';
import getObjects from '../../shared/delta/get_objects';
import getSubdocIds from '../../shared/subdoc_ids';
import * as entryActions from './entry';
import * as messageActions from './messages';
import * as publishActions from './publish';
import * as communityActions from './communities';
import * as userActions from './users';
import { isValidUrlWithSlash } from '../lib/is_valid_url';

export function createDoc(schemaName, params={}) {
  app.state.set(['requestInProgress'], true);
  return request('/new' + schemaName, {
    method: 'POST',
    query: {
      community_id: app.state.get('community', 'id')
    },
    body: params
  }).then((doc) => {
    app.state.set(['requestInProgress'], false);
    return doc;
  });
}

export function createDocAndRedirect(schemaName, params={}) {
  return createDoc(schemaName, params).then(doc => {
    var page = 'compose';
    if (params.entry_type === 'LandingPage') {
      page = 'package';
    } else if (params.entry_type === 'CustomStory') {
      page = 'customstory';
    }
    document.location.href = `/${page}/${doc.uuid}`;
  });
}

// The many steps necessary to move a doc from one community to another
export async function changeDocCommunityAndRedirect(communityId) {
  app.state.set(['requestInProgress'], true);
  const doc = app.state.get(['doc']);

  if (doc.status === 'SCHEDULED') {
    await publishActions.unschedule(doc);
  }

  const delta = publishActions.pubHistoryUpdate(doc, 'community_changed', { communityId });

  // Update the community twitter handle
  const newCommunity = await communityActions.getCommunity(communityId, 'Basic');
  const newCommunityHandle = newCommunity.twitterScreenName || null;
  const currentCommunityHandle = getPath(doc.head.contents, ['draft', 'autotweets', 0, 'handle']) || null;
  if (newCommunityHandle !== currentCommunityHandle && !isPublished(doc.status)) {
    delta.push({
      p: ['draft', 'autotweets', 0, 'handle'],
      od: currentCommunityHandle,
      oi: newCommunityHandle
    });
  }

  await updateDoc(doc.id, { community_id: communityId, delta });

  if (isPublished(doc.status)) {
    await publishActions.publishOrSchedule(doc, { republishPublished: true });
  } else {
    await entryActions.updateEntry(doc, { community_id: communityId });
  }

  document.location.href = `/preflight/${doc.uuid}?communityChanged=true`;
}

export function getDoc(id) {
  return request('/api/content/docs/' + id, {
    query: { includes: 'owner,updateUser,community,external_ids' },
    opts: { cache: true, force: true }
  });
}

export function validateDoc(id) {
  return request(`/api/content/docs/${id}/validate`, { query: {
    returnErrors: true,
    returnWarnings: true,
    returnValid: true
  } });
}

export function getEntryParams(id) {
  return request(`/api/content/docs/${id}/entry_params`);
}

function parseUrlsFromQuery(query) {
  const newQuery = cloneDeep(query);
  let searchText = newQuery.q || '';
  const terms = searchText.split(' ').reduce((memo, term) => {
    if (isValidUrlWithSlash(term)) {
      memo.url = stripProtocol(term);
    } else if (term) {
      memo.q.push(term);
    }

    return memo;
  }, { url: undefined, q: [] });

  if (terms.q.length > 0) {
    newQuery.q = terms.q.join(' ');
  } else {
    delete newQuery.q;
  }

  if (terms.url) {
    newQuery.url = terms.url;
  }

  return newQuery;
}

export async function searchDocs(query, opts = null) {
  const searchText = query.q || '';
  const queryWithUrlParsedOut = parseUrlsFromQuery(query);

  let { docs, pagination, parsedQuery } = await request('/api/content/docs', { query: queryWithUrlParsedOut, opts });

  if (query.schema_name === 'story') {
    let userIds = [];
    for (let doc of docs) {
      let contributors = getPath(doc, ['head', 'contents', 'draft', 'contributors'], []);
      userIds = userIds.concat(contributors.map(c => c.chorus_id));
    }

    let users = await userActions.getUsers(userIds);
    for (let doc of docs) {
      let contributors = getPath(doc, ['head', 'contents', 'draft', 'contributors'], []);
      doc.contributors = compact(contributors.map(c => users.find(u => u && u.id === c.chorus_id)));
    }
  }

  return {
    docs,
    formattedQuery: formatParsedQuery(parsedQuery, searchText),
    pagination: parsePagination(pagination),
  };
}

function formatParsedQuery(parsedQuery, searchText) {
  if (!parsedQuery || !searchText) {
    return [];
  }

  let { keywordQueries, phrases, tokens } = parsedQuery;

  let keywords = keywordQueries.reduce((formatted, { keyword, type, value }) => {
    let isPagesQuery = (
      keyword === 'type' &&
      value === 'page' &&
      window.location.pathname.includes('pages')
    );
    // Remove `type:page` implicitly added in searchParams if on pages dashboard
    if (isPagesQuery) {
      return formatted;
    }

    if (type === 'phrase') {
      value = `"${value}"`;
    } else if (type === 'date') {
      value = value.original;
    }

    formatted.push(`${keyword}:${value}`);
    return formatted;
  }, []);

  return keywords.map(value => ({ type: 'keyword', value }))
    .concat((phrases || []).map(value => ({ type: 'phrase', value: `"${value}"` })))
    .concat([{ type: 'tokens', value: tokens }])
    .filter(({ value }) => value !== '');
}

export function updateDoc(id, params) {
  return request('/api/content/docs/' + id, {
    method: 'PUT',
    body: params
  }, { handleError: false });
}

export function subdocsForDoc(doc) {
  var subdocIds = getSubdocIds(doc.head.contents);
  return app.state.get(['subdocs']).filter(doc => subdocIds.includes(doc.id));
}

export function getAllDocObjects(doc, types) {
  var contents = doc.head.contents;
  var subdocs = subdocsForDoc(doc);

  // objects from doc body
  var objects = getObjects(contents.draft.body, types);

  // objects from all aside and scorecard subdocs
  for (let subdoc of subdocs) {
    if ((subdoc.schema_name === 'aside' || subdoc.schema_name === 'scorecard') && subdoc.head.contents.hasOwnProperty('body')) {
      objects = objects.concat(getObjects(subdoc.head.contents.body, types));
    }
  }

  // if oembed is in the given types, add the object from main_oembed
  const mainOembed = contents.draft.main_oembed;
  if (types.includes('oembed') && mainOembed) {
    objects.push({ oembed: mainOembed });
  }

  return objects;
}

export function setArchived(doc) {
  var archived = !doc.archived;
  return updateDoc(doc.id, { archived }).then(() => {
    return archived;
  });
}

// Within this action, both the unscheduling logic + the logic to update trashed_at
// in Anthem use server-side autosave via a `delta` param. This is because `setTrashed`
// can be called from the dashboard, where client-side autosave isn't instantiated.
export function setTrashed(doc) {
  var prevTrashedAt = getPath(doc, ['head', 'contents', 'trashed_at']);
  var nextTrashedAt = prevTrashedAt ? null : moment().toISOString();

  // If the user is moving a scheduled story to the trash, ensure we unschedule
  // the story first
  var promise = Promise.resolve();
  if (nextTrashedAt && doc.status === 'SCHEDULED') {
    promise = publishActions.unschedule(doc);
  }

  return promise
    .then(() => updateDoc(doc.id, {
      delta: [{
        p: ['trashed_at'],
        od: prevTrashedAt,
        oi: nextTrashedAt
      }]
    }))
    .then(() => entryActions.updateEntryFlags(doc, { trashed_at: nextTrashedAt }))
    .then(() => {
      return nextTrashedAt;
    })
    .catch(() => messageActions.showError());
}

export async function setStatus(doc, status, publishHistoryType) {
  try {
    app.state.set(['requestInProgress'], true);
    await updateDoc(doc.id, { status, delta: publishActions.pubHistoryUpdate(doc, publishHistoryType || status.toLowerCase()) });
    app.state.set(['requestInProgress'], false);
  } catch (err) {
    messageActions.showError();
  }
}
