<template>
  <li :class="className" >
    <div v-show="editing" class="m-editable-list-item__content">
      <res-input @keyup.enter.prevent="save">
        <input ref="input" v-model.trim="name" type="text" />
      </res-input>
      <res-button
        class="m-editable-list-item__save"
        design="primary"
        @click="save">
        <res-icon name="check" />
        <button/>
      </res-button>
      <res-button
        class="m-editable-list-item__cancel"
        design="secondary"
        @click="cancel">
        <res-icon name="x"/>
        <button />
      </res-button>
    </div>
    <div v-show="!editing" class="m-editable-list-item__content">
      <res-button
        :disabled="disabled"
        class="m-editable-list-item__name"
        paddingless
        @click="!disabled && onNameClick()">
        <button>{{ name }}</button>
      </res-button>
      <res-button
        :disabled="disabled"
        class="m-editable-list-item__edit"
        title="Edit"
        @click="!disabled && edit()">
        <res-icon name="edit"/>
        <button/>
      </res-button>
      <res-button
        :res-modal-open="modalId"
        :disabled="disabled"
        title="Delete"
        class="m-editable-list-item__delete"
        negative
        @click="!disabled && onDeleteClick()">
        <res-icon name="trash" />
        <button />
      </res-button>
    </div>
  </li>
</template>

<script>
import { mapState } from 'vuex';

export default {
  props: {
    itemName: {
      type: String,
      default: '',
      required: true,
    },
    onNameClick: {
      type: Function,
      default: () => {},
      required: true,
    },
    onDeleteClick: {
      type: Function,
      default: () => {},
      required: true,
    },
    onSaveClick: {
      type: Function,
      default: () => {},
      required: true,
    },
    active: {
      type: Boolean,
      default: false,
      required: true,
    },
    disabled: {
      type: Boolean,
      default: false,
    },
  },
  data() {
    return {
      editing: false,
      name: '',
    };
  },
  computed: {
    ...mapState(['modalId']),
    className() {
      let className = 'm-editable-list-item';
      if (this.active) {
        className += ' m-editable-list-item--active';
      }
      return className;
    }
  },
  mounted() {
    this.name = this.itemName;
  },
  methods: {
    async edit() {
      this.editing = true;
      await this.$nextTick();
      this.$refs.input.focus();
    },
    save() {
      if (this.name !== this.itemName) {
        this.onSaveClick(this.name);
      }
      this.editing = false;
    },
    cancel() {
      this.name = this.itemName;
      this.editing = false;
    }
  }
};
</script>
