import * as cmd from './commands';
import sharedMutations from '../../shared/vue/store/mutations';

export default {
  ...sharedMutations,

  [cmd.SET_IS_MOBILE](state, isMobile) {
    state.isMobile = isMobile;
  },

  [cmd.SAVE_WINDOW_SIZE](state, { width = null, height = null }) {
    if (width !== null) {
      state.windowSize.width = width;
    }

    if (height !== null) {
      state.windowSize.height = height;
    }
  },

  /**
   * Adds chorus-help announcement-specific fields to the currentUser state
   */
  [cmd.PATCH_USER](state, patch = {}) {
    if (Object.keys(patch).length) {
      state.currentUser = Object.assign({}, state.currentUser, patch);
    }
  },

  /**
  * Sets the currently active sidebar type.
  */
  [cmd.SET_SIDEBAR](state, options) {
    // Mutation may specify a "when" type criteria,
    // at which time we'll only make changes when in the checked type state.
    if (options && options.when && !options.when.test(state.currentSidebar.type)) {
      return;
    }

    const type = options ? options.to : null;
    const sidebarComponents = [
      'chorus-help'
    ];
    // make sure the options.to value is a valid sidebar component name
    const valid = sidebarComponents.includes(type);

    state.currentSidebar = {
      type: valid ? type : null,
      title: valid ? (options.title || null) : null,
      id: valid ? (options.id || null) : null,
      childProps: valid ? (options.childProps || null) : null,
      orientation: valid ? (options.orientation) : 'right',
      pushContent: valid ? (options.pushContent) : false
    };
  },

  /**
   * Sets the currently active modal
   */
  [cmd.SET_MODAL](state, options) {
    if (!options) {
      state.currentModal = null;
      return;
    }

    const { childProps, component } = options;
    const modalComponents = [
      'shortlink-modal',
      'saved-search-delete-modal',
      'saved-search-create-modal',
    ];
    const valid = modalComponents.includes(component);
    const shouldReRender = valid && component !== state.currentModal.component;

    state.currentModal = {
      childProps: childProps || null,
      component: valid ? component : null,
    };

    // If the child modal component is changing, increment the
    // AppModal's key to avoid rendering issues
    if (shouldReRender) {
      state.modalKey++;
    }
  },

  /**
   * Sets the currently active overlay
   */
  [cmd.SET_OVERLAY](state, options) {
    if (!options) {
      state.currentOverlay = null;
      return;
    }

    const { childProps, component, orientation } = options;
    const overlayComponents = [
      'chorus-help-announcement',
      'chorus-help-contact-form'
    ];
    const valid = overlayComponents.includes(component);

    state.currentOverlay = {
      childProps: childProps || null,
      component: valid ? component : null,
      orientation: orientation || 'left',
    };
  },

  /**
   * inits the Anthem Socket
   */
  [cmd.ANTHEM_SOCKET_INIT](state, socket) {
    state.anthemSocket = socket;
  },

  /**
   * add a websocket user to the autosave state
   */
  [cmd.ANTHEM_SOCKET_ADD_USER](state, { newUser }) {
    state.autosave.connectedUsers.push(newUser);
  },

  /**
   * remove a websocket user from the autosave state
   */
  [cmd.ANTHEM_SOCKET_REMOVE_USER](state, { oldUser }) {
    state.autosave.connectedUsers.splice(state.autosave.connectedUsers.indexOf(oldUser), 1);
  },

  /**
   * set socketError status, if true, this will put the editor into a read-only state
   */
  [cmd.SOCKET_ERROR](state, status) {
    state.socketError = status;
  },
  /**
   * Recovers app from error state rendered by Error Boundary
   */
  [cmd.RESET_ERROR_BOUNDARY](state) {
    state.errorBoundaryKey++;
  }

};
