var isMapstack = require('./is_mapstack');
var isStream = require('./is_stream');
var isLinkPost = require('./is_link_post');
var isPackage = require('./is_package');
var isPage = require('./is_page');
var isCustomstory = require('./is_customstory');

module.exports = function getStoryType(contents) {
  if (isMapstack(contents)) {
    return 'mapstack';
  } else if (isStream(contents)) {
    return 'stream';
  } else if (isLinkPost(contents)) {
    return 'link';
  } else if (isPackage(contents)) {
    return 'package';
  } else if (isPage(contents)) {
    return 'page';
  } else if (isCustomstory(contents)) {
    return 'custom story';
  }
  return 'story';
};
