export const EditorCommunity = `
  query EditorCommunity($communityId: Int!) {
    community(id: $communityId) {
      id
      name
      slug
      domain
      schemaDomain
      commentArchiveAgeInDays
      faviconUrl
      placeFirstAvailableDefault
      timeZone
      placeholderImageUrl
      twitterScreenName
      twitterSendDefault
      defaultImageSearchParams
      featuresEnabled:                siteConfigBoolean(key: "features_enabled")
      adsEnabled:                     siteConfigBoolean(key: "ads_enabled")
      paywallEnabled:                 siteConfigBoolean(key: "paywall_enabled")
      tweetWithImageByDefault:        siteConfigBoolean(key: "tweet_with_image_by_default")
      entryDisclaimerEnabled:         siteConfigBoolean(key: "entry_disclaimer_enabled")
      commentsEnabled:                siteConfigBoolean(key: "comments_enabled")
      coralCommentsEnabled:           siteConfigBoolean(key: "coral_enabled")
      commentsClosedDefault:          siteConfigBoolean(key: "comments_closed_on_editorial_posts_by_default")
      publishForGoogleAmp:            siteConfigBoolean(key: "publish_for_google_amp")
      bylineShowTitle:                siteConfigBoolean(key: "byline_show_title")
      printSlugEnabled:               siteConfigBoolean(key: "print_slug_enabled")
      printDeskHidden:                siteConfigBoolean(key: "print_desk_hidden")
      ratingCardMaxStars:             siteConfigInt(key: "rating_card_max_stars")
      useProjectDetails:              siteConfigBoolean(key: "project_details_enabled")
      useToolsAndMaterials:           siteConfigBoolean(key: "tools_and_materials_enabled")
      projectDetailsSkillLevels:      siteConfigJson(key: "project_details_skill_levels")
      hasGettyApi:                    hasSiteConfig(key: "getty_api_config", type: "json")
      hasUsatodayApi:                 hasSiteConfig(key: "usatoday_api_config", type: "json")
      hasPlagscan:                    hasSiteConfig(key: "plagscan_api_config", type: "json")
      hasMerlinApi:                   hasSiteConfig(key: "merlin_api_config", type: "json")
      entrySlugIsEvergreenDefault:    siteConfigBoolean(key: "entry_slug_is_evergreen_default")
      showEntryEvergreenUrlToggle:    siteConfigBoolean(key: "show_entry_evergreen_url_toggle")
      defaultGroupSlug:               siteConfigString(key: "default_story_group_slug")
      headlessModeEnabled:            siteConfigBoolean(key: "headless_mode_enabled")
      externalPreviewEnabled:         hasSiteConfig(key: "external_preview_url", type: "string")
      externalPreviewUrl:             siteConfigString(key: "external_preview_url")
      externalPreviewMode:            siteConfigString(key: "external_preview_mode")
      externalPreviewSharedSecret:    siteConfigString(key: "external_preview_shared_secret")
      externalPreviewRequestMethod:   siteConfigString(key: "external_preview_request_method")
      externalPreviewApiHeaders:      siteConfigJson(key: "external_preview_api_headers")
      externalPreviewEnableAmp:       siteConfigBoolean(key: "external_preview_enable_amp")
      publishForAppleNews:            siteConfigBoolean(key: "publish_for_apple_news")
      useAffiliateLinks:              siteConfigBoolean(key: "use_affiliate_links")
      allowDisablingOfAffiliateLinks: siteConfigBoolean(key: "allow_disabling_of_affiliate_links_in_stories")
      network {
        id
        name
        schemaDomain
        slug
        autoplayDefault:      siteConfigBoolean(key: "autoplay_videos_on_entry_pages")
        usesSuperGroups:      siteConfigBoolean(key: "uses_super_groups")
        usesCampaignGroups:   networkConfigBoolean(key: "uses_campaign_groups?")
        usesAutotags:         siteConfigBoolean(key: "uses_autotagger")
        excludesFromRssFeeds: networkConfigBoolean(key: "excludes_from_rss_feeds?")

        organization {
          id
          slug
        }
      }
      locale {
        lat
        lng
      }
      newsletters {
        name
        slug
      }
      googleAmpLogo {
        url
      }
      autoTagging {
        leagueRequired
        leagueId
        teamId
        autolinkTagTypes
        autotagTagTypes: manualTagTypes
      }
    }
  }
`;

export const DashboardCommunity = `
  query DashboardCommunity($communityId: Int!) {
    community(id: $communityId) {
      id
      name
      faviconUrl
      timeZone
      printSlugEnabled: siteConfigBoolean(key: "print_slug_enabled")
      network {
        id
        name
        schemaDomain
      }
    }
  }
`;

export const BasicCommunity = `
  query BasicCommunity($communityId: Int!) {
    community(id: $communityId) {
      id
      name
      twitterScreenName
    }
  }
`;
