<template>
  <div v-show="loading" :class="`m-app-loader m-app-loader__${animation}`">
    <res-icon :name="iconName" size="large"/>
  </div>
</template>

<script>
import store from './store.js';
import { mapState } from 'vuex';

export default {
  name: 'AppLoader',
  props: {
    iconName: {
      type: String,
      default: 'loading'
    },
    animation: {
      type: String,
      default: 'spin'
    }
  },
  computed: {
    ...mapState('appLoader', ['loading'])
  },
  created() {
    // check to see if module is already registered before registering again
    // because HMR re-registers this during development
    if (!this.$store.state.appLoader) this.$store.registerModule('appLoader', store);
  }
};
</script>
