import * as cmd from './commands';
import sharedGetters from '../../shared/vue/store/getters';

export default {
  ...sharedGetters,

  [cmd.READ_ONLY](state) {
    const autosaveError = state.autosave.error;
    const socketError = state.socketError;
    const maintenanceMode = (state.messageBanner.notifications.some(m => m.persistent)); // any persistent banner (not just one for maintenance mode) will indicate read-only state
    // const docMissingEntry = state.docMissingEntry; @TODO: hook this up
    // const trashedAt = state.trashedAt; @TODO: hook this up
    return autosaveError || socketError || maintenanceMode;
  },

  [cmd.CUSTOM_FIELDS_ENABLED](state) {
    const ENABLED_ORGANIZATIONS = ['startribune'];
    let organizationEnabled = false;
    if (state.currentCommunity && state.currentCommunity.network && state.currentCommunity.network.organization && state.currentCommunity.network.organization.slug) {
      organizationEnabled = ENABLED_ORGANIZATIONS.includes(state.currentCommunity.network.organization.slug);
    }

    const ENABLED_NETWORKS = [];
    let networkEnabled = false;
    if (state.currentCommunity && state.currentCommunity.network && state.currentCommunity.network.slug) {
      networkEnabled = ENABLED_NETWORKS.includes(state.currentCommunity.network.slug);
    }

    const ENABLED_COMMUNITIES = ['startribune', 'citypages', 'anthem-test-blog-sbnprivate', 'crimsontesting'];
    let communityEnabled = false;
    if (state.currentCommunity && state.currentCommunity.slug) {
      communityEnabled = ENABLED_COMMUNITIES.includes(state.currentCommunity.slug);
    }

    return organizationEnabled || networkEnabled || communityEnabled;
  },

  [cmd.ANTHEM_SOCKET_CONNECTED_STATUS](state) {
    if (state.anthemSocket) {
      return state.anthemSocket.connected;
    } else {
      return false;
    }
  },
};
