<template>
  <span>
    <res-dropdown ref="dropdown" :for="doc.uuid" placement="left">
      <button
        v-if="canCopyOrMoveDoc"
        :disabled="readOnly"
        @click="copyDoc">
        Copy
      </button>
      <!-- Filter is empty and user can archive/trash -->
      <button
        v-if="!filter && canArchiveOrTrashStories"
        :disabled="readOnly"
        @click="updateArchived('archive')">
        Archive
      </button>
      <button
        v-if="!filter && canArchiveOrTrashStories"
        :disabled="readOnly"
        @click="updateTrashedAt('trash')">
        Move to trash
      </button>
      <!-- Shortlink button -->
      <button
        v-if="!filter"
        :disabled="readOnly"
        :res-modal-open="modalId"
        type="button"
        @click="setShortlinkModal">
        Share
      </button>
      <!-- Archive view and user can archive/trash -->
      <button
        v-if="filter === 'archive' && canArchiveOrTrashStories"
        :disabled="readOnly"
        @click="updateArchived('dashboard')">
        Restore
      </button>
      <button
        v-if="filter === 'archive' && canArchiveOrTrashStories"
        :disabled="readOnly"
        @click="updateArchivedAndTrashed('trash')">
        Move to trash
      </button>
      <!-- Trash view -->
      <button
        v-if="filter === 'trash'"
        :disabled="readOnly"
        @click="updateTrashedAt('dashboard')">
        Restore
      </button>
      <button
        v-if="filter === 'trash'"
        :disabled="readOnly"
        @click="updateArchivedAndTrashed('archive')">
        Move to archive
      </button>
    </res-dropdown>

    <res-button
      v-if="isMobile"
      v-show="showActionsButton"
      :key="mobileKey"
      :id="doc.uuid"
      design="secondary">
      <button type="button">Actions</button>
    </res-button>

    <res-button
      v-if="!isMobile"
      v-show="showActionsButton"
      :key="mobileKey"
      :id="doc.uuid"
      design="default">
      <res-icon
        size="medium"
        title="Menu"
        name="more"
        animated="false" />
      <button type="button" />
    </res-button>

    {{ actionStatus }}

    <res-button
      v-if="moveCompleted"
      :design="isMobile ? 'secondary' : ''"
      class="m-dashboard__table__actions__restore"
      @click="restoreDoc">
      <button type="button">Restore</button>
    </res-button>
  </span>
</template>

<script>
import canCopyOrMove from '../../client/lib/can_copy_or_move';
import messages from '../../client/config/messages';
import request from '../../client/lib/request';
import { mapState, mapGetters } from 'vuex';
import { reportError } from '../../client/actions/sentry';
import { toggleArchived, updateTrashedAt } from '../lib/docs';
import { SET_MODAL } from '../store/commands';

export default {
  props: {
    doc: {
      type: Object,
      required: true,
      default: () => ({})
    },
    filter: {
      type: String,
      default: ''
    },
    canArchiveOrTrashStories: {
      type: Boolean,
      default: false,
    },
    onMoveCompleted: {
      type: Function,
      default: () => {},
    },
  },
  data() {
    return {
      canCopyOrMoveDoc: canCopyOrMove(this.doc),
      copyInProgress: false,
      destination: '',
      // Clone of the doc to keep track of changes made via actions
      docObj: this.doc,
      moveCompleted: false,
      moveInProgress: false,
      mobileKey: 1
    };
  },
  computed: {
    ...mapState([
      'currentCommunity',
      'isMobile',
      'currentUser',
      'modalId'
    ]),
    ...mapGetters(['readOnly']),
    showActionsButton() {
      return !this.moveInProgress && !this.moveCompleted && !this.copyInProgress;
    },
    actionStatus() {
      if (this.moveInProgress) {
        return `Moving to ${this.destination}`;
      } else if (this.copyInProgress) {
        return 'Copying';
      }
      return '';
    },
    isTrashed() {
      return this.docObj.head.contents.trashed_at !== null;
    },
  },
  watch: {
    filter() {
      this.moveCompleted = false;
    },
    isMobile() {
      this.mobileKey++;
    }
  },
  methods: {
    handleError(error) {
      reportError(error, false);
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED
      });
    },
    async copyDoc() {
      this.copyInProgress = true;
      const params = {
        community_id: this.currentCommunity.id,
        copy_doc_uuid: this.doc.uuid,
      };
      try {
        // TODO: Look into making a util for /newstory requests
        const copiedDoc = await request('/newstory', {
          method: 'POST',
          body: params,
          opts: { handleError: false }
        });
        window.location.assign(`/compose/${copiedDoc.uuid}`);
      } catch (error) {
        this.copyInProgress = false;
        this.handleError(error);
      }
    },
    setMoveInProgress(destination) {
      this.destination = destination;
      this.moveInProgress = true;
      this.moveCompleted = false;
    },
    setMoveCompleted(isLeaving) {
      this.moveInProgress = false;
      this.moveCompleted = isLeaving;
      this.onMoveCompleted(isLeaving);
    },
    async updateArchived(destination) {
      // Hide the actions res-dropdown
      this.$refs.dropdown.toggle();
      this.setMoveInProgress(destination);
      await this.toggleArchived();
      this.setMoveCompleted(true);
    },
    async restoreDoc() {
      if (this.filter && this.destination !== 'dashboard') {
        return this.updateArchivedAndTrashed(this.destination);
      }

      if (this.docObj.archived) {
        this.setMoveInProgress('dashboard');
        await this.toggleArchived();
        this.setMoveCompleted(false);
      } else if (this.docObj.head.contents.trashed_at) {
        this.setMoveInProgress('dashboard');
        await this.toggleTrashed();
        this.setMoveCompleted(false);
      }
    },
    async toggleArchived() {
      try {
        const doc = await toggleArchived(this.docObj);
        this.docObj = doc;
      } catch (error) {
        this.handleError(error);
      }
    },
    async updateTrashedAt(destination) {
      // Hide the actions res-dropdown
      this.$refs.dropdown.toggle();
      this.setMoveInProgress(destination);
      await this.toggleTrashed();
      this.setMoveCompleted(true);
    },
    async updateArchivedAndTrashed(destination) {
      // Hide the actions res-dropdown
      this.$refs.dropdown.toggle();
      this.setMoveInProgress(destination);
      try {
        const [doc, trashedDoc] = await Promise.all([
          toggleArchived(this.docObj),
          updateTrashedAt(this.docObj, this.currentUser.id)
        ]);
        // merge values in case of discrepancy from promises order
        this.docObj = { ...trashedDoc, archived: doc.archived };
        this.setMoveCompleted(true);
      } catch (error) {
        this.setMoveCompleted(false);
        this.handleError(error);
      }
    },
    async toggleTrashed() {
      try {
        const doc = await updateTrashedAt(this.docObj, this.currentUser.id);
        this.docObj = doc;
      } catch (error) {
        this.setMoveCompleted(false);
        this.handleError(error);
      }
    },
    setShortlinkModal() {
      this.$store.commit(SET_MODAL, {
        childProps: { doc: this.doc },
        component: 'shortlink-modal',
      });
    }
  }
};
</script>
