const request = require('../lib/request');
const docActions = require('./docs');
const messageActions = require('./messages');
const toPlaintext = require('convert-rich-text').toPlaintext;
const moment = require('moment-timezone');
const isPublished = require('../lib/is_published');
const isPackage = require('../../shared/is_package');
const getPackageStories = require('../../shared/get_package_stories');
const getWordCount = require('../../shared/delta/get_word_count');
const getEventDatetime = require('../../shared/get_event_datetime');
const { SEND_TO_PRINT_SUCCESS } = require('../config/messages');
const sentryActions = require('./sentry');

export async function publishOrSchedule(doc, params, onProgress) {
  app.state.set(['requestInProgress'], true);
  const contents = doc.head.contents;
  let total = 1;
  let loaded = 0;

  if (isPackage(contents)) {
    const toc = contents.draft.package_toc;
    const stories = getPackageStories(toc);
    total += stories.length;
    onProgress({ loaded, total });

    try {
      await Promise.all(stories.reduce((promises, story) => {
        const packageDoc = app.state.get(['packageDocs', story.uuid]);
        if (!isPublished(packageDoc.status)) {
          promises.push(publishOrScheduleDoc(packageDoc, params));
        }
        loaded += 1;
        onProgress({ loaded, total });
        return promises;
      }, []));
    } catch (err) {
      sentryActions.reportError(err);
    }
  }

  await publishOrScheduleDoc(doc, params);
  if (onProgress) {
    onProgress({ loaded: total, total });
  }
  app.state.set(['requestInProgress'], false);
}

async function publishOrScheduleDoc(doc, params) {
  if ('publishScheduledFor' in params) {
    await scheduleDoc(doc, params.publishScheduledFor);
  } else {
    await publishDoc(doc, params);
  }
  if (params.publishScheduledFor !== null) {
    trackPublishOrScheduleEvent(params.publishScheduledFor ? 'Scheduled' : 'Published', doc, params);
  }
}

async function publishDoc(doc, params) {
  try {
    await request(`/api/content/docs/${doc.id}/publish`, { method: 'POST', body: params });
  } catch (err) {
    if (isPackage(doc.head.contents)) {
      sentryActions.reportError(err, false);
    }
    messageActions.showError();
    app.state.set(['requestInProgress'], false);
  }
}

export async function scheduleDoc(doc, publishScheduledFor, opts = {}) {
  const history = doc.head.contents.publish_history;

  // If scheduling is being canceled, do our best to restore the doc's previous status
  let status;
  if (publishScheduledFor) {
    status = 'SCHEDULED';
  } else if (getEventDatetime(history, ['approved'])) {
    status = 'APPROVED';
  } else if (getEventDatetime(history, ['submitted'])) {
    status = 'SUBMITTED';
  } else {
    status = 'DRAFT';
  }

  let eventType;
  if (!publishScheduledFor) {
    eventType = 'schedule_canceled';
  } else if (publishScheduledFor && doc.head.contents.publish_scheduled_for) {
    eventType = 'rescheduled';
  } else {
    eventType = 'scheduled';
  }

  // Update the doc status, scheduled date, and publish history
  await docActions.updateDoc(doc.id, {
    status,
    delta: [
      {
        p: ['publish_scheduled_for'],
        oi: publishScheduledFor,
        od: doc.head.contents.publish_scheduled_for
      },
      ...pubHistoryUpdate(doc, eventType, { publishScheduledFor, ...opts })
    ]
  });
}

export function unschedule(doc, onProgress) {
  return publishOrSchedule(doc, { publishScheduledFor: null }, onProgress);
}

export function pubHistoryUpdate(doc, type, opts = {}) {
  const event = {
    date: new Date().toISOString(),
    user_id: opts.userId || app.state.get(['user', 'id']),
    event_type: type
  };
  if (type === 'community_changed') {
    event.origin_community_id = app.state.get(['community', 'id']);
    event.destination_community_id = opts.communityId;
  } else if (type === 'scheduled' || type === 'rescheduled') {
    event.scheduled_date = opts.publishScheduledFor;
  }
  const index = doc.head.contents.publish_history.length;
  return [{
    p: ['publish_history', index],
    li: event
  }];
}

function trackPublishOrScheduleEvent(eventName, doc, params) {

  if (window.ga) {
    const draft = doc.head.contents.draft;
    const history = doc.head.contents.publish_history;

    const firstPublishedDate = getEventDatetime(history, ['published', 'published_via_schedule', 'backdated'], { latest: false });
    const lastPublishedDate = getEventDatetime(history, ['published', 'published_via_schedule', 'backdated']);
    const isRepublish = firstPublishedDate && firstPublishedDate !== lastPublishedDate;
    const formatDate = date => {
      return date ? moment(date).format('YYYY-MM-DD HH:mm') : '';
    };
    window.ga('send', {
      hitType: 'event',
      eventCategory: 'publish',
      eventAction: 'publish:entry',
      dimension1: doc.entry_id,
      dimension4: draft.contributors[0].chorus_id,
      dimension5: formatDate(params.publishScheduledFor || firstPublishedDate),
      dimension8: formatDate(params.publishScheduledFor || lastPublishedDate),
      dimension10: draft.site_groups.map(g => g.name).join(':'),
      dimension11: app.state.get('community', 'slug'),
      dimension12: app.state.get(['community', 'network', 'slug']),
      dimension26: new Date(params.publishScheduledFor || lastPublishedDate).getHours(),
      dimension35: app.state.get('user', 'fullOrUserName'),
      dimension36: toPlaintext(draft.hed),
      dimension37: toPlaintext(draft.internal_title),
      dimension38: toPlaintext(draft.seo_title),
      dimension39: toPlaintext(draft.social_title),
      dimension40: isRepublish ? 'Yes' : 'No',
      dimension53: getWordCount(draft.body)
    });
  }
}

export async function sendToPrint(doc, deskName) {
  app.state.set(['requestInProgress'], true);
  try {
    await request(`/api/content/docs/${doc.id}/dti_publish`, { method: 'POST', body: { deskName } });
    messageActions.showMessage({
      type: 'positive',
      message: SEND_TO_PRINT_SUCCESS
    });
  } catch (err) {
    messageActions.showError();
  } finally {
    app.state.set(['requestInProgress'], false);
  }
}
