<template>
  <form class="m-overlay__form" @submit.prevent="submitFeedback()">
    <res-input block>
      <label>Send feedback or report an issue.</label>
      <textarea v-model="message" aria-label="Send feedback to the product team" cols="30" rows="5" />
    </res-input>
    <div class="p-button-row--left">
      <p>By submitting this form, you agree to our <a href="https://www.voxmedia.com/pages/community-guidelines-support-requests">community guidelines</a>.</p>
      <res-button :disabled="!message ? true : false" design="primary" class="m-t-1">
        <button type="submit">Submit</button>
      </res-button>
    </div>
    <res-icon v-if="submitted" size="small" name="more" />
  </form>
</template>

<script>
import { mapState } from 'vuex';
import { SET_SIDEBAR, SET_OVERLAY } from './../../store/commands';
import request from '../../../client/lib/request';
import messages from '../../../client/config/messages';

export default {
  data() {
    return {
      message: '',
      submitted: false
    };
  },
  computed: {
    ...mapState([
      'currentUser',
      'currentCommunity'
    ]),
  },
  methods: {
    handleError() {
      this.$store.commit('messageBanner/PUT_NOTICE', {
        type: 'negative',
        message: messages.UNEXPECTED
      });
    },
    async submitFeedback() {
      const feedback = {
        author: this.currentUser,
        schemaDomain: this.currentCommunity.network.schemaDomain,
        url: window.location.href,
        text: this.message,
        userAgent: navigator.userAgent
      };
      try {
        await request('/feedback', {
          method: 'POST',
          body: feedback
        });
        // ga.sendEvent('support', 'sendFeedback');
        this.$store.commit('messageBanner/PUT_NOTICE', {
          type: 'positive',
          message: messages.FEEDBACK_SENT,
          timeout: false,
          icon: 'check'
        });
        this.closeOverlay();
      } catch(error) {
        this.handleError();
      }
    },
    closeOverlay() {
      this.$store.commit(SET_SIDEBAR, null);
      this.$store.commit(SET_OVERLAY, {
        childProps: null,
        component: null
      });
    }
  }
};
</script>
