<template>
  <div>
    <div :class="`m-nav-mobile ${activeMobileNavClass}`">
      <button type="button" @click="toggleMobileNav()">
        <div class="bar" />
        <div class="bar" />
        <div class="bar" />
      </button>
    </div>

    <div :class="`m-nav-mobile--menu ${activeMobileNavClass}`" :style="activeMobileNavHeightStyle">
      <div class="m-nav-mobile--menu__top">
        <h1>{{ currentDoc.title }}</h1>
        <help-toggle :isMobile="isMobile" />
      </div>
      <div class="m-nav-mobile--menu__community">
        <community-lockup :communityId="communityId" :isMobile="isMobile" />
      </div>
      <nav style="background: white;">
        <a v-for="link in navLinks"
           :key="link.label"
           :href="`/${link.label.toLowerCase()}/${currentDoc.uuid}`"
           :class="pageActive(link.name, link.label.toLowerCase())"
           type="button">
          {{ link.label }}
        </a>
      </nav>
      <div :class="`m-nav-mobile--menu__status ${connectedUsers.length > 1 ? 'm-nav-mobile--menu__status--active' : ''}`">
        <div class="m-nav-mobile__user-status">
          <user-status />
        </div>
        <div class="m-nav-mobile__icon-cont">
          <status-icon />
          <span :class="`m-status m-status--${currentDoc.status.toLowerCase()}`">{{ formattedDocStatus }}</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';
import sentenceCase from '../../../shared/sentence_case';
import docStatuses from '../../../client/config/doc_statuses';
import CommunityLockup from './community-lockup.vue';
import HelpToggle from './help-toggle.vue';
import UserStatus from './user-status.vue';
import StatusIcon from './status-icon.vue';

export default {
  components: {
    CommunityLockup,
    HelpToggle,
    UserStatus,
    StatusIcon
  },
  props: {
    navLinks: {
      type: Array,
      required: true
    },
    isMobile: {
      type: Boolean,
      default: true
    },
    pageActive: {
      type: Function,
      required: true
    }
  },
  data() {
    return {
      activeMobileNav: false
    };
  },
  computed: {
    ...mapState({
      windowSize: state => state.windowSize,
      connectedUsers: state => state.autosave.connectedUsers
    }),
    ...mapGetters([
      'currentDoc'
    ]),
    communityId() {
      return this.currentDoc && this.currentDoc.community_id;
    },
    activeMobileNavClass() {
      return this.activeMobileNav ? 'is-active' : '';
    },
    activeMobileNavHeightStyle() {
      if (!this.activeMobileNav) return;
      const windowHeight = this.windowSize.height;
      return `height: ${windowHeight}px`;
    },
    formattedDocStatus() {
      const status = this.currentDoc.status;
      if (status === 'UPDATED') {
        return 'Changes';
      } else {
        return sentenceCase(docStatuses[status]);
      }
    }
  },
  methods: {
    toggleMobileNav() {
      this.activeMobileNav = !this.activeMobileNav;
    }
  }
};
</script>
