<template>
  <div class="m-dashboard__modal">
    <res-input class="m-dashboard__modal__input" @keyup.enter.prevent="handleCreate">
      <label>Saved search title</label>
      <input
        v-model.trim="title"
        :placeholder="placeholder"
        autofocus
        type="text" />
    </res-input>
    <div class="m-dashboard__modal__actions">
      <res-button
        :disabled="disableButtons"
        design="primary"
        @click.prevent="handleCreate">
        <button>Save search</button>
      </res-button>
      <res-button :disabled="disableButtons" @click.prevent="clearModal">
        <res-icon class="m-dashboard__modal__icon" name="x" />
        <button>Cancel</button>
      </res-button>
    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex';
import { SET_MODAL } from '../../store/commands';

export default {
  props: {
    childProps: {
      type: Object,
      required: true,
      default: () => ({})
    }
  },
  data() {
    return {
      title: '',
      placeholder: 'Untitled search',
    };
  },
  computed: {
    ...mapState(['modalId', 'currentModal', 'currentCommunity']),
    disableButtons() {
      return this.childProps.saving;
    }
  },
  mounted() {
    this.title = this.childProps.keyword || '';
  },
  methods: {
    handleCreate() {
      if (this.disableButtons) {
        return;
      }
      const { addSavedSearch } = this.childProps;
      const params = {
        name: this.title || this.placeholder,
        communityId: String(this.currentCommunity.id),
        query: this.$route.query.q,
      };
      addSavedSearch(params);
      this.clearModal();
    },
    clearModal() {
      this.$store.commit(SET_MODAL, {
        childProps: null,
        component: null,
      });
    }
  }
};
</script>
