<template>
  <div :class="`m-chorus-help__icon ${announcementClass}`" :style="isMobile ? 'position: absolute; right: 0;' : ''">
    <res-button design="default" paddingless @click="toggleHelp()">
      <res-icon size="medium" title="Help" name="help" />
      <button type="button" />
    </res-button>
  </div>
</template>

<script>
import * as cmd from '../../store/commands';
import moment from 'moment-timezone';
import { mapState } from 'vuex';

export default {
  props: {
    isMobile: {
      type: Boolean,
      default: false
    }
  },
  data() {
    return {
      announcementClass: 'is-unseen'
    };
  },
  computed: {
    ...mapState([
      'announcements',
      'currentUser',
      'currentSidebar',
      'currentOverlay',
      'user'
    ]),
  },
  mounted() {
    if (!this.currentUser.hasOwnProperty('lastAnnouncementSeenAt')) this.setAnnouncementState();
  },
  methods: {
    closeSidebar() {
      this.$store.commit(cmd.SET_SIDEBAR, null);
    },
    closeOverlay() {
      this.$store.commit(cmd.SET_OVERLAY, {
        childProps: null,
        component: null
      });
    },
    openChorusHelpSidebar() {
      this.$store.commit(cmd.SET_SIDEBAR, {
        to: 'chorus-help',
        id: 'chorus-help',
        title: 'Chorus help',
        childProps: {},
        orientation: 'right',
        pushContent: false
      });
      this.viewAnnouncements();
    },
    toggleHelp() {
      if (this.currentSidebar.id === 'chorus-help' && (this.currentOverlay.component === 'chorus-help-announcement' || this.currentOverlay.component === 'chorus-help-contact-form')) {
        // the sidebar is open, displaying the chorus-help child component
        // AND the overlay is open, displaying the chorus-help-announcement or chorus-help-contact-form child component
        // so close both of these when the toggle button is clicked
        this.closeSidebar();
        this.closeOverlay();
      } else if (this.currentSidebar.id === 'chorus-help') {
        this.closeSidebar();
      } else if (this.currentSidebar.id && this.currentSidebar.id !== 'chorus-help') {
        this.closeSidebar();
        setTimeout(() => { this.openChorusHelpSidebar(); }, 300);
      } else {
        this.openChorusHelpSidebar();
      }
    },
    viewAnnouncements() {
      // the chorus-help sidebar was toggled-open
      // so set the `lastSeen` var to today/now
      const lastSeen = new Date().toISOString(); // today/now
      // store today/now as the data in the user state
      this.user.lastAnnouncementSeenAt = lastSeen;
      // store the `lastSeen` date in the session
      sessionStorage.setItem('lastAnnouncementSeenAt', lastSeen);
      // make sure there is a session item for the last time an announcement was read,
      // though this data will be properly set from within the chorus-help sidebar when the
      // user opens an announcement
      const lastRead = this.user.lastAnnouncementReadAt;
      sessionStorage.setItem('lastAnnouncementReadAt', lastRead);
      // update the class on the toggle button
      this.announcementClass = this.setAnnouncementClass(lastSeen, lastRead);
    },
    setAnnouncementState() {
      // check the current session for announcements data relative to the user...
      const lastSeenSession = sessionStorage.getItem('lastAnnouncementSeenAt') || this.user.lastAnnouncementSeenAt || '';
      const lastReadSession = sessionStorage.getItem('lastAnnouncementReadAt') || this.user.lastAnnouncementReadAt || '';
      // prep objects to be added to user state
      const announcementStats = {
        'lastAnnouncementSeenAt': lastSeenSession,
        'lastAnnouncementReadAt': lastReadSession
      };
      // PATCH_USER / add objects to the currentUser state
      this.$store.commit(cmd.PATCH_USER, announcementStats);
      this.announcementClass = this.setAnnouncementClass();
    },
    setAnnouncementClass(lastSeen, lastRead) {
      lastSeen = lastSeen || this.currentUser.lastAnnouncementSeenAt || this.user.lastAnnouncementSeenAt;
      lastRead = lastRead || this.currentUser.lastAnnouncementReadAt || this.user.lastAnnouncementReadAt;
      const lastPosted = this.announcements.last_announcement_posted_at;
      if (!lastSeen || moment(lastSeen).isBefore(lastPosted)) {
        return 'is-unseen';
      } else if (!lastRead || moment(lastRead).isBefore(lastPosted)) {
        return 'is-unread';
      } else {
        return '';
      }
    }
  }
};
</script>
