<script>
import NotFound from './not-found.vue';
import NotFoundError from '../../../shared/errors/not_found';

export default {
  name: 'ErrorBoundary',
  components: {
    NotFound,
  },
  data() {
    return {
      error: {},
    };
  },
  errorCaptured(error) {
    // Log the error but don't clutter test logs
    if (process.env.NODE_ENV !== 'test') {
      console.error('Error captured by ErrorBoundary', error);
    }
    // Handle API 404 responses
    if (error.response && error.response.status === 404) {
      this.error = new NotFoundError(error.message);
    } else {
      this.error = error;
    }
    return false;
  },
  render(createElement) {
    switch (this.error.name) {
    case 'CommunityMismatchError':
    case 'NotFoundError':
      return createElement(NotFound);
    default:
      return createElement('div', this.$slots.default);
    }
  },
};
</script>
