<template>
  <div :class="wrapperClass">
    <res-icon
      :title="status"
      :class="`m-nav-top__status-icon m-nav-top__status-icon--${status.toLowerCase()}`"
      :name="statusIcon"
      :key="status"
      size="medium"
    />
    <span>{{ status }}</span>
  </div>
</template>

<script>
import { mapState, mapGetters } from 'vuex';

export default {
  props: {
    wrapperClass: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      connected: false,
      statusMap: {
        Disconnected: 'x',
        Error: 'x',
        Working: 'more',
        Saved: 'check',
      }
    };
  },
  computed: {
    ...mapState({
      autosaveError: state => state.autosave.error
    }),
    ...mapGetters([
      'currentDoc',
      'anthemSocketConnectedStatus',
      'isPipelineDirty'
    ]),
    status() {
      if (this.autosaveError) {
        return 'Error';
      } else if (this.connected) {
        return this.isPipelineDirty ? 'Working' : 'Saved';
      } else {
        return 'Disconnected';
      }
    },
    statusIcon() {
      return this.statusMap[this.status];
    }
  },
  watch: {
    anthemSocketConnectedStatus() {
      this.connected = this.anthemSocketConnectedStatus;
    }
  },
  mounted() {
    this.connected = this.anthemSocketConnectedStatus;
  }
};
</script>
